const CrmStore = {
    state: {
        userData: {},
        companyData: {},
        contactData: {},
        formType: "",
        formName: "",
        userProfile: "",
        // Activities
        timeline: [],
        timelineId: '',
        activity: {
          attributes: {
            attendees_id: [],
            attendees: [],
            attachment: [],
            client_type: "",
            company_uuid: "",
            site_id: "",
            date: "",
            duration: "",
            entity_id: "",
            file_name: "",
            file_note: "",
            first_name: "",
            staff_id: "",
            last_name: "",
            notes: "",
            subject: "",
            time: "",
            type: "meeting"
          },
          type: "activities"
        },
        mode: 'ADD', 
        allCustomers: [],
        totalCountCustomers: 0,
        addFromCompany: false,
        companyOptions: [],
        mutatingCompanies: false
    },
    mutations: { 
      pushUserData(state, user) {
          state.userData = user;
      },
      pushFormType(state, formAction) {
          state.formType = formAction;
      },
      pushFormName(state, formName) {
          state.formName = formName;
      },
      pushUserProfile(state, profile) {
          state.userProfile = profile;
      },
      // Activity Methods
      setActivity(state, activityData) {
        // eslint-disable-next-line
        state.activity = activityData;
      },
      setMode(state, mode) {
        state.mode = mode;
      },
      setTimelineId(state, id) {
        state.timelineId = id;
      },
      setTimeline(state, data) {
        state.timeline = data;
      },
      setAddFromCompany(state, id){
        state.addFromCompany = id;
      },
      setCompanyData(state, data){
        state.companyData = data;
      },
      setContactData(state, data){
        state.contactData = data;
      },
      setCompanyOptions(state, data){
        state.companyOptions = data.map(item => {
          return {
            id: item.id,
            label: item.company_name
          }
        });
      },
      pushCompanyOption(state, data){
        state.mutatingCompanies = true;
        state.companyOptions.push(data);
        setTimeout(() => {
          state.mutatingCompanies = false;
        }, 300);
      },
      removeCompanyOption(state, id){
        let indexOfId = state.companyOptions.filter(option => {
          return option.id !== id;
        });
      }
    }
}
export default CrmStore;