const SystemFieldsStore = {
  state: {
    contact_type: {results:[]},
    company_type: {results:[]},
    industry_type: {results:[]},
    google_maps_api_key: {results:[]},
    administratorList: [],
    swalContactTypes: {},
  },
  mutations: {
    setSystemField(state, data) {
      state[data.type] = data.items;
    },
    setSwalContactTypes(state, data){
      state.swalContactTypes["Secondary"] = "Secondary";
      data.forEach(item => {
        state.swalContactTypes[item.id] = item.value;
      })
    },
    setAdministratorList(state, data){
      state.administratorList = data;
    }
  },
  actions: {
    fetchSystemFields(context, type){
      let url = `/api/get-system-fields-data.json?key=${type}`; 
      this._vm.$http.get(url)
        .then(res => {
          context.commit('setSystemField', {type, items: res.data.items});
          if (type === 'contact_type'){
            context.commit('setSwalContactTypes', res.data.items.results);
          }
        }).catch(error => {
          toastIt("error", "Failed to load system fields.", this.ops);
        });
    }
  }
}
export default SystemFieldsStore;