<template>
  <div>
    <div id="tableButtons">
      <ins-button
        field="addNewCompany"
        id="addNewCompany" 
        label="ADD NEW COMPANY" 
        color="blue" 
        solid="true"
        size="medium"
        v-on:onClickInsButton='addNewCompany()'
        v-bind:disabled="loadingPage">
      </ins-button>
      <ins-button
        field="addNewContact"
        id="addNewContact" 
        label="ADD NEW CONTACT" 
        color="blue" 
        solid="true"
        size="medium"
        v-on:onClickInsButton='addNewContact()'
        v-bind:disabled="loadingPage">
      </ins-button>   
    </div>
    <ins-base-table
      id="insBaseTable"
      searchbar-placeholder="Search by Company, Contact or Email..."
      v-on:onPaginationChange="tablePaginationHandler"
      v-on:onTableSearch="tableSearchHandler"
      v-on:onTableSort="tableSortHandler"
      v-on:onTableBulkAction="tableBulkActionHandler"
      v-on:onTableRowAction="tableRowActionHandler">
    </ins-base-table>
  </div>
</template>

<script>
import moment from "moment";
import { mapState } from 'vuex';
import { isEmpty } from 'lodash';

export default {
  name: "ListCRM",
  components: {},
  data() {
    return {
      loadingPage: false,
      headers: undefined,
      crmListData: [],
      companyListData: [],
      crmBuilt: false,
      companyBuilt: false,
      sourceCompany: "/api/get-all-company-list-data.json",
      sourceCRM: "/api/get-all-customers-list-data.json",
      sourceCompanyDetails: "/api/get-company-details-data.json?id=",
      sourceCRMDetails: "/api/get-all-company-contact-list-data.json?id=",
      searchSource: "/api/search-company-data.json",
      userData: {},
      companyData: {},
      totalCount: 0,
      pageNumber: 1,
      pageSize: 10,
      searchKeyword: "",
      sortedKeyword: "",
      sortedBy: "asc",
      rawData: [], 
      filteredData: [],
      displayedData: [],       
      formModel: {
        form_configuration_name: "crm_delete_customer",
        form: {
          profiles_attributes: {
            customer_attributes: {
              properties_attributes: {
                is_enabled: "false"
              }
            }
          }
        }
      },
      continueDeletion: false,
      errorMessage: "Failed to load Data. Please try again."
    };
  },
  methods: {    
    addNewCompany() {
      this.$store.commit("pushUserProfile", "company");
      this.$store.commit("pushFormType", "add");
      this.$store.commit("pushFormName", "add_new_company");
      this.$router.push("/manage-company");
    },
    addNewContact() {
      this.$store.commit("pushUserProfile", "customer");
      this.$store.commit("pushFormType", "add");
      this.$store.commit("pushFormName", "add_new_customer");
      this.$router.push("/manage-crm");
    },
    tablePaginationHandler(event) {
      let pageSize = parseInt(event.detail.pageSize);
      this.pageNumber = event.detail.pageNumber;
      if (this.pageSize !== pageSize){
        this.pageNumber = 1;
      }
      this.pageSize = pageSize;
      this.processTableData();
    },
    dynamicKeySort(key, order) {
      let OrderSortIndex = order === 'asc' ? 1 : -1;

      return function (a, b) {
        let num = (a[key] < b[key]) ? -1 : (a[key] > b[key]) ? 1 : 0;
        return num * OrderSortIndex;
      }
    },
    tableSearchHandler(event){
       if (
          (event.detail.keyCode === 13 && event.detail.value !== "") ||
          (event.detail.value === "" && this.searchKeyword !== "")
        ) {     
            this.insBaseTable.loadingScreen = true;
            this.searchKeyword = event.detail.value;
            if (!this.searchKeyword) {
              this.processTableData();
              return;
            }
            this.filteredData = [];
            let self = this;
            this.rawData.forEach((value) => {
              Object.keys(value).forEach((key) => {
                if (key === 'Company' || key === 'Contact' || key === 'Email') {
                  let match = value[key];
                  console.log("search: ", this.searchKeyword.toLowerCase());
                  if (match.toLowerCase().includes(self.searchKeyword.toLowerCase())) {
                    let checkExist = this.filteredData.find(item => {
                      return item.company_id === value.company_id || item.contact_id === value.contact_id;
                    })
                    if (!checkExist) { this.filteredData.push(value); }
                  }
                }
              });
            });
            
            if (this.sortedKeyword && this.filteredData.length > 0) {
              this.filteredData.sort(this.dynamicKeySort(this.sortedKeyword));
            }
            this.insBaseTable.pageNumber = 1;
            this.processTableData();
            if (!this.filteredData.length) {
              this.insBaseTable.loadingScreen = true;
              this.insBaseTable.loaderTitle = '';
              this.insBaseTable.loaderMessage = 'No result found for "' + this.searchKeyword + '"';
              this.insBaseTable.loaderIcon = '';
            }
        }
    },
    tableSortHandler(event) {
      this.sortedKeyword = event.detail.keyword;
      this.sortedBy = event.detail.order;
      

      if (this.searchKeyword){
        this.filteredData.sort(this.dynamicKeySort(this.sortedKeyword, this.sortedBy));
      } else {
        this.rawData.sort(this.dynamicKeySort(this.sortedKeyword, this.sortedBy));
      }
      this.processTableData();
    },
    tableBulkActionHandler(event) {
      if (event.detail.action === "Archive") {
        this.archiveSelection(event.detail.selections);
      }
    },
    tableRowActionHandler(event) {
      if (event.detail.action === "Archive") {
        this.archiveItem(event.detail);
      } else if (event.detail.action === "Edit") {
          if (event.detail.header === "Company") { 
            this.$store.commit("pushUserData", event.detail.data);
          } else if (event.detail.header === "Contact") {
            if (event.detail.data.Company){
              this.$store.commit("pushUserData", event.detail.data.contact_data);
            } else {
              this.$store.commit("pushUserData", event.detail.data);
            }
          }
          this.$store.commit("pushFormType", "edit");
          this.viewItem(event.detail.header);
      } else if (event.detail.action === "View") {
        // this.$store.commit("pushFormType", "view");
        this.viewHandler(event.detail);
      }
    },
    viewItem(header) {
      if (header === "Company") {
        this.$router.push("/manage-company");
      } else if (header === "Contact") {
        this.$router.push("/manage-crm");
      } else {
        this.$router.push("/customers");
      }
    },
    viewHandler(detail){
      switch(detail.header){
        case 'Company':
          this.$store.commit("setCompanyData", detail.data);
          this.$router.push("/view-company");
          break;

        case 'Contact':
        
          if (detail.data.Company){
            this.$store.commit("setContactData", event.detail.data.contact_data);
          } else {            
            this.$store.commit("setContactData", event.detail.data);
          }
          this.$router.push("/view-contact");
      }
    },
    archiveItem(value) {
      let deleteMsg = "Are you sure you want to archive this contact?"
      if(value.header == 'Company'){
          deleteMsg = "Are you sure you want to archive this company?  By doing so, it will also archive all contacts listed in it."
      } 
      swal({
        title: "",
        html: `
          <ins-loader
            state-icon="warning"
            icon-color="warning"
            state-message="${deleteMsg}">
          </ins-loader>`,
        type: "",
        showCancelButton: true,
        confirmButtonText: "Archive"
      }).then(result => {
        if (result.value) {          
          this.continueDeletion = true;

          if (value.header == 'Company'){
            this.deleteCompanyAndContacts(value.data.company_id);
          } else {
            this.deleteContact(value.data.contact_id, true);
          }          
        }
      });
    },
    archiveSelection(selections) {
      let self = this;
      swal({
        title: "",
        html: `
          <ins-loader
            state-icon="warning"
            icon-color="warning"
            state-message="Are you sure you want to archive these items?">
          </ins-loader>`,
        type: "",
        showCancelButton: true,
        confirmButtonText: "Archive"
      }).then(result => {
        if (result.value) {
          let counter = 0;
          self.continueDeletion = true;

          selections.forEach(async value => {
            if (self.continueDeletion) {
              
              if (value.company_id !== "" || value.company_id !== null) {
                  await self.deleteCompanyAndContacts(value.company_id);
              } else {
                  await self.deleteContact(value.contact_id, false);
              }
              
              counter++;

              if (selections.length === counter) {
                self.continueDeletion = false;
                toastIt("success", "Items have been archived.", self.ops);
                // self.insBaseTable.resetSelections();
                self.fetchDataFromSrc();
              }
            }
          }); 
        }
      });
    },
    deleteContactList(list) {
        list.forEach(async contact => {
            await this.deleteContact(contact.id, false);
        });
        return true;
    },
    async deleteCompanyAndContacts(company_id) {  // INIT() delete company and all on contact list          
        let contactListUrl = `${this.sourceCRMDetails}${company_id}&sortBy=updated_at&orderBy=desc` // get company's contact list        
        let deleteCompany = false; 
        if (this.continueDeletion) {             
            let contactList = await this.getAdditionalData(contactListUrl);  // get company contact list 
    
            if (!contactList || contactList === []) {
                deleteCompany = true;
            } else {
                deleteCompany = this.deleteContactList(contactList)
            }
            
            if (deleteCompany) {
                this.continueDeletion = false;
                let companyDeleted = this.deleteCompany(company_id, false);
                // toastIt("success", "Items have been archived.", this.ops);
                // companyDeleted ? this.fetchDataFromSrc() : '';
            }
        }
    },
    deleteCompany(company_id, showSuccess) { // delete company
        let self = this;
        let companyurl = `/api/customizations/${company_id}`;
        let companyForm = {
          form_configuration_name: "crm_delete_company",
          parent_resource_id: "company",
          form: {
            properties: {
              is_enabled: "false"
            }            
          }
        };
        let body = JSON.stringify(companyForm);       
        
        // ajax delete company
        this.$http
          .put(companyurl, body, { headers: this.headers })
          .then(response => {
              this.$store.commit('removeCompanyOption', company_id);
              if (!this.continueDeletion) {
                showSuccess ? toastIt("success", "Company and its contacts have been archived.", this.ops): '';
                this.fetchDataFromSrc();
                return true;
              }
          })
          .catch(error => {
            this.continueDeletion = false;
            this.fetchDataFromSrc();
            toastIt(
              "error",
              "Failed to archive company. Please try again.",
              this.ops
            );
          });

    },
    deleteContact(contact_id, showSuccess) {
      let url = `/api/users/${contact_id}`;      
      let body = JSON.stringify(this.formModel);
     
      this.$http
        .put(url, body, { headers: this.headers })
        .then(response => {
            if (showSuccess) {
                toastIt("success", "Contact has been archived.", this.ops);
                this.fetchDataFromSrc();            
            }
            
        })
        .catch(error => {
          this.continueDeletion = false;
          this.fetchDataFromSrc();
          toastIt(
            "error",
            "Failed to archive user. Please try again.",
            this.ops
          );
        });
        
        if (!this.continueDeletion)
            this.fetchDataFromSrc();
    },
    processTableData() {
      let last_index = this.pageNumber * this.pageSize;
      let first_index = (last_index - this.pageSize);

      this.displayedData = [];

      if (this.searchKeyword) {
        this.filteredData.forEach((value, index) => {
          if (index >= first_index && index <= last_index) {
            this.displayedData.push(value);
          } else if (this.filteredData.length < this.pageSize){
            this.displayedData.push(value);
          }
        });
      } else {
        this.rawData.forEach((value, index) => {
          if (index >= first_index && index < last_index) {
            this.displayedData.push(value);
          }
        });
      }
      
      this.insBaseTable.totalCount = this.rawData.length;
      this.insBaseTable.tableData = this.displayedData;
      this.insBaseTable.loadingScreen = false;
    },
    searchPrimaryContact(tempData) {
      
      let result = tempData.find(item => {
        return item.customer_details.contact_type === "Primary";
      });

      if (!result){
        result = tempData.find(item => {
          return item.customer_details.contact_type === "Secondary";
        });
      }
      
      if (!result){ 
        return tempData[0]; // return latest contact updated
      }

      return result; // return primary or secondary
    },
    removeTableDataDuplicate() { // remove duplicates by 'company_id'   
        let tempObj = {};
        console.log("REMOVE DUPLICATES");
             
        for ( let i=0; i < this.rawData.length; i++ ) {
            if(typeof(this.rawData[i]['company_id']) !== 'undefined' && this.rawData[i]['company_id'] !== '' && this.rawData[i]['company_id'] !== null) {
                tempObj[this.rawData[i]['company_id']] = this.rawData[i];   
                console.log(" has company ", tempObj);
                
            } else { 
              if(isEmpty(tempObj)){               
                tempObj[[i]] = this.rawData[i];
              } else {                  
                 for(let x in tempObj) {
                   console.log(tempObj[x].id, " ", this.rawData[i].id, " has comp ", tempObj[this.rawData[i]['company_id']]);
                   if(tempObj[x].id !== this.rawData[i].id && tempObj[this.rawData[i]['company_id']] !== this.rawData[i].id) { 
                      tempObj[[i]] = this.rawData[i];
                   }
                 }
                }
            }       
        }        
        let cleanData = [];
        for ( let i in tempObj ) { // re-build to new array
            cleanData.push(tempObj[i]);
        }

        this.insBaseTable.totalCount = cleanData.length;
        this.rawData = cleanData; 
        this.processTableData();  
    },
    renderTable(){
      if (this.crmBuilt && this.companyBuilt){

        if (this.crmListData.length || this.companyListData.length){
          this.removeTableDataDuplicate();
        } else {
          let message = 'No customers yet.'
          if (this.searchKeyword){
            message = `No result for "${this.searchKeyword}"`;
          }
          this.loaderStateHandler('', message, 'no data');
          this.loadingPage = false;
        }

      }
    },
    fetchCRMData(){
      let url = this.constructURL(`${this.sourceCRM}?size=10000&page=1`);
      this.$http
        .get(url)
        .then(this.crmResponseHandler)
        .catch(this.errorHandler);
    },
    crmResponseHandler(response){
      if (response.data.items.results.length) {
        this.crmListData = response.data.items.results;
        this.buildCRMTableData();
      } else {
        this.crmBuilt = true;
        this.fetchCompanyData();
      }
    },
    buildCRMTableData(){
      let counter = 0;
      this.crmListData.forEach(async item => {
        let companyData;
        let companyID = item.customer_details.company_id;
        let lastActivity = item.updated_at;

        if (companyID !== null && companyID !== ""){
          let url = `${this.sourceCompanyDetails}${companyID}`;
          companyData = await this.getAdditionalData(url);
          if (companyData) {
            if (companyData.is_enabled === "true" || companyData.is_enabled) {
              companyData = companyData[0]
              lastActivity = companyData.updated_at;
            }
          }
        }

        let formatted = this.constructDateTime(lastActivity);
        let tempObj = this.constructCRMTableData(item, companyData, formatted);
        this.rawData.push(tempObj);
        counter++;

        if (counter === this.crmListData.length){
          this.crmBuilt = true;
          this.fetchCompanyData();
        }
      });
    },
    getAdditionalData(url){
      return this.$http.get(url)
        .then(response => {
          if (response.data.items){
            if (response.data.items.results.length){
              return response.data.items.results;
            }
            return false;
          }
          return false;
        }).catch(this.errorHandler);
    },
    fetchCompanyData(){
      let url = this.constructURL(`${this.sourceCompany}?size=10000&page=1`);
      this.$http.get(url)
        .then(this.companyResponseHandler)
        .catch(this.errorHandler);
    },
    companyResponseHandler(response){
      if (response.data.items.results.length) {
        this.companyListData = response.data.items.results;
        this.$store.commit('setCompanyOptions', this.companyListData);
        this.loadingPage = false;
        this.buildCompanyTableData();
      } else {
        this.companyBuilt = true;
        this.renderTable();
      }
    },
    buildCompanyTableData(){
      let counter = 0;
      this.companyListData.forEach(async item => {
        let primaryContact;
        let lastActivity = item.updated_at;
        let url = `${this.sourceCRMDetails}${item.id}&sortBy=updated_at&orderBy=desc`;
        let crmData = await this.getAdditionalData(url);
        
        if (crmData) {
          primaryContact = this.searchPrimaryContact(crmData);
          if (isEmpty(primaryContact) || primaryContact === null) {                 
            lastActivity = primaryContact.updated_at;
          }
        }

        lastActivity = this.constructDateTime(lastActivity);
        let tempObj = this.constructCompanyTableData(item, primaryContact, lastActivity);
        this.rawData.push(tempObj);
        counter++;

        if (counter === this.companyListData.length){
          this.companyBuilt = true;
          this.renderTable();
        }

      });
    },
    constructCompanyTableData(item, primaryContact, lastActivity){
      let obj = {
        "id": item.id === null ? "" : item.id,
        "raw": item,
        "contact_data": {},
        "company_id": item.id === null ? "" : item.id,
        "Company": item.company_name === null ? "" : item.company_name,
        "Profile Image": (isEmpty(item) || item.profile_image === null) ? "" : item.profile_image.original,
        "Company_Img": (isEmpty(item) || item.profile_image === null) ? "" : item.profile_image.original,
        "company_updated_at": item.updated_at === null ? "" : this.constructDateTime(item.updated_at),
        "Phone": this.constructPhoneNumber('mobile', item),
        "Home Phone": this.constructPhoneNumber('home', item),
        "Work Phone": this.constructPhoneNumber('work', item),
        // "Mobile Phone": '',
        // "Home Phone": '',
        // "Work Phone": '',
        "Last Activity": lastActivity,
        "contact_id": '',
        "First Name": '',
        "Last Name": '',
        "Contact": '',
        "Contact_Img": '',
        "Contact Type": '',
        "Email": '',
        "Contact_Img": '',
        "Company Mobile Phone Country Code": this.checkValue(item.mobile_phone_country_code),
        "Company Mobile Phone Area Code": this.checkValue(item.mobile_phone_area_code),
        "Company Mobile Phone Number": this.checkValue(item.mobile_phone_number),
        "Company Home Phone Country Code": this.checkValue(item.home_phone_country_number),
        "Company Home Phone Area Code": this.checkValue(item.home_phone_area_number),
        "Company Home Phone Number": this.checkValue(item.home_phone_number),
        "Company Work Phone Country Code": this.checkValue(item.work_phone_country_number),
        "Company Work Phone Area Code": this.checkValue(item.work_phone_area_number),
        "Company Work Phone Number": this.checkValue(item.work_phone_number),
        "Home Address": item.location_1.length > 0 ? item.location_1[0].address !== 'undefined' ? item.location_1[0].address : '' : '', 
        "Home Street": item.location_1.length > 0 ? item.location_1[0].street !== 'undefined' ? item.location_1[0].street : '' : '', 
        "Home City": item.location_1.length > 0 ? item.location_1[0].city !== 'undefined' ? item.location_1[0].city : '' : '', 
        "Home State": item.location_1.length > 0 ? item.location_1[0].state != 'undefined' ? item.location_1[0].state : '' : '', 
        "Home Area Code": item.location_1.length > 0 ? item.location_1[0].postcode != 'undefined' ? item.location_1[0].postcode : '' : '', 
        "Home Country": item.location_1.length > 0 ? item.location_1[0].country !== 'undefined' ? item.location_1[0].country : '' : '',
        "Facebook" : this.checkValue(item.facebook_link),
        "Instagram" : this.checkValue(item.instagram_link),
        "Twitter" : this.checkValue(item.twitter_link),
        "Google" : this.checkValue(item.google_plus_link),
        "Linkedin" : this.checkValue(item.linkedin_link),
        "Youtube" : this.checkValue(item.youtube_link),
        "Notes": this.checkValue(item.notes),
        "Website": this.checkValue(item.website),
        "Industry Type": this.checkValue(item.industry_type),
        "Company Type": this.checkValue(item.company_type),
        "Assigned To": this.checkValue(item.assigned_to),
        "contact_updated_at": '',
        "Industry Type": this.checkValue(item.industry_type),
        "Company Type": this.checkValue(item.company_type)
      }

      if (primaryContact){
        // console.log('hasPrimaryContact', primaryContact);
        obj["contact_id"] = (isEmpty(primaryContact) || primaryContact) ? primaryContact.id : "";
        obj["First Name"] = (isEmpty(primaryContact) || primaryContact) ? primaryContact.first_name : "";
        obj["Last Name"] = (isEmpty(primaryContact) || primaryContact) ? primaryContact.last_name : "";
        obj["Contact Type"] = (isEmpty(primaryContact) || primaryContact) ? primaryContact.customer_details.contact_type : "";
        obj["Contact"] = ((isEmpty(primaryContact) || primaryContact === null)? "" : primaryContact.first_name !== null || primaryContact.first_name !== "" ? primaryContact.first_name : "") + ((isEmpty(primaryContact) || primaryContact === null)? "" : primaryContact.last_name !== null || primaryContact.last_name !== "" ? " " + primaryContact.last_name : "");
        obj["Email"] = (isEmpty(primaryContact) || primaryContact)? primaryContact.email : "";
        obj["Mobile Phone"] = this.constructPhoneNumber('mobile', primaryContact.contact_number);
        obj["Mobile Phone Country Code"] = isEmpty(primaryContact.contact_number) ? '' : this.checkValue(primaryContact.contact_number.mobile_phone_country_code);
        obj["Mobile Phone Area Code"] = isEmpty(primaryContact.contact_number) ? '' : this.checkValue(primaryContact.contact_number.mobile_phone_area_code);                
        obj["Mobile Phone Number"] = isEmpty(primaryContact.contact_number) ? '' : this.checkValue(primaryContact.contact_number.mobile_phone_number);
        obj["Home Phone"] = this.constructPhoneNumber('home', primaryContact.contact_number);
        obj["Home Phone Country Code"] = isEmpty(primaryContact.contact_number) ? '' : this.checkValue(primaryContact.contact_number.home_phone_country_code);
        obj["Home Phone Area Code"] = isEmpty(primaryContact.contact_number) ? '' : this.checkValue(primaryContact.contact_number.home_phone_area_code);
        obj["Home Phone Number"] = isEmpty(primaryContact.contact_number) ? '' : this.checkValue(primaryContact.contact_number.home_phone_number);
        obj["Work Phone"] = this.constructPhoneNumber('work', primaryContact.contact_number);
        obj["Work Phone Country Code"] = isEmpty(primaryContact.contact_number) ? '' : this.checkValue(primaryContact.contact_number.work_phone_country_code);
        obj["Work Phone Area Code"] = isEmpty(primaryContact.contact_number) ? '' : this.checkValue(primaryContact.contact_number.work_phone_area_code);
        obj["Work Phone Number"] = isEmpty(primaryContact.contact_number) ? '' : this.checkValue(primaryContact.contact_number.work_phone_number); 
        obj["contact_updated_at"] = isEmpty(primaryContact) || primaryContact ? "" : this.constructDateTime(primaryContact.updated_at);
        obj.contact_data = this.constructCRMTableData(primaryContact, item, this.constructDateTime(primaryContact.updated_at));
        obj["Contact_Img"] = isEmpty(primaryContact.customer_details) ? '' : primaryContact.customer_details.profile_image === null ? "" : primaryContact.customer_details.profile_image.original;
        
        // console.log('constructed if', obj);
      }
      // console.log('constructed', obj);
      return obj;
    },
    fetchDataFromSrc() {
      this.rawData = [];
      this.displayedData = [];

      this.loaderStateHandler(
        "Processing Your Data",
        "Just a moment, we're fetching and processing your data.",
        "processing");
      this.fetchCRMData();
    },
    errorHandler(error){
      console.error("Error: " + error);
      this.continueDeletion = false;
      this.loaderStateHandler('', this.errorMessage, 'error');
    },
    loaderStateHandler(title, message, type) {
      this.insBaseTable.loadingScreen = true;
      this.insBaseTable.loaderTitle = title;
      this.insBaseTable.loaderMessage = message;
      this.insBaseTable.loaderIcon = type;
    },
    setupInsTable(){
      this.insBaseTable = document.getElementById("insBaseTable");
      this.insBaseTable.rowActions = ["View", "Edit", "Archive"];
      this.insBaseTable.bulkActions = ["Archive"];
      this.insBaseTable.tableHeaders = [
        this.constructTableHeader("Company", "string", true, true, true),
        this.constructTableHeader("Contact", "string", true, true, true),
        this.constructTableHeader("Email", "string", true),
        this.constructTableHeader("Phone"),
        this.constructTableHeader("Last Activity", "string", true)
      ]
      // this.insBaseTable.pageSize = this.pageSize;
      // this.insBaseTable.pageSizeOptions = [1,2,3,4];
    },
    constructTableHeader(label, type = "string", sortable, hasColumnAction, hasImage){
      return { label, type, sortable, hasColumnAction, hasImage }
    },
    constructURL(source){
      let url = source;
      // url += `?size=${this.pageSize}`;
      // url += `&page=${this.pageNumber}`;
      url += `?sortBy=${this.sortedKeyword}`;
      url += `&orderBy=${this.sortedBy}`;
      return url;
    },
    constructDateTime(dateTime){
      return !dateTime ? '' : 
        moment(dateTime).calendar(null, {sameElse: 'Do MMM YYYY'});
    },
    constructPhoneNumber(field, item){
      let phoneNumber = "";
      // if (this.checkValue(item) != '') {
      if (item) {

        if (this.checkValue(item[field + '_phone_area_code']) ||
            this.checkValue(item[field + '_phone_number'])) {

          phoneNumber = `${this.checkValue(item[field + '_phone_country_code'], true)}`;
          phoneNumber += ` ${this.checkValue(item[field + '_phone_area_code'])}`;
          phoneNumber += ` ${this.checkValue(item[field + '_phone_number'])}`;  
        }
      }
     
      return phoneNumber.trim();
    },
    checkValue(value, withPlus){
      return value && value !== 'undefined' ? `${withPlus ? '+':''}${value}` : '';
    },
    constructCRMTableData(item, companyData, lastActivity){
      return {
        "id": item.id === null ? "" : item.id,
        "raw": item,
        "Contact Type": item.customer_details.contact_type,
        "company_id": (isEmpty(companyData) || companyData.id === null) ? "" : companyData.id,
        "Company": (isEmpty(companyData) || companyData.company_name === null) ? "" : companyData.company_name,
        "Company_Img": (isEmpty(companyData) || isEmpty(companyData.profile_image)) ? "" : companyData.profile_image.original,
        "company_updated_at": (isEmpty(companyData) || companyData.updated_at === null) ? "" : this.constructDateTime(companyData.updated_at),
        "contact_id": item.id,
        "First Name": item.first_name,
        "Last Name": item.last_name,
        "Contact": (item.first_name !== null && item.first_name !== "" ? item.first_name : "") + (item.last_name !== null && item.last_name !== "" ? " " + item.last_name : ""),
        "Email": item.email,
        "Notes": this.checkValue(item.customer_details.notes),
        "Contact_Img": item.customer_details.profile_image === null ? '' : this.checkValue(item.customer_details.profile_image.original),
        "Profile Image": (isEmpty(item) || item.customer_details.profile_image === null) ? "" : item.customer_details.profile_image.original,
        "Phone": isEmpty(item.contact_number) ? '' : this.constructPhoneNumber('mobile', item.contact_number),
        "Mobile Phone Country Code": isEmpty(item.contact_number) ? '' :this.checkValue(item.contact_number.mobile_phone_country_code),
        "Mobile Phone Area Code": isEmpty(item.contact_number) ? '' :this.checkValue(item.contact_number.mobile_phone_area_code),                
        "Mobile Phone Number": isEmpty(item.contact_number) ? '' : this.checkValue(item.contact_number.mobile_phone_number),
        "Home Phone": isEmpty(item.contact_number) ? '' : this.constructPhoneNumber('home', item.contact_number),
        "Home Phone Country Code": isEmpty(item.contact_number) ? '' : this.checkValue(item.contact_number.home_phone_country_code),
        "Home Phone Area Code": isEmpty(item.contact_number) ? '' : this.checkValue(item.contact_number.home_phone_area_code),
        "Home Phone Number": isEmpty(item.contact_number) ? '' : this.checkValue(item.contact_number.home_phone_number), 
        "Work Phone": isEmpty(item.contact_number) ? '' : this.constructPhoneNumber('work', item.contact_number),
        "Work Phone Country Code": isEmpty(item.contact_number) ? '' : this.checkValue(item.contact_number.work_phone_country_code),
        "Work Phone Area Code": isEmpty(item.contact_number) ? '' : this.checkValue(item.contact_number.work_phone_area_code),
        "Work Phone Number": isEmpty(item.contact_number) ? '' : this.checkValue(item.contact_number.work_phone_number), 
        "Home Address": item.customer_details.home_address.length > 0 ? this.checkValue(item.customer_details.home_address[0].address) : '', 
        "Home Street": item.customer_details.home_address.length > 0 ? this.checkValue(item.customer_details.home_address[0].street) : '', 
        "Home City": item.customer_details.home_address.length > 0 ? this.checkValue(item.customer_details.home_address[0].city) : '', 
        "Home State": item.customer_details.home_address.length > 0 ? this.checkValue(item.customer_details.home_address[0].state) : '', 
        "Home Area Code": item.customer_details.home_address.length > 0 ? this.checkValue(item.customer_details.home_address[0].postcode) : '', 
        "Home Country": item.customer_details.home_address.length > 0 ? this.checkValue(item.customer_details.home_address[0].country) : '', 
        "Facebook" : isEmpty(item.social_links) ? '' : this.checkValue(item.social_links.facebook_link ),
        "Instagram" : isEmpty(item.social_links) ? '' : this.checkValue(item.social_links.instagram_link),
        "Twitter" : isEmpty(item.social_links) ? '' : this.checkValue(item.social_links.twitter_link),
        "Google" : isEmpty(item.social_links) ? '' : this.checkValue(item.social_links.google_plus_link),
        "Linkedin" : isEmpty(item.social_links) ? '' : this.checkValue(item.social_links.linkedin_link),
        "Youtube" : isEmpty(item.social_links) ? '' : this.checkValue(item.social_links.youtube_link),
        "contact_updated_at": this.constructDateTime(item.updated_at),
        "Last Activity": lastActivity
      };
    },
    setInsRendererLabel(){
      let insRendererEl = document.querySelector('ins-renderer');
      if (insRendererEl.updateRouteLabel){
        insRendererEl.updateRouteLabel('Customers');
      } else {
        setTimeout(() => {
          this.setInsRendererLabel();
        }, 300);
      }
    }
  },
  mounted() {
    this.setInsRendererLabel();
    this.headers = JSON.parse(JSON.stringify(this.apiHeader));
    this.setupInsTable();
    this.fetchDataFromSrc();
  }
};
</script>

