<template>
  <div class="ins reveal" id="activityModal" data-reveal data-close-on-click="false">
    <div class="reveal-header">
      <h1>Activity</h1>

      <button class="close-button" data-close aria-label="Close modal" type="button">
        <span aria-hidden="true">&times;</span>
      </button>
    </div>
    
    <div class="grid-x grid-padding-x small-up-5 medium-up-5 large-up-5" v-if="initialised"><!-- -->

      <div class="cell activity-type-cell" v-on:click="changeType('meeting')">
        <div v-bind:class="{
          'icon-wrap' : true,
          'active': activityModel.attributes.type === 'meeting'
        }">
          <div class="icon-radius meeting">
            <i class="activity-icon icon-briefcase-1"></i>
          </div>
          <div class="activity-label">Meeting</div>
        </div>
      </div>

      <div class="cell activity-type-cell" v-on:click="changeType('call')">
        <div v-bind:class="{
          'icon-wrap' : true,
          'active': activityModel.attributes.type === 'call'
        }">
          <div class="icon-radius call">
            <i class="activity-icon icon-phone-1"></i>
          </div>
          <div class="activity-label">Call</div>
        </div>
      </div>

      <div class="cell activity-type-cell" v-on:click="changeType('email')">
        <div v-bind:class="{
          'icon-wrap' : true,
          'active': activityModel.attributes.type === 'email'
        }">
          <div class="icon-radius email">
            <i class="activity-icon icon-email-1"></i>
          </div>
          <div class="activity-label">Email</div>
        </div>
      </div>

      <div class="cell activity-type-cell" v-on:click="changeType('event')">
        <div v-bind:class="{
          'icon-wrap' : true,
          'active': activityModel.attributes.type === 'event'
        }">
          <div class="icon-radius event">
            <i class="activity-icon icon-headset-1"></i>
          </div>
          <div class="activity-label">Event</div>
        </div>
      </div>

      <div class="cell activity-type-cell" v-on:click="changeType('file_note')">
        <div v-bind:class="{
          'icon-wrap' : true,
          'active': activityModel.attributes.type === 'file_note'
        }">
          <div class="icon-radius file-note">
            <i class="activity-icon icon-notepad-1"></i>
          </div>
          <div class="activity-label">File Note</div>
        </div>
      </div>

    </div>

    <form class="reveal-body" v-if="initialised"><!-- -->
      <div class="grid-container">

        <div class="grid-x grid-padding-x" v-show="activityModel.attributes.type === 'email'">
          <div class="large-12 cell">
            <!-- <label>Subject
              <input type="text"
                placeholder="Please enter a Subject" 
                v-model="activityModel.attributes.subject" />
            </label> -->
            <InsInputWrap label="Subject" placeholder="Please enter a Subject" field="text" />
          </div>
        </div>

        <div class="grid-x grid-padding-x">
          <div class="large-12 cell">
            <!-- <label>Notes
              <textarea class=""
                placeholder="Please enter a Notes"
                v-model="activityModel.attributes.notes">
              </textarea>
            </label> -->
            <InsTextareaWrap label="Notes" placeholder="Please enter a Notes" />
          </div>
        </div>

        <div class="grid-x grid-padding-x"
          v-show="activityModel.attributes.type !== 'file_note'">
          
          <div class="large-12 cell">
            <label>
              Date
              <ins-datepicker name="date"
                v-on:onDateChange="dateChangeHandler"
                v-bind:value="tempDate">
              </ins-datepicker>
            </label>
          </div>

          <div class="large-12 cell">
            <!-- <label>Time
              <input type="text" id="activityTime"
                placeholder="Please select Time"
                v-model="tempTime" />
            </label> -->
            <InsInputWrap label="Time" placeholder="Please enter a Time" field="text" />
          </div>

          <div class="large-12 cell">
            <!-- <label>Duration
              <input type="text" id="activityDuration"
                placeholder="Please select Duration"
                v-model="tempDuration" />
            </label> -->
            <InsInputWrap label="Duration" placeholder="Please enter a Duration" field="text" />
          </div>

          <!-- <div class="large-12 cell jq-select2-container">
            <label>{{ activityModel.attributes.type === 'email' ? 'Recipients' : 'Attendees' }}
              <ActivitySelect2 v-bind:selectOptions="fetchedStaffs"/>
            </label>
          </div> -->

        </div>


        <div class="grid-x grid-padding-x"
          v-show="activityModel.attributes.type === 'file_note' || activityModel.attributes.type === 'email'">
          
          <div class="large-12 cell">
            
            <div class="dz-wrap">
              <div class="dz-label">Attachment</div>
              <div v-if="files.length === 0" class="dz placeholder">
                Drop files here or Click to choose files <br>
                (Max file size 10MB)
              </div>
              <div id="activityAttachments"></div>
              <div v-if="dzHasError" class="dz-message">
                {{ dzErrorMessage }}
              </div>
            </div>

          </div>

          <div class="large-12 cell">
            <label class="attached-files-wrap">Attached:
              <ul>
                <li v-for="attachedFile in attachedFiles" v-bind:key="attachedFile.uuid">
                  <a v-bind:href="attachedFile.file_note" target="_blank">
                    {{ attachedFile.file_name }}
                  </a>
                </li>
              </ul>
            </label>
          </div>

        </div>
      </div>
    </form>

    <div class="reveal-footer text-right">
      <!-- <button type="button" class="secondary ins-button"
        data-close aria-label="Close modal" v-on:click="clearForm">
        <span aria-hidden="true">CANCEL</span>
      </button>

      <button class="primary ins-button"
        v-on:click="validateForm"
        v-bind:disbaled="validatingForm"
        type="button">

        <span aria-hidden="true">{{ activityMode }} ACTIVITY</span>
      </button> -->

      <ins-button type="button" label="CANCEL" v-on:click="clearForm"></ins-button>
      <ins-button type="button"
        v:bindlabel="activityMode + ' ' + ACTIVITY"
        v-on:click="validateForm"
        v-bind:disabled="validatingForm">
      </ins-button>
    </div>

  </div>
</template>

<script>
/*eslint no-console: ["error", { allow: ["warn", "error", "log"] }] */
import * as _ from "lodash";
import $ from "jquery";
import "timepicker/jquery.timepicker.min.js";
import "timepicker/jquery.timepicker.min.css";
import Dropzone from "dropzone/dist/dropzone";
// import ActivitySelect2 from "./ActivitySelect2.vue";
import swal from "sweetalert2";
import moment from "moment";

import InsInputWrap from "../components/InsInputWrap.vue";
import InsSelectWrap from "../components/InsSelectWrap.vue";
import InsTextareaWrap from "../components/InsTextareaWrap.vue";

export default {
  name: "ActivityModal",
  components: {
    InsInputWrap,
    InsSelectWrap,
    InsTextareaWrap
  },
  computed: {
    activityStore() {
      return this.$store.state.CrmStore.activity;
    },
    activityMode() {
      return this.$store.state.CrmStore.mode;
    }
  },
  watch: {
    activityStore(){
      const vm = this;
      vm.activityModel = JSON.parse(JSON.stringify(vm.$store.state.CrmStore.activities.activity));
  
      if (vm.activityMode === 'ADD') {
        vm.tempDuration =  "01:00";
        vm.tempDate =  moment().format("MM/DD/YYYY");
        vm.tempTime =  moment().format("hh:mm A");
      }

      if (vm.activityMode === 'UPDATE'){

        let selectedIds = vm.activityModel.attributes.attendees_id.split(',');
        $(".jq-select2").select2("val", selectedIds);

        if (vm.activityModel.attributes.file_name) {

          let fileNames = vm.activityModel.attributes.file_name.split(',');
          let fileNotes = vm.activityModel.attributes.file_note.split(',');

          fileNames.forEach((file, index) => {
            let tempObj = {
              file_name: file,
              file_note: fileNotes[index],
              uuid: vm.guid()
            }
            vm.attachedFiles.push(tempObj);
          });
          
        }
      }
    }
  },
  // components: { ActivitySelect2 },
  data() {
    return {
      initialised: false,
      fetchedStaffs: [],
      activityModel: {},
      tempDuration: "01:00",
      tempDate: moment().format("MM/DD/YYYY"),
      tempTime: moment().format("hh:mm A"),
      dzHasError: false,
      dzErrorMessage: "",
      files: [],
      msgFile: false,
      validatingForm: false,
      dzEl: undefined,
      attachedFiles: []
    };
  },
  props: {
    activity: Object
  },
  mounted() {
    const vm = this;
    // vm.getStaffs();
    vm.initialiseModal();
  },
  beforeDestroy(){
    const vm = this;
    vm.clearForm();
  },
  methods: {
    dateChangeHandler(event) {
      let vm = this;
      vm.tempDate = event.detail.value;
    },
    initialiseModal(){
      const vm = this;
      vm.activityModel = JSON.parse(JSON.stringify(this.$store.state.CrmStore.activity));
      vm.initialised = true;
      $(function() {
        $("#activityTime")
          .timepicker({
            step: 5,
            scrollDefault: "now",
            timeFormat: "h:i A"
          })
          .on("changeTime", function(event) {
            vm.activityModel.attributes.time = event.target.value;
          });

        // eslint-disable-next-line
        $("#activityDuration")
          .timepicker({
            step: 5,
            appendTo: "body",
            timeFormat: "H:i"
          })
          .on("changeTime", function(event) {
            vm.activityModel.attributes.duration = event.target.value;
          });

        // eslint-disable-next-line 
        vm.dzEl = new Dropzone("#activityAttachments", {
          url: "/",
          method: "post",
          autoProcessQueue: false,
          uploadMultiple: true,
          addRemoveLinks: true,
          paramName: "file",
          acceptedFiles:
            ".doc, .docx, .pdf, .csv, .xml, .txt, .jpeg, .JPEG, .jpg, .JPG, .png, .PNG, .xls, .xlsx, .ppt, .pptx, .msg, .eml, .oft, .mht, .zip",
          maxFiles: 999,
          maxFilesize: 10,
          filesizeBase: 1024,
          dictDefaultMessage: `
              Drop files here or Click to choose files<br>
              (Max file size 10MB)
          `,
          dictRemoveFile: `remove`,
          init: function() {
            let self = this;

            self.on("maxfilesexceeded", function(file) {
              self.removeAllFiles();
              self.addFile(file);
            });

            self.on("addedfile", function(file) {
              file.uuid = vm.guid();

              let validFileExtensions = [
                "doc",
                "docx",
                "pdf",
                "csv",
                "xml",
                "txt",
                "jpeg",
                "JPEG",
                "jpg",
                "JPG",
                "png",
                "PNG",
                "xls",
                "xlsx",
                "ppt",
                "pptx",
                "msg",
                "eml",
                "oft",
                "mht"
              ];

              let getFileExtension = file.name.slice(
                ((file.name.lastIndexOf(".") - 1) >>> 0) + 2
              );
              let isFileValid = false;

              let item = {
                file_name: "",
                file_note: "",
                uuid: "",
                is_msg: false
              };

              validFileExtensions.forEach(value => {
                if (value === getFileExtension) {
                  isFileValid = true;
                  return;
                }
              });

              if (getFileExtension == "msg" || getFileExtension == "eml") {
                item.is_msg = true;
              }

              let validateFileSize = file.size < 10999999 ? true : false;

              if (isFileValid && validateFileSize) {
                vm.dzHasError = false;

                let reader = new FileReader();

                reader.onload = evt => {
                  item.file_note = evt.target.result;

                  // if (vm.msgFile) {
                  //     item.file_note = item.file_note.replace('data:;', 'data:application/vnd.ms-outlook;');
                  //     item.is_msg = true;
                  // }

                  item.file_name = file.name;
                  item.uuid = file.uuid;

                  vm.files.push(item);
                  vm.msgFile = false;
                };

                reader.readAsDataURL(file);
              } else if (!validateFileSize) {
                vm.fileName = "";
                vm.dzErrorMessage = "Max file size is 10MB.";
                vm.dzHasError = true;
              }
            });

            self.on("removedfile", function(file) {
              _.remove(vm.files, { uuid: file.uuid });
            });

            self.on("error", function(file) {
              self.removeFile(file);
            });
          }
        });
      });
    },
    getStaffs() {
      let vm = this;
      let baseUrl = this.apiBaseUrl + "/staffs";
      // let headers = this.apiHeader.headers;

      fetch(baseUrl, {
        method: "GET",
        // headers: headers
      }).then(response => {
        return response.json();
      }).then(data => {
        // console.log('staffs', data);
        vm.fetchedStaffs = data.data;
        vm.initialiseModal();
      }).catch(err => {
        // console.log("error: ", err);
      });
    },
    changeType(type) {
      const vm = this;
      vm.activityModel.attributes.type = type;
    },
    removeLocKeys(item) {
      if (item.is_msg) {
        item.file_note = item.file_note.replace(
          "data:;",
          "data:application/vnd.ms-outlook;"
        );
      }
      delete item["uuid"];
      delete item["is_msg"];
      return item;
    },
    validateForm() {
      const vm = this;
      vm.validatingForm= true;

      if (vm.activityModel.attributes.type === "file_note") {
        if (vm.files.length === 0) {
          vm.dzHasError = true;
          vm.dzErrorMessage = "Attachment is required";
        } else {
          vm.activityModel.attributes.attachment = vm.files.map(
            vm.removeLocKeys
          );
        }
      }

      let selectedIds = $(".jq-select2").select2("val");
      let selectedAttendeesIds = [];
      let selectedAttendees = [];
      let pos_id = localStorage.getItem('pos_id');

      selectedIds.forEach(id => {

        let staff = vm.fetchedStaffs.find(staff => staff.id === id);
        selectedAttendeesIds.push(staff.id);
        selectedAttendees.push(
          staff.attributes.first_name + " " + staff.attributes.last_name
        );

        if (pos_id === staff.attributes.pos_id && vm.activityMode === 'ADD') {
          vm.activityModel.attributes.first_name = staff.attributes.first_name;
          vm.activityModel.attributes.last_name = staff.attributes.last_name;
          vm.activityModel.attributes.entity_id = staff.attributes.pos_id;
          vm.activityModel.attributes.staff_id = staff.id;
        }
      });

      vm.activityModel.attributes.attendees_id = selectedAttendeesIds.join(",");
      vm.activityModel.attributes.attendees = selectedAttendees.join(",");

      let durationArr = vm.tempDuration.split(':');
      let hours = parseInt(durationArr[0]) * 3600;
      let mins = parseInt(durationArr[1]) * 60;
      let totalDuration = hours + mins;
      vm.activityModel.attributes.duration = totalDuration;

      let dateTime = `${vm.tempDate} ${vm.tempTime}`;
      vm.activityModel.attributes.time = moment(dateTime, 'MM/DD/YYYY hh:mm A').format('HH:mm:ss');
      vm.activityModel.attributes.date = moment(vm.tempDate).format('YYYY-MM-DD');

      if (vm.activityMode === 'ADD'){
        vm.postActivity();
      } else if (vm.activityMode === 'UPDATE') {
        vm.putActivity();
      }
    },
    postActivity() {
      const vm = this;
      let id = vm.$router.history.current.params.id;
      let baseUrl = vm.apiBaseUrl + "/checks/" + id + "/activities";
      // let headers = vm.apiHeader.headers;
      let payload = vm.activityModel.attributes;
      
      $.ajax({
        url: baseUrl,
        method: "POST",
        // headers: headers,
        data: JSON.stringify(payload),
        success(response){
          // console.log(response);
          // eslint-disable-next-line
          jQuery('#activityModal').foundation('close');
          let id = this.$router.history.current.params.id;
          let baseUrl = vm.apiBaseUrl + "/checks/" + id + "/activities";
          vm.$store.dispatch('getTimeline', {url: baseUrl});
          swal("", "Activity added successfully.", "success");
          vm.validatingForm = false;
        },
        error(err){
          console.log('error: ', err);
          swal("", "Failed to add activity. Please try again.", "error");
          vm.validatingForm = false;
        }
      });
    },
    putActivity() {
      const vm = this;
      let baseUrl = `${vm.apiBaseUrl}/activities/${vm.activityModel.id}`;
      // let headers = vm.apiHeader.headers;
      let payload = vm.activityModel.attributes;
      
      $.ajax({
        url: baseUrl,
        method: "PUT",
        // headers: headers,
        data: JSON.stringify(payload),
        success(response){
          // console.log(response);
          // eslint-disable-next-line
          jQuery('#activityModal').foundation('close');
          let id = this.$router.history.current.params.id;
          let baseUrl = this.apiBaseUrl + "/checks/" + id + "/activities";
          this.$store.dispatch('getTimeline', {url: baseUrl});
          swal("", "Activity updated successfully.", "success");
          vm.validatingForm = false;
        },
        error(err){
          // console.log('error: ', err);
          swal("", "Failed to update activity. Please try again.", "error");
          vm.validatingForm = false;
        }
      });
    },
    guid() {
      return (
        this.s4() + this.s4() + "-" + this.s4() + "-" + this.s4() + "-" +
        this.s4() + "-" + this.s4() + this.s4() + this.s4()
      );
    },
    s4() {
      return Math.floor((1 + Math.random()) * 0x10000)
        .toString(16)
        .substring(1);
    },
    clearForm(){
      const vm = this;
      vm.activityModel = {
        attributes: {
          attendees_id: [],
          attendees: [],
          attachment: [],
          client_type: "",
          company_uuid: "",
          site_id: "",
          date: moment().format("DD/MM/YYYY"),
          duration: "",
          entity_id: "",
          file_name: "",
          file_note: "",
          first_name: "Shane",
          staff_id: "76418",
          last_name: "McGeorge",
          notes: "",
          subject: "",
          time: moment().format("hh:mm A"),
          type: "meeting"
        },
        type: "activities"
      }
      vm.tempDuration = "01:00";
      vm.dzEl.removeAllFiles(true);
      $(".jq-select2").select2("val", []);
      vm.attachedFiles = [];
    }
  }
};
</script>

<style lang="scss" scoped>
.activity-type-cell {
  cursor: pointer;

  &:hover {
    background-color: #f9f9f9;
  }
  &:active {
    background-color: #f0f0f0;
  }
}

.icon-wrap {
  text-align: center;
  padding: 0.9375rem;

  .activity-label {
    font-family: "Open Sans", sans-serif;
    font-size: 14px;
    margin: 0.9375rem 0;
    color: #8c94a4;
  }

  &.active .activity-label {
    color: #0a2435;
  }
}

.icon-radius {
  width: 64px;
  height: 64px;
  border: solid 1px #8c94a4;
  color: #8c94a4;
  border-radius: 50%;
  margin: 0 auto;
  position: relative;

  .activity-icon {
    position: absolute;
    top: calc(50% - 8px);
    left: calc(50% - 8px);
    width: 16px;
    height: 16px;
    display: block;
  }

  &.meeting {
    border-color: #1e86e3;
    color: #1e86e3;

    &:hover,
    .active & {
      color: #fff;
      background-color: #1e86e3;

      .activity-icon:before {
        content: "\e942";
        font-size: 14px;
      }
    }
  }
  &.call {
    border-color: #24be8d;
    color: #24be8d;

    &:hover,
    .active & {
      color: #fff;
      background-color: #24be8d;

      .activity-icon:before {
        content: "\e91b";
      }
    }
  }
  &.email {
    border-color: #59d2ec;
    color: #59d2ec;

    &:hover,
    .active & {
      color: #fff;
      background-color: #59d2ec;

      .activity-icon:before {
        content: "\e938";
        font-size: 14px;
      }
    }
  }
  &.event {
    border-color: #fdd835;
    color: #fdd835;

    &:hover,
    .active & {
      color: #fff;
      background-color: #fdd835;

      .activity-icon:before {
        content: "\e932";
      }
    }
  }
  &.file-note {
    border-color: #f27474;
    color: #f27474;

    &:hover,
    .active & {
      color: #fff;
      background-color: #f27474;

      .activity-icon:before {
        content: "\e91a";
      }
    }
  }
}
.reveal-footer {
  border-top: solid 1px #eee;

  .ins-button {
    margin-left: 20px;
  }
}
.attached-files-wrap ul {
  list-style: none;
  margin: 0;
}
</style>