<template>
  <div class="google-map-direction-wrap">
    <div class="map-wrap">
      <div v-if="googleMapsApiKey.results.length && !address" class="no-location" v-bind:style="`background-image: ${bgImg}`">
        Map not Available
      </div>
      <div v-if="!googleMapsApiKey.results.length && !address" class="no-location" v-bind:style="`background-image: ${bgImg}`">
        No Google Maps API Key
      </div>
      <div v-if="address" id="profileMap">
      </div>
    </div>
    <div class="address-wrap">
      <span>{{ address }}</span>
    </div>
    <div v-if="address" class="direction-wrap">
      <a target="_blank" v-bind:href="checkURL(`http://www.google.com/maps/search/${address}`)">
        <i class="icon-location-arrow"></i>
        <span>Get Directions</span>
      </a>
    </div>
  </div>
</template>
<script>
import { mapState } from 'vuex';
export default {
  name: "GoogleMapDirection",
  props: [
    'address',
    'location'
  ],
  computed: mapState({
    googleMapsApiKey: state => state.SystemFieldsStore.google_maps_api_key,
  }),
  data() {
    return { bgImg: '' }
  },
  methods: {
      checkURL(url){
          if (url.includes('https://')){
              return url;
          } else if (url.includes('http://')){
              return url;
          } else {
              return `//${url}`;
          }
      }
  },
  mounted(){
    if (document.location.hostname === 'localhost'){
      this.bgImg = '/assets/google-map-placeholder';
    }
  }
}
</script>
<style lang="scss" scoped>
  .google-map-direction-wrap {
    padding-left: 20px;

    .map-wrap, #profileMap {
      height: 186px;
      border: solid 1px #E4E6EC;
      box-sizing: border-box;
      position: relative;

      .no-location {
        text-transform: uppercase;
        letter-spacing: 1px;
        font-size: 14px;
        font-weight: 600;
        text-align: center;
        color: #E4E6EC;
        position: absolute;
        width: 100%;
        top: calc(50% - 12px);
      }
    }

    .address-wrap {
      font-size: 14px;
      color: #8C94A4;
      padding: 4px 0px;
    }

    .direction-wrap {
      line-height: 14px;

      a {
        color: #1e86e3;
        font-size: 12px;

        span {
          margin-left: 5px;
        }
      }
    }
  }
</style>