<template>
<div>
  <div v-if="formType === 'my-profile' || formType === 'change-password'" class="large-12 cell">
    <!-- profile form -->
    <div class="grid-container-full">
      <div class="grid-x grid-padding-x">
        <form id="userFormId" name="userForm" class="large-5 medium-6 small-12 cell no-pd-left"
          action="#" data-abide data-live-validate="true" novalidate onsubmit="event.preventDefault()"
          v-on:submit="bindValues">

          <ins-card steady>
            <div class="grid-x grid-padding-x">
              <div class="large-12 cell">
                <h4 class="ins-card-heading">User Details</h4>
                <InsInputWrap id="userFirstNameField" name="userFirstNameField"
                  placeholder="First Name" label="First Name*" field="text"
                  autocomplete="off" required="true" errorMessage="First Name is required."
                  v-model="userData['First Name']" />
              </div>

              <div class="large-12 cell">
                <InsInputWrap id="userLastNameField" name="userLastNameField"
                  placeholder="Last Name" label="Last Name*" field="text"
                  autocomplete="off" required="true" errorMessage="Last Name is required."
                  v-model="userData['Last Name']" />
              </div>
              <div class="large-12 cell">
                <ins-input-tel label="Mobile Phone" area-code="userDataAreaCode"
                  areacode-placeholder="Area Code" phone-number="userDataPhoneNumber"
                  phonenum-placeholder="Phone Number" ref="insInputTel"
                  v-bind:phonenum-value="phoneNumValue" v-bind:areacode-value="areaCodeValue"
                  v-bind:readonly="formType === 'view'">
                </ins-input-tel>
              </div>
              <div class="large-12 cell">
                <InsInputWrap id="userCompanyField" name="userCompanyField"
                  placeholder="Company" label="Company" field="company"
                  autocomplete="off" v-model="userData['Company']" />

              </div>
            </div>
          </ins-card>
          <div class="grid-x grid-padding-x grid-padding-y">
            <div class="large-12 cell text-right">
              <ins-button type="button" id="cancelButtonDetails" label="Cancel"
                color="blue" outlined="true" size="normal"
                v-on:onClickInsButton='backToList()'
                v-bind:disabled="bindingValues">
              </ins-button>

              <ins-button type="submit" id="submitButtonDetails"
                color="blue" solid="true" size="normal"
                v-bind:label="buttonLabel"
                v-bind:disabled="bindingValues">
              </ins-button>
            </div>
          </div>
        </form>

        <form id="userFormPasswordId" name="userFormPasswordId" class="large-5 medium-6 small-12 cell"
          action="#" data-abide data-live-validate="true" novalidate onsubmit="event.preventDefault()"
          v-on:submit="changePassword">
          <ins-card steady>
            <div class="grid-x grid-padding-x">
              <div class="large-12 cell">
                <h4 class="ins-card-heading">Account Details</h4>
                <InsInputWrap id="userEmailField" name="userEmailField"
                  placeholder="Email" label="Email*" field="email" autocomplete="off"
                  required="true" errorMessage="Email is invalid." ref="userEmailField"
                  v-model="userData['Email']"
                  v-bind:readonly="formType === 'view' || formType === 'my-profile'" />
              </div>
              <div class="large-12 cell">
                <InsInputWrap id="userPasswordField" name="userPasswordField"
                  placeholder="Password" label="Password*" field="Password"
                  autocomplete="off" required="true" errorMessage="Password is required and not less than 6 characters."
                  v-model="password" ref="userPasswordField" />
              </div>
              <!-- <div class="large-12 cell">
                    <InsInputWrap
                      id="userConfirmPasswordField"
                      name="userConfirmPasswordField" 
                      placeholder="ConfirmPassword"  
                      label="Confirm Password*" 
                      field="Password"               
                      autocomplete="off"
                      required="true"
                      v-bind:has-error="password !== confirmPassword"
                      errorMessage="Password does not match."
                      v-model="confirmPassword"
                      ref= "userConfirmPasswordField"
                      />    
                </div> -->
            </div>
          </ins-card>
          <div class="grid-x grid-padding-x grid-padding-y">
            <div class="large-12 cell text-right">
              <ins-button type="submit" id="submitButtonDetails" label="Update Password"
                color="blue" solid="true" size="normal" 
                v-bind:disabled="bindingValues">
              </ins-button>
            </div>
          </div>
        </form>

      </div>
    </div>
  </div>

  <div v-if="formType !== 'my-profile' && formType !== 'change-password'">
    <form id="userFormId" name="userForm" action="#" data-abide
      data-live-validate="true" novalidate onsubmit="event.preventDefault()"
      v-on:submit="bindValues">

      <div class="grid-container-full">
        <div class="grid-x grid-padding-x">
          <div class="large-10 medium-12 small-12">

            <ins-card steady>
              <div class="grid-container-full">
                <div class="grid-x grid-padding-x">

                  <div class="large-6 medium-6 small-12 cell">
                    <div class="grid-x grid-padding-x">
                      <div class="large-12 cell">
                        <h4 class="ins-card-heading">User Details</h4>
                        <InsInputWrap id="userFirstNameField" name="userFirstNameField"
                          placeholder="First Name" label="First Name*" field="text"
                          autocomplete="off" required="true" errorMessage="First Name is required."
                          v-model="userData['First Name']" v-bind:readonly="formType === 'view'" />
                      </div>

                      <div class="large-12 cell">
                        <InsInputWrap id="userLastNameField" name="userLastNameField"
                          placeholder="Last Name" label="Last Name*" field="text"
                          autocomplete="off" required="true" errorMessage="Last Name is required."
                          v-model="userData['Last Name']" v-bind:readonly="formType === 'view'" />
                      </div>
                      <div class="large-12 cell">
                        <ins-input-tel label="Mobile Phone" area-code="userDataAreaCode"
                          areacode-placeholder="Area Code" phone-number="userDataPhoneNumber"
                          phonenum-placeholder="Phone Number" ref="insInputTel"
                          v-bind:phonenum-value="phoneNumValue"
                          v-bind:areacode-value="areaCodeValue" v-bind:readonly="formType === 'view'">
                        </ins-input-tel>
                      </div>
                      <div class="large-12 cell">
                        <InsInputWrap id="userCompanyField" name="userCompanyField"
                          placeholder="Company" label="Company" field="company"
                          autocomplete="off" v-model="userData['Company']"
                          v-bind:readonly="formType === 'view'" />

                      </div>
                    </div>

                  </div>

                  <div class="large-6 medium-6 small-12 cell">
                    <div class="grid-x grid-padding-x">
                      <div class="large-12 cell">
                        <h4 class="ins-card-heading">Account Details</h4>
                        <InsInputWrap id="userEmailField" name="userEmailField"
                          placeholder="Email" label="Email*" field="email"
                          autocomplete="off" required="true" errorMessage="Email is invalid."
                          ref="userEmailField" v-model="userData['Email']"
                          v-bind:readonly="formType === 'view' || formType === 'my-profile'"  />
                      </div>
                      <div v-if="formType === 'add'" class="large-12 cell">
                        <InsInputWrap id="userPasswordField" name="userPasswordField"
                          placeholder="Password" label="Password*" field="Password"
                          autocomplete="off" required="true" errorMessage="Password is required and not less than 6 characters."
                          v-model="password" ref="userPasswordField" />
                      </div>
                    </div>
                  </div>

                </div>
              </div>
            </ins-card>
          </div>
        </div>
      </div>
      <div class="grid-container-full">
        <div class="grid-x grid-padding-y">
          <div class="large-10 text-right cell submit-btn-wrap inline">
            <ins-button type="button" id="cancelButtonDetails" label="Cancel"
              color="blue" outlined="true" size="normal"
              v-on:onClickInsButton='backToList()'
              v-bind:disabled="bindingValues">
            </ins-button>

            <ins-button type="submit" id="submitButtonDetails" v-if="formType !== 'view'"
              v-bind:label="buttonLabel" color="blue" solid="true" size="normal"
              v-bind:disabled="bindingValues">
            </ins-button>

            <ins-button type="button" id="saveAddMoreButtonDetails" v-if="formType == 'add'"
              label="Save and Create Another" color="blue" solid="true" size="normal"
              v-bind:disabled="bindingValues"
              v-on:onClickInsButton="saveAddMore()">
            </ins-button>
          </div>
        </div>
      </div>
    </form>

  </div>
</div>
</template>

<script>
import moment from "moment";
import InsInputWrap from "../components/InsInputWrap.vue";

export default {
  name: "ViewUserDetails",
  components: {
    InsInputWrap
  },

  data() {
    return {
      updateExistUrl: '',
      userData: {},
      formType: this.$store.state.AdminStore.formType,
      formMethod: "",
      formName: "",
      fromURL: "/api/users",
      authToken: "",
      location: "",
      userCountryCode: "au",
      password: "",
      confirmPassword: "",
      buttonLabel: "Create",
      addMore: false,
      formModel: {
        form_configuration_name: "delete_admin_user",
          form: {
            profiles_attributes: {
              administrator_attributes: {
                properties_attributes: {
                  is_enabled: "true"
                }
              }
            }
          }
      },
      headers: {
        "X-CSRF-Token": "3b70c2a1087ba76b70ae54f620cd8710da18ba55",
        "X-Requested-With": "XMLHttpRequest",
        "Accept": "application/json",
        "Content-Type": "application/json"
      },
      toastMsg: "",
      toastOpt: {
        closeHtml: '<button><i class="icon-close-1"></i></button>',
        closeButton: true,
        timeOut: 0,
        extendedTimeOut: 0
      },
      sourceProfile: "/api/get-current-user-data.json",
      areaCodeValue: "",
      phoneNumValue: "",
      bindingValues: false
    };
  },
  methods: {
    backToList() {
      if (this.formType === "my-profile"){
        document.querySelector('ins-sidebar-item').routePageHandler();
      } else {
        this.$router.push("/admin-users");
      }
    },
    saveAddMore() {
      this.addMore = true;
      this.bindValues();
    },
    onInputHandler(event) {
      event.target.id === "userNameField";
    },
    // validate form
    formValidate(form) {
      let missing_fields = 0;
      let _self = this;

      Array.from(form.getElementsByClassName("ins-form-field")).forEach(
        function(_field) {
          if (
            _field.hasAttribute("required") === true ||
            _field.getAttribute("required") == "true"
          ) {
            missing_fields += _self.validateInput(_field);
          }
          if (_field.getAttribute("type").toLowerCase() === "email") {
            missing_fields += _self.validateEmail(_field);
          }
          if (_field.getAttribute("type").toLowerCase() === "password") {
            missing_fields += _self.validatePassword(_field);
          }
        }
      );

      if (missing_fields > 0) return false;
      return true;
    },
    // validate password
    validatePassword(_field) {
        if (_field.value.trim() === "" || _field.value.length < 6) {
        this.invalidFieldNotif(_field);
        return 1;
      } else {
        this.validFieldNotif(_field);
        return 0;
      }
    },
    // validate input value
    validateInput(_field) {
      if (_field.value.trim() === "" || _field.value === null) {
        this.invalidFieldNotif(_field);
        return 1;
      } else {
        this.validFieldNotif(_field);
        return 0;
      }
    },
    // update material design to show error message
    invalidFieldNotif(_field) {
      _field.closest("ins-input").setAttribute("has-error", true);
    },
    // update material design to remove error message
    validFieldNotif(_field) {
      _field.closest("ins-input").removeAttribute("has-error");
    },
    // check if email is valid
    validateEmail(_field) {
      let re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

      if (re.test(String(_field.value).toLowerCase()) == true) {
        this.validFieldNotif(_field);
        return 0;
      } else {
        this.invalidFieldNotif(_field);
        return 1;
      }
    },
    changePassword() {
      this.formType = "change-password";
      this.bindValues();
    },
    bindValues() {
      this.bindingValues = true;
      let data = {};
      let countryCode = this.$refs.insInputTel.getCountryData().dialCode;
      let form = "";

      if (this.formType === "change-password") {
        form = document.getElementById("userFormPasswordId");
        data = {
          form_configuration_name: this.formName,
          form: {
            email: this.userData["Email"],
            password: this.password
          }
        };
      }
      
      if (this.formType === "add") {
        form = document.getElementById("userFormId");
        data = {
          form_configuration_name: this.formName,
          form: {
            email: this.userData["Email"],
            first_name: this.userData["First Name"],
            last_name: this.userData["Last Name"],
            name: this.userData["First Name"] + " " + this.userData["Last Name"],
            password: this.password,
            profiles_attributes: {
              administrator_attributes: {
                properties_attributes: {
                  is_enabled: "true",
                  company: $("#userCompanyField").val()
                }
              },
              contact_number_attributes: {
                properties_attributes: {
                  mobile_phone_country_code: countryCode,
                  mobile_phone_area_code: document.getElementsByName(
                    "userDataAreaCode"
                  )[0].value,
                  mobile_phone_number: document.getElementsByName(
                    "userDataPhoneNumber"
                  )[0].value
                }
              }
            }
          }
        };
      } else {
        form = document.getElementById("userFormId");
        data = this.constructPayload(this.formName);
      }

      if (this.formType === "add") {
        this.toastMsg = "User";
      } else if (this.formType === "change-password") {
        this.toastMsg = "Your Password";
      } else {
        // edit user
        this.addMore = false;
        this.toastMsg = "Your user";
      }

      if (this.formValidate(form)) {
        this.submitForm(data);
      } else {
        this.bindingValues = false;
      }
    },
    constructPayload(form_configuration_name){
      let countryCode = this.$refs.insInputTel.getCountryData().dialCode;
      return {
        form_configuration_name,
        form: {
          email: this.userData["Email"],
          first_name: this.userData["First Name"],
          last_name: this.userData["Last Name"],
          name: this.userData["First Name"] + " " + this.userData["Last Name"],
          profiles_attributes: {
            administrator_attributes: {
              properties_attributes: {
                is_enabled: "true",
                company: $("#userCompanyField").val()
              }
            },
            contact_number_attributes: {
              properties_attributes: {
                mobile_phone_country_code: countryCode,
                mobile_phone_area_code: document.getElementsByName(
                  "userDataAreaCode"
                )[0].value,
                mobile_phone_number: document.getElementsByName(
                  "userDataPhoneNumber"
                )[0].value
              }
            }
          }
        }
      };
    },
    submitForm(data, urlToUse, methodToUse) {
      let self = this;
      let method = "";
      let headers = {
        "X-Requested-With": "XMLHttpRequest",
        "X-CSRF-Token": this.authToken,
        Accept: "application/json",
        "Content-Type": "application/json"
      };

      let options = {
        headers,
        method: methodToUse ? methodToUse : this.formMethod,
        data: JSON.stringify(data),
        success: response => {
          self.successHandler();
        },
        error: error => {
          self.errorHandler(error);
        }
      };

      let url = urlToUse ? urlToUse : this.formURL;

      if (this.formType == "change-password") {
        $.ajax(url, options);
      } else {
        $.ajax(url, options);
      }     
    },
    checkIfAdministrator() {
      let getAdminData = `${this.location}/admin/get-customer-profile-data.json?email=${this.userData['Email']}`;
      return this.$http.get(getAdminData).then(response => {  
        let userExist = response.data.items;
        if (userExist){

            let hasAdminDetails = userExist.results[0].administrator_details;

            if (hasAdminDetails){
              let isEnabled = hasAdminDetails.is_enabled;
              this.updateExistUrl = `${this.location}/api/users/${userExist.results[0].id}`;
              return  isEnabled ? isEnabled === 'true' ? true : false : false;
            }

          return false;
        }
        return false;
      })
      .catch(error => {
        toastIt("error", "Failed to create admin user. Please try again.", this.toastOpt);
      });
    },
    setAdministrator() {
      let data = this.constructPayload('set_administrator');
      this.submitForm(data, this.updateExistUrl, 'PUT');
    },
    successHandler() {
      toastIt(
        "success",
        `${this.toastMsg} has been ${
          this.formType === "add" ? "added" : "updated"
        }.`,
        this.toastOpt
      );
      document.getElementById("userFormId").reset();
      this.userData["First Name"] = "";
      this.userData["Last Name"] = "";
      this.userData["Company"] = "";
      this.userData["Email"] = "";
      this.password = "";

      if (this.formType === "my-profile"){
        document.querySelector('ins-sidebar-item').routePageHandler();
      } else {
        !this.addMore ? router.push("/admin-users") : "";
      }

      this.addMore = false;
      this.bindingValues = false;
    },
    
    async errorHandler(error) {
        let errorMsg = "";
        let self = this;
        if (error.responseJSON.errors) {
          if (
            error.responseJSON.errors.email !== "undefined" &&
            error.responseJSON.errors.email !== null &&
            "email" in error.responseJSON.errors
          ) {
            if (error.responseJSON.errors.email[0].toLowerCase() == 'has already been taken') {
              let isAdmin = await self.checkIfAdministrator();

              if (isAdmin) {
                  toastIt("error", "User is already an administrator", this.toastOpt);
                  this.$router.push("/admin-users");
              } else {
                  swal({
                    title: "",
                    html: `
                      <ins-loader
                        state-icon="warning"
                        icon-color="warning"
                        state-message="Email already exists, do you want to set them as an Administrator? This will overwrite their details except for the password.">
                      </ins-loader>`,
                    type: "",
                    showCancelButton: true,
                    confirmButtonText: "Yes"
                  }).then(result => {
                    if (result.value){
                      this.setAdministrator();
                    } else if (result.dismiss) {
                      this.bindingValues = false;
                    }
                  });
              }
              
            } else {
              errorMsg += error.responseJSON.errors.email
                ? "- Email " + error.responseJSON.errors.email[0] + "<br />"
                : "";
            }
          }

          if (
            error.responseJSON.errors.password !== "undefined" &&
            error.responseJSON.errors.password !== null &&
            "password" in error.responseJSON.errors
          ) {
            errorMsg += error.responseJSON.errors.password
              ? "- Password " + error.responseJSON.errors.password[0]
              : "";
          }
        }

        // this.enableSubmitButtons();
        toastIt("error", errorMsg, this.toastOpt);
    },
    enableSubmitButtons() {
      // document
      //   .getElementById("submitButtonDetails")
      //   .setAttribute("disabled", false);
      // if (this.formType === "add") {
      //   document
      //     .getElementById("saveAddMoreButtonDetails")
      //     .setAttribute("disabled", false);
      // }
      // document
      //   .getElementById("cancelButtonDetails")
      //   .setAttribute("disabled", false);
    },
    disableSubmitButtons() {
      // document
      //   .getElementById("submitButtonDetails")
      //   .setAttribute("disabled", true);
      // document
      //   .getElementById("saveAddMoreButtonDetails")
      //   .setAttribute("disabled", true);
      // document
      //   .getElementById("cancelButtonDetails")
      //   .setAttribute("disabled", true);
    },
    setPhoneValue() {
      let self = this;
      setTimeout(() => {
        $(".phone").intlTelInput(
          "setNumber",
          "+" + self.userData["Mobile Phone Country Code"]
        );
        self.areaCodeValue = self.userData["Mobile Phone Area Code"];
        self.phoneNumValue = self.userData["Mobile Phone Number"];
      }, 300);
    },
    setFormSettings() {
      let self = this;
      if (this.formType === "add") {
        
        this.buttonLabel = "Create";
        this.formMethod = "POST";
        this.formURL = "/api/users";
        this.formName = "admin_sign_up";
        this.userData = {};
        this.$refs.userEmailField.disabled = false
        // document.getElementsByName("userEmailField")[0].disabled = false;

      } else if (this.formType === "view") {

        this.buttonLabel = "Update";
        this.userData = this.$store.state.AdminStore.userData;
        this.formMethod, this.formURL, (this.formName = "");
        let form = document.getElementById("userFormId");
        let elements = form.elements;

        // for (var i = 0, len = elements.length; i < len; ++i) {
        //   elements[i].readOnly = true;
        //   elements[i].disabled = true;
        // }

        this.setPhoneValue();

      } else if (this.formType === "edit") {

        this.buttonLabel = "Update";
        this.userData = self.$store.state.AdminStore.userData;
        this.formMethod = "PUT";
        this.formURL = `${this.location}/api/users/${this.userData.id}`;
        this.formName = "admin_update_profile";

        this.setPhoneValue();

      } else if (this.formType === "remove") {
        this.userData = this.$store.state.AdminStore.userData;
      } else if ( this.formType === "my-profile" || this.formType === "change-password") {
        this.getProfileData();
        this.buttonLabel = "Update Details";
        this.formMethod = "PUT";
        this.formName = "admin_update_profile";
        this.setPhoneValue();
      } else {
        this.$router.push("/overview");
      }
    },
    getProfileData() {
      let self = this;
      this.$http.get(this.sourceProfile).then(response => {
        if (response) {
          let data = response.data.current_user;
          let tempObj = {};
          let mobilePhone;

          if (data.contact_number  === null) {
            mobilePhone = "";
          } else {
            let mobileCountryCode = data.contact_number.mobile_phone_country_code !== null ? '+' + data.contact_number.mobile_phone_country_code : '';
            let mobileAreaCode = data.contact_number.mobile_phone_area_code !== null ? data.contact_number.mobile_phone_area_code : '';
            let mobilePhoneNumber = data.contact_number.mobile_phone_number !== null ? data.contact_number.mobile_phone_number : '';
            mobilePhone = `${mobileCountryCode} ${mobileAreaCode} ${mobilePhoneNumber}`;
          }

          if (data) {
            tempObj = {
              id: data.id,
              "First Name": data.first_name,
              "Last Name": data.last_name,
              Name: data.first_name + " " + data.last_name,
              Email: data.email,
              Role: "Administrator",
              Company: data.administrator.company_name === null ? "" : data.administrator.company_name,
              "Mobile Phone": mobilePhone,
              "Mobile Phone Country Code": data.contact_number === null ? "" : data.contact_number.mobile_phone_country_code,
              "Mobile Phone Area Code": data.contact_number === null ? "" : data.contact_number.mobile_phone_area_code,
              "Mobile Phone Number": data.contact_number === null ? "" : data.contact_number.mobile_phone_number,
              "Last Activity": moment(data.updated_at).calendar(null, {sameElse: 'Do MMM YYYY'})
            };

            // populate userData
            self.userData = tempObj;
            self.formURL = `${self.location}/api/users/${self.userData.id}`;
            
            self.setPhoneValue();
          }
        } else {
          // user is not logged in
          this.$router.push("/overview");
        }
      });
    },
    setInsRendererLabel(){
      let insRendererEl = document.querySelector('ins-renderer');
      if (insRendererEl){
          
          if (this.formType === "add") {
            insRendererEl.updateRouteLabel('Add New Admin Users');
          } else if (this.formType === "view") {
            insRendererEl.updateRouteLabel('Admin Details');
          } else if (this.formType === "edit") {
            insRendererEl.updateRouteLabel('Update Admin User');
          }

      } else {
        setTimeout(() => {
          this.setInsRendererLabel();
        }, 300);
      }
    }
  },
  mounted() {
    this.setInsRendererLabel();
    setTimeout(() => {
      this.$store.state.AdminStore.userData["Mobile Phone Country Code"] =
        this.$store.state.AdminStore.userData["Mobile Phone Country Code"] === ""
          ? "61"
          : this.$store.state.AdminStore.userData["Mobile Phone Country Code"];

      this.setFormSettings();
    }, 300);

    if (window.location.hostname !== "localhost") {
      this.authToken = JSON.parse(
        document.getElementById("formConfig").innerHTML
      );
    } else {
      this.authToken =
