<template>
  <ins-image-picker
    v-bind:img-type="imgType"
    v-bind:label="label"
    v-bind:button-color="buttonColor"
    v-bind:value="value"
    @valueChange="onValueChange" />
</template>

<script>
export default {
  name: "InsImagePickerWrap",
  props: {
    imgType: String,
    buttonColor: String,
    value: String,
    label: String
  },
  methods: {
    onValueChange(ev) {
      let self = this;
      self.$emit("input", ev.detail.base64);
      self.$emit('fileNameChanged', ev.detail.filename);
    }
  }
};

</script>
<style scoped></style>