<template>
  <div class="grid-container-full">
    <div class="grid-x grid-padding-x" v-if="contactData">
      <div class="cell large-8 medium-12 small-12 margin-bottom-30">
        <ins-card steady>
          <div class="grid-x grid-padding-x">
            <div class="cell large-12 medium-12 small-12">
              <ProfileHead
                v-bind:profile-name="checkValue(contactData['Contact'])"
                v-bind:profile-img="checkValue(contactData['Contact_Img'])"
                v-bind:profile-facebook="checkValue(contactData['Facebook'])"
                v-bind:profile-google-plus="checkValue(contactData['Google'])"
                v-bind:profile-instagram="checkValue(contactData['Instagram'])"
                v-bind:profile-linkedin="checkValue(contactData['Linkedin'])"
                v-bind:profile-twitter="checkValue(contactData['Twitter'])"
                v-bind:profile-youtube="checkValue(contactData['Youtube'])" />

              <div class="cell large-12 medium-12 small-12">
                <div class="grid-x">
                  <!-- <div v-bind:class="`cell ${constructedAddress ? 'large-6':'large-12'} medium-12 small-12 `"> -->
                  <div class="cell large-6 medium-12 small-12">
                    <table class="information-wrap">
                      <!-- <tr>
                        <td class="td-label">Contact Type</td>
                        <td>{{ checkValue(contactData['contact_id']) }}</td>
                      </tr> -->
                      <tr>
                        <td class="td-label">Work Phone</td>
                        <!-- <td>{{ constructPhoneNumber('Work') }}</td> -->
                        <td>{{ checkValue(contactData['Work Phone']) }}</td>
                      </tr>
                      <tr>
                        <td class="td-label">Mobile Phone</td>
                        <!-- <td>{{ constructPhoneNumber('Mobile') }}</td> -->
                        <td>{{ checkValue(contactData['Mobile Phone']) }}</td>
                      </tr>
                      <tr>
                        <td class="td-label">Home Phone</td>
                        <!-- <td>{{ constructPhoneNumber('Home') }}</td> -->
                        <td>{{ checkValue(contactData['Home Phone']) }}</td>
                      </tr>
                      <tr>
                        <td class="td-label">Email</td>
                        <td>{{ contactData['Email'] }}</td>
                      </tr>
                    </table>
                  </div>

                  <div class="cell large-6 medium-12 small-12">
                    <!-- <GoogleMapDirection
                      v-if="constructedAddress"
                      v-bind:address="constructedAddress" /> -->
                    <GoogleMapDirection                     
                      v-bind:address="constructedAddress" />
                  </div>
                  
                </div>
              </div>

              <div class="cell large-12 medium-12 small-12">
                <div class="notes-wrap">
                  {{ contactData['Notes'] }}
                </div>
              </div>

              <div class="cell large-12 medium-12 small-12 text-right">
                <ins-button label="EDIT CONTACT" outlined
                  v-on:onClickInsButton="editContactHandler">
                </ins-button>
              </div>
            </div>
          </div>
        </ins-card>
      </div>
      <div class="cell large-4 medium-12 small-12 activities-if margin-bottom-30">
        <AdminActivities v-bind:activities="activities" />
      </div>
      <div class="cell large-12 medium-12 small-12">
        <ins-button label="ARCHIVE CONTACT" color="negative"
          v-on:onClickInsButton="archiveContact">
        </ins-button>
      </div>
    </div>
  </div>
</template>

<script>
import moment from "moment";

import InsInputWrap from "../components/InsInputWrap.vue";
import InsSelectWrap from "../components/InsSelectWrap.vue";
import AdminActivities from "../components/AdminActivities.vue";
import ProfileHead from "../components/ProfileHead.vue";
import GoogleMapDirection from "../components/GoogleMapDirection.vue";

export default {
    name: "ViewContactDetails",
    components: {
      InsInputWrap,
      InsSelectWrap,
      AdminActivities,
      ProfileHead,
      GoogleMapDirection,
    },
    data() {
      return {
        profileImg: '',  
        activities: undefined,
        contactData: undefined,
        constructedAddress: undefined,
        formModel: {
          form_configuration_name: "crm_delete_customer",
          form: {
            profiles_attributes: {
              customer_attributes: {
                properties_attributes: {
                  is_enabled: "false"
                }
              }
            }
          }
        },
        mapTheme: this.getTheme,
      }
    },
    methods: { 
      constructAddress(){
        let fullAddress = '';

        if (this.contactData['Home Address']) fullAddress = this.contactData['Home Address'];
        if (this.contactData['Home Street']) fullAddress += ` ${this.contactData['Home Street']}`;
        if (this.contactData['Home City']) fullAddress += ` ${this.contactData['Home City']}`;
        if (this.contactData['Home State']) fullAddress += ` ${this.contactData['Home State']}`;
        if (this.contactData['Home Country']) fullAddress += ` ${this.contactData['Home Country']}`;
        if (this.contactData['Home Area Code']) fullAddress += ` ${this.contactData['Home Area Code']}`;

        return fullAddress;
      },
      constructPhoneNumber(field){
        let phoneNumber = '';

        if (this.checkValue(this.contactData.contact_number[field + '_phone_area_code']) ||
            this.checkValue(this.contactData.contact_number[field + '_phone_number'])){

          phoneNumber = `${this.checkValue(this.contactData.contact_number[field + '_phone_country_code']), true}`;
          phoneNumber += ` ${this.checkValue(this.contactData.contact_number[field + '_phone_area_code'])}`;
          phoneNumber += ` ${this.checkValue(this.contactData.contact_number[field + '_phone_number'])}`;

        }

        return phoneNumber.trim();
      },
      checkValue(value, withPlus){
        if (value && value !== 'undefined' && value !== 'null'){
          return value ? `${withPlus ? '+':''}${value}` : '';
        }
        return '';
      },
      editContactHandler(){
        this.$store.commit("pushUserData", this.contactData);
        this.$store.commit("pushFormType", "edit");
        this.$router.push("/manage-crm");
      },
      archiveContact(){
        swal({
          title: "",
          html: `
            <ins-loader
              state-icon="warning"
              icon-color="warning"
              state-message="Are you sure you want to archive these items?">
            </ins-loader>`,
          type: "",
          showCancelButton: true,
          confirmButtonText: "Archive"
        }).then(result => {
          if (result.value) {
            this.deleteContact();
          }
        });
      },
      deleteContact(contact_id) {
        let url = `/api/users/${this.contactData.id}`;      
        let body = JSON.stringify(this.formModel);

        this.$http
          .put(url, body, { headers: this.headers })
            .then(response => {
              toastIt("success", "Contact has been archived.", this.ops);
              this.$router.push('/customers');
            }).catch(error => {
              toastIt("error", "Failed to archive user. Please try again.", this.ops);
            });
      },
      mapInit() {
        this.mapTheme = this.getTheme;
        let profileMap = document.getElementById("profileMap");
        let geocoder = new google.maps.Geocoder();
        let map = new google.maps.Map(profileMap, {
            scrollwheel: false,
            draggable: true,
            disableDoubleClickZoom: true,
            zoomControll: false,            
            zoom: 16,
            styles: this.mapTheme
        });
        // set address location
        this.mapGetCoordinates(geocoder, map);
      },
      mapGetCoordinates(geocoder, resultsMap) {
        let address = this.constructedAddress;
        geocoder.geocode({'address': address}, function(results, status) {
          if (status === 'OK') {
            resultsMap.setCenter(results[0].geometry.location);
            let marker = new google.maps.Marker({
              map: resultsMap,
              position: results[0].geometry.location
            });
          } else {
            console.error('Geocode was not successful for the following reason: ' + status);
          }
        });

      },
      setInsRendererLabel(){
        let insRendererEl = document.querySelector('ins-renderer');
        if (insRendererEl.updateRouteLabel){
          insRendererEl.updateRouteLabel('Contact Details');
        } else {
          setTimeout(() => {
            this.setInsRendererLabel();
          }, 300);
        }
      }
    },
    created() {
      this.contactData = JSON.parse(JSON.stringify(this.$store.state.CrmStore.contactData));
      if (this.contactData){
        this.headers = JSON.parse(JSON.stringify(this.apiHeader));
        // this.profileImg = this.contactData.customer_details.profile_image ? this.contactData.customer_details.profile_image.original : '';
        this.constructedAddress = this.constructAddress();
      } else {
        this.$router.push('/customers');
      }
    },
    mounted() {
      this.setInsRendererLabel();
      if (this.constructedAddress) {
        this.mapInit();
      }
    }
}
</script>

<style lang="scss" scoped>
</style>
