<template>
  <div>
    <div class="grid-container-full">
      <div class="grid-x grid-padding-x">
        <div class="large-12 medium-12 small-12 cell">
          <form id="crmForm" action="#" data-abide data-live-validate="true"
            novalidate onsubmit="event.preventDefault()" enctype="multipart/form-data"
             v-if="showForm"><!-- v-on:submit="fetchContactList" -->
            <ins-card steady>
              <div class="grid-x grid-padding-x ">
                <div class="large-12 medium-12 small-12 cell">
                  <h4 class="ins-card-heading">Profile Picture</h4>
                  <InsImagePickerWrap name="userCustomerProfileImage" type="picture"
                    button-color="blue" v-model="userData['Profile Image']"
                    v-on:fileNameChanged="getFilename">
                  </InsImagePickerWrap>
                </div>

              </div>
              <div class="grid-x grid-padding-x ">
                <div class="large-12 small-12 cell">
                  <h4 class="ins-card-heading">Contact Details</h4>
                </div>
                <div class="large-4 medium-6 small-12 cell">
                  <div class="grid-x grid-padding-x"><!-- align-bottom -->
                    <InsInputWrap id="userFirstNameField" name="userFirstNameField" maxlength="30"
                      class="large-6 medium-6 small-12 cell" placeholder="First Name"
                      label="First Name*" field="text" autocomplete="off" 
                      required="true" errorMessage="First Name is required."
                      v-model="userData['First Name']" 
                      v-on:keyup.native="validateInput" />

                    <InsInputWrap id="userLastNameField" name="userLastNameField" maxlength="30"
                      class="large-6 medium-6 small-12 cell" placeholder="Last Name"
                      label="Last Name*" field="text" autocomplete="off" required="true"
                      errorMessage="Last Name is required." 
                      v-model="userData['Last Name']"
                      v-on:keyup.native="validateInput" />
                  </div>

                  <InsSelectWrap id="userAssignedToField" name="form[properties_attributes][contact_type]"
                    label="Contact Type" placeholder="- Select -" v-model="userData['Contact Type']"
                    searchable>

                    <ins-select-option label="Primary" value="Primary"></ins-select-option>
                    <ins-select-option label="Secondary" value="Secondary"></ins-select-option>

                    <ins-select-option v-for="contactType in contactTypes.results"
                      v-bind:key="contactType.id" v-bind:label="contactType.value"
                      v-bind:value="contactType.id">
                    </ins-select-option>
                  </InsSelectWrap>

                </div>
                <div class="large-4 medium-6 small-12 cell">
                  <ins-input-tel id="workPhone" label="Work Phone" area-code="userDataWorkAreaCode"
                    areacode-placeholder="Area Code" phone-number="userDataWorkPhoneNumber"
                    phonenum-placeholder="Phone Number" ref="workPhone"
                    v-bind:phonenum-value="workPhoneNumValue"
                    v-bind:areacode-value="workAreaCodeValue" v-bind:readonly="formType === 'view'">
                  </ins-input-tel>
                  <ins-input-tel id="mobilePhone" label="Mobile Phone"
                    area-code="userDataMobileAreaCode" areacode-placeholder="Area Code"
                    phone-number="userDataMobilePhoneNumber"
                    phonenum-placeholder="Phone Number" ref="mobilePhone"
                    v-bind:phonenum-value="mobilePhoneNumValue"
                    v-bind:areacode-value="mobileAreaCodeValue" v-bind:readonly="formType === 'view'">
                  </ins-input-tel>
                </div>
                <div class="large-4 medium-6 small-12 cell">
                  <ins-input-tel id="homePhone" label="Home Phone" area-code="userDataHomeAreaCode"
                    areacode-placeholder="Area Code" phone-number="userDataHomePhoneNumber"
                    phonenum-placeholder="Phone Number" ref="homePhone"
                    v-bind:phonenum-value="homePhoneNumValue"
                    v-bind:areacode-value="homeAreaCodeValue" v-bind:readonly="formType === 'view'">
                  </ins-input-tel>
                  <InsInputWrap id="userEmailField" name="userEmailField" maxlength="30"
                    placeholder="Email Address" label="Email Address*" field="email" required
                    autocomplete="off" v-bind:errorMessage="emailErrorMessage" ref="userEmailField"
                    v-model="userData['Email']" 
                    v-on:keyup.native="validateEmail"/>
                </div>
              </div>
              <div class="grid-x grid-padding-x spacer-top-m">
                <div class="large-12 small-12 cell">
                  <h4 class="ins-card-heading">Address</h4>
                </div>
                <div class="large-4 medium-6 small-12 cell">
                  <InsInputWrap id="searchAddress" name="searchAddress"
                    label="Search Address" field="text" ref="searchAddressField" 
                    v-bind:placeholder="googleMapsApiKey.results.length ? 'Search Address':'No Google API Key'"
                    v-bind:disabled="!googleMapsApiKey.results.length" autocomplete="off"
                    v-model="searchAddress" />

                  <div class="grid-x grid-padding-x"> <!-- align-bottom -->
                    <InsInputWrap id="userHomeStreetField" name="userHomeStreetField" maxlength="50"
                      class="large-5 medium-5 small-5 cell" placeholder="Address 1"
                      label="Address 1" field="text" autocomplete="off" v-model="userData['Home Street']" />

                    <InsInputWrap id="userHomeAddressField" name="userHomeAddressField" maxlength="50"
                      class="large-7 medium-7 small-7 cell" placeholder="Address 2" label="Address 2"
                      field="text" autocomplete="off" v-model="userData['Home Address']" />
                  </div>
                </div>
                <div class="large-4 medium-6 small-12 cell">
                  <InsInputWrap id="userHomeCityField" name="userHomeCityField" maxlength="50"
                    placeholder="City" label="City" field="text" autocomplete="off"
                    v-model="userData['Home City']" />
                  <InsInputWrap id="userHomeStateField" name="userHomeStateField" maxlength="50"
                    placeholder="State" label="State" field="text" autocomplete="off"
                    v-model="userData['Home State']" />

                </div>
                <div class="large-4 medium-6 small-12 cell">
                  <InsInputWrap id="userHomeAreaCodeField" name="userHomeAreaCodeField" maxlength="50"
                    class="large-5 medium-5 small-5 cell" placeholder="Post Code"
                    label="Post Code" field="text" autocomplete="off" v-model="userData['Home Area Code']" />
                  <InsInputWrap id="userHomeCountryField" name="userHomeCountryField" maxlength="50"
                    class="large-5 medium-5 small-5 cell" placeholder="Country"
                    label="Country" field="text" autocomplete="off" v-model="userData['Home Country']" />
                </div>
              </div>
              <div class="grid-x grid-padding-x spacer-top-m">
                <div class="large-12 small-12 cell">
                  <h4 class="ins-card-heading">Additional Info</h4>
                </div>
                <div class="large-4 medium-6 small-12 cell">

                  <InsInputWrap id="userFacebookField" name="userFacebookField" maxlength="50"
                    placeholder="Facebook" label="Facebook Profile Link" field="text"
                    autocomplete="off" v-model="userData['Facebook']" />
                  <InsInputWrap id="userTwitterField" name="userTwitterField" maxlength="50"
                    placeholder="Twitter" label="Twitter Profile Link" field="text"
                    autocomplete="off" v-model="userData['Twitter']" />
                  <InsInputWrap id="userYoutubeField" name="userYoutubeField" maxlength="50"
                    placeholder="Youtube" label="YouTube Profile Link" field="text"
                    autocomplete="off" v-model="userData['Youtube']" />
                  <!-- <InsInputWrap id="userGoogleField" name="userGoogleField" maxlength="50"
                    placeholder="Google+" label="Google+ Profile Link" field="text"
                    autocomplete="off" v-model="userData["Google"]" /> -->
                </div>
                <div class="large-4 medium-6 small-12 cell">
                  <InsInputWrap id="userLinkedinField" name="userLinkedinField" maxlength="50"
                    placeholder="Linkedin" label="Linkedin Profile Link" field="text"
                    autocomplete="off" v-model="userData['Linkedin']" />
                  <InsInputWrap id="userInstagramField" name="userInstagramField" maxlength="50"
                    placeholder="Instagram" label="Instagram Profile Link"
                    field="text" autocomplete="off" v-model="userData['Instagram']" />
                </div>
                <div class="large-4 medium-6 small-12 cell">

                  <InsSelectWrap id="userAssignedToField" name="form[properties_attributes][contact_type]"
                    label="Company" placeholder="- Select -" v-if="!mutatingCompanies"
                    dynamic-placeholder="Add New Company" v-model="userData['company_id']"
                    searchable with-dynamic-option v-on:onSubmitOption="onSubmitOptionHandler">

                    <ins-select-option v-if="!companyOptions.length"
                      label="No company yet." value="" disabled>
                    </ins-select-option>

                    <ins-select-option v-for="companyOption in companyOptions"
                      v-bind:key="companyOption.id" v-bind:label="companyOption.label"
                      v-bind:value="companyOption.id">
                    </ins-select-option>
                  </InsSelectWrap>
                    
                  <InsTextareaWrap id="userNoteField" name="userNoteField"
                    placeholder="Notes" label="Notes" autocomplete="off"
                    maxlength="255"
                    v-model="userData['Notes']" />
                </div>
              </div>
            </ins-card>
            <div class="grid-x grid-padding-x grid-padding-y">
              
              <div class="cell large-4 medium-3 small-12" v-if="formType == 'edit'">
                <ins-button label="ARCHIVE CONTACT" color="negative" type="button"
                  v-on:onClickInsButton="archiveContact">
                </ins-button>
              </div>

              <div v-bind:class="`${formType === 'edit' ? 'large-8':'large-12'} medium-9 small-12 cell text-right`">
                <ins-button type="button" label="Cancel" outlined
                  v-bind:disabled="submittingForm" v-on:onClickInsButton='backToList'></ins-button>
                <ins-button type="submit" solid v-bind:label="buttonLabel"
                  v-bind:disabled="submittingForm" v-on:onClickInsButton="fetchContactList"></ins-button>
                <ins-button type="button" label="Save and Create Another" solid v-if="formType == 'add'"
                  v-on:onClickInsButton="saveAddMore" v-bind:disabled="submittingForm"></ins-button>
              </div>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import moment from "moment";
import { mapState } from 'vuex';
import { each } from "lodash";

import InsInputWrap from "../components/InsInputWrap.vue";
import InsSelectWrap from "../components/InsSelectWrap.vue";
import InsImagePickerWrap from "../components/InsImagePickerWrap.vue";
import InsTextareaWrap from "../components/InsTextareaWrap.vue";

export default {
  name: "EditCRMDetails",
  components: {
    InsInputWrap,
    InsTextareaWrap,
    InsSelectWrap,
    InsImagePickerWrap,
  },
  data() {
    return {
      submittingForm: false,
      showForm: false,
      userData: {},
      oldContactType: "",
      userProfile: this.$store.state.CrmStore.userProfile,
      formType: this.$store.state.CrmStore.formType,
      formMethod: "",
      formName: "",
      fromURL: "/api/users",
      searchAddress: "",
      searchAddressField: undefined,
      authToken: "",
      location: "",
      emailErrorMessage: "Invalid email address.",
      userCountryCode: "au",
      buttonLabel: "Add Contact",
      addMore: false,
      toastMsg: "",
      toastOpt: {
        closeHtml: '<button><i class="icon-close-1"></i></button>',
        closeButton: true,
        timeOut: 0,
        extendedTimeOut: 0
      },
      sourceProfile: "/api/get-current-user-data.json",
      workAreaCodeValue: "",
      workPhoneNumValue: "",
      mobileAreaCodeValue: "",
      mobilePhoneNumValue: "",
      homeAreaCodeValue: "",
      homePhoneNumValue: "",
      headers: {},
      formModel: {
        form_configuration_name: "crm_delete_customer",
        form: {
          profiles_attributes: {
            customer_attributes: {
              properties_attributes: {
                is_enabled: "false"
              }
            }
          }
        }
      },
    }
  },
  computed: mapState({
    contactTypes: state => state.SystemFieldsStore.contact_type,
    swalContactTypes: state => state.SystemFieldsStore.swalContactTypes,
    companyOptions: state => state.CrmStore.companyOptions,
    mutatingCompanies: state => state.CrmStore.mutatingCompanies,
    googleMapsApiKey: state => state.SystemFieldsStore.google_maps_api_key
  }),
  methods: {
    onSubmitOptionHandler(event) {
      let data = {
        form_configuration_name: "add_new_company",
        parent_resource_id: "company",
        form: {
          properties: {
            company_name: event,
            is_enabled: "true"
          }
        }
      }
      this.addCompanyOption(data)
    },
    addCompanyOption(data) {
      this.submittingForm = true;
      let parsedData = JSON.stringify(data);
      let url = `/api/customizations`;

      this.$http.post(url, parsedData, { headers: this.headers })
        .then(res => {
          let newCompanyId = res.data.id.toString();
          this.$store.commit('pushCompanyOption', {
            id: newCompanyId,
            label: data.form.properties.company_name
          });
          this.userData['company_id'] = newCompanyId;
          this.submittingForm = false;
          toastIt("success", "Successfully created company.", this.ops);
        })
        .catch(error => {
          console.error('error', error);
          this.submittingForm = false;
          toastIt("error", "Failed to add company.", this.ops);
        });
    },
    getFilename(event) {
      this.profilePictureName = event;
    },
    base64ToBlob(base64, mime) {
      mime = mime || '';
      let sliceSize = 1024;
      let byteChars = window.atob(base64);
      let byteArrays = [];

      for (let offset = 0, len = byteChars.length; offset < len; offset += sliceSize) {
        let slice = byteChars.slice(offset, offset + sliceSize);

        let byteNumbers = new Array(slice.length);
        for (let i = 0; i < slice.length; i++) {
          byteNumbers[i] = slice.charCodeAt(i);
        }

        let byteArray = new Uint8Array(byteNumbers);

        byteArrays.push(byteArray);
      }

      return new Blob(byteArrays, {
        type: mime
      });
    },
    appendToFormData(data) {
      let formData = new FormData();

      each (data, (val, key) => {
        formData.append(key, val);
      });
      return formData;
    },
    backToList() {
      this.$router.go(-1)
    },
    saveAddMore() {
      this.addMore = true;
      // this.bindValues();
      this.fetchContactList();
    },
    attachFile() {
      this.$refs.profilePic.click();
    },
    bindValues() {
      this.submittingForm = true;
      let mobileCountryCode = this.$refs.mobilePhone.getCountryData();
      mobileCountryCode = mobileCountryCode.dialCode;

      let homeCountryCode = this.$refs.homePhone.getCountryData();
      homeCountryCode = homeCountryCode.dialCode;

      let workCountryCode = this.$refs.workPhone.getCountryData();
      workCountryCode = workCountryCode.dialCode;

      let formData = {
        "authenticity_token": this.authToken,
        "form_configuration_name": this.formName,
        "parent_resource_id": this.parentResourceId,
        "form[email]": this.userData["Email"],
        "form[first_name]": this.userData["First Name"],
        "form[last_name]": this.userData["Last Name"],
        "form[name]": `${this.userData["First Name"]} ${ this.userData["Last Name"]}`,
        "form[profiles_attributes][customer_attributes][properties_attributes][company_id]": this.userData["company_id"],
        "form[profiles_attributes][customer_attributes][properties_attributes][contact_type]": this.userData["Contact Type"],
        "form[profiles_attributes][customer_attributes][properties_attributes][notes]": this.userData["Notes"],
        "form[profiles_attributes][customer_attributes][properties_attributes][is_enabled]": "true",
        "form[profiles_attributes][customer_attributes][custom_addresses_attributes][home_address_attributes][street]": this.userData["Home Street"],
        "form[profiles_attributes][customer_attributes][custom_addresses_attributes][home_address_attributes][address]": this.userData["Home Address"],
        "form[profiles_attributes][customer_attributes][custom_addresses_attributes][home_address_attributes][city]": this.userData["Home City"],
        "form[profiles_attributes][customer_attributes][custom_addresses_attributes][home_address_attributes][state]": this.userData["Home State"],
        "form[profiles_attributes][customer_attributes][custom_addresses_attributes][home_address_attributes][postcode]": this.userData["Home Area Code"],
        "form[profiles_attributes][customer_attributes][custom_addresses_attributes][home_address_attributes][country]": this.userData["Home Country"],
        "form[profiles_attributes][contact_number_attributes][properties_attributes][mobile_phone_country_code]": mobileCountryCode,
        "form[profiles_attributes][contact_number_attributes][properties_attributes][mobile_phone_area_code]": document.getElementsByName('userDataMobileAreaCode')[0].value,
        "form[profiles_attributes][contact_number_attributes][properties_attributes][mobile_phone_number]": document.getElementsByName('userDataMobilePhoneNumber')[0].value,
        "form[profiles_attributes][contact_number_attributes][properties_attributes][home_phone_country_code]": homeCountryCode,
        "form[profiles_attributes][contact_number_attributes][properties_attributes][home_phone_area_code]": document.getElementsByName('userDataHomeAreaCode')[0].value,
        "form[profiles_attributes][contact_number_attributes][properties_attributes][home_phone_number]": document.getElementsByName('userDataHomePhoneNumber')[0].value,
        "form[profiles_attributes][contact_number_attributes][properties_attributes][work_phone_country_code]": workCountryCode,
        "form[profiles_attributes][contact_number_attributes][properties_attributes][work_phone_area_code]": document.getElementsByName('userDataWorkAreaCode')[0].value,
        "form[profiles_attributes][contact_number_attributes][properties_attributes][work_phone_number]": document.getElementsByName('userDataWorkPhoneNumber')[0].value,
        "form[profiles_attributes][social_links_attributes][properties_attributes][facebook_link]": this.userData["Facebook"],
        "form[profiles_attributes][social_links_attributes][properties_attributes][twitter_link]": this.userData["Twitter"],
        "form[profiles_attributes][social_links_attributes][properties_attributes][youtube_link]": this.userData["Youtube"],
        "form[profiles_attributes][social_links_attributes][properties_attributes][linkedin_link]": this.userData["Linkedin"],
        "form[profiles_attributes][social_links_attributes][properties_attributes][google_plus_link]": "", // this.userData["Google"]
        "form[profiles_attributes][social_links_attributes][properties_attributes][instagram_link]": this.userData["Instagram"]
      }

      let parsedFormData = this.appendToFormData(formData);
      let profile_img = this.userData['Profile Image'] ? this.userData['Profile Image'] : "";

      if (profile_img !== "" || profile_img !== undefined) {
        let base64ImageContent = profile_img.replace(/^data:image\/(png|jpg|jpeg);base64,/, "");
        let img_type = profile_img.match(/^data:image\/(png|jpg|jpeg);base64,/);

        if (img_type) {
          let blob = this.base64ToBlob(base64ImageContent, "image/" + img_type[1]);
          parsedFormData.append("form[profiles_attributes][customer_attributes][custom_images_attributes][profile_image_attributes][image]", blob, this.profilePictureName);
        }
      }

      let form = document.getElementById("crmForm");
      if (this.formType === "add") {
        this.formURL = `/api/users`;
      }

      this.toastMsg = "Customer";
      if (this.formValidate(form)) {
        this.submitForm(parsedFormData);
      } else {
        this.addMore = false;
        this.submittingForm = false;
      }
    },
    submitForm(data) {
      let self = this;
      let options = {
        type: this.formMethod,
        contentType: false,
        processData: false,
        data: data,
        success: response => {
          self.successHandler();
        },
        error: error => {
          self.errorHandler(error);
        }
      };

      $.ajax(this.formURL, options);
    },
    checkIfContact() {
      let getContactData = `${this.location}/admin/get-customer-profile-data.json?email=${this.userData['Email']}`;
      return this.$http.get(getContactData).then(response => {
          let userExist = response.data.items;

          if (userExist) {

            let hasContactDetails = userExist.results[0].customer_details;

            if (hasContactDetails) {
               return hasContactDetails.is_enabled ? hasContactDetails.is_enabled === 'true' ? 'enabled' : 'disabled' : 'disabled';
            }
            return false;
          }
          return false;
        })
        .catch(error => {
          this.addMore = false;
          this.submittingForm = false;
          toastIt("error", "Failed to add contact. Please try again.", this.ops);
        })
    },
    setAsContact() {
      let setAsContact = `${this.location}/admin/set-as-contact?email=${this.userData['Email']}`;

      this.$http.get(setAsContact)
        .then(response => {
          this.successHandler();
        })
        .catch(error => {
          this.addMore = false;
          this.submittingForm = false;
          toastIt("error", "Failed to add contact. Please try again.", this.ops);
        })
    },
    successHandler() {
      toastIt(
        "success",
        `${this.toastMsg} has been ${this.formType === "add" ? "created" : "updated"}.`,
        this.ops
      );
      document.getElementById("crmForm").reset();
      this.userData["First Name"] = "";
      this.userData["Last Name"] = "";
      this.userData["Email"] = "";
      
      !this.addMore ? this.$router.push("/customers") : "";
      this.addMore = false;
      this.submittingForm = false;
    },
    async errorHandler(error) {
      let errorMsg = "";
      let self = this;
      if (error.responseJSON){

        if (error.responseJSON.errors) {

          if (error.responseJSON.errors.email !== "undefined" &&
              error.responseJSON.errors.email !== null &&
              "email" in error.responseJSON.errors){

            if (error.responseJSON.errors.email[0].toLowerCase() == 'has already been taken') {
              let isContact = await self.checkIfContact();

              if (isContact === 'enabled') {
                this.addMore = false;
                this.submittingForm = false;
                toastIt("error", "Contact already exist.", this.ops);
              } else if (isContact === 'disabled') {
                this.warnUser('This contact is archived, do you want to restore them?');
              } else {
                // this.warnUser('Email already exist, do you want to add them as contact?');
                this.userExistsError('Email is already in use, Please use a different email.');
              }

            } else {
              errorMsg += error.responseJSON.errors.email ?
                "Email " + error.responseJSON.errors.email[0] + "<br />" :
                "";
              this.addMore = false;
              this.submittingForm = false;
              toastIt("error", errorMsg, this.ops);
            }
          }
        }

      } else {
        this.addMore = false;
        this.submittingForm = false;
        toastIt("error", "Failed to add contact.", this.ops);
      }
      
    },
    userExistsError(message) {
        swal({
        title: "",
        html: `
          <ins-loader
              state-icon="error"
              icon-color="error"
              state-message="${message}.">
          </ins-loader>`,
        type: "",
        showCancelButton: false
      });
    },
    warnUser(message) {
      swal({
        title: "",
        html: `
          <ins-loader
              state-icon="warning"
              icon-color="warning"
              state-message="${message} This will use their existing data.">
          </ins-loader>`,
        type: "",
        showCancelButton: true,
        confirmButtonText: "Yes"
      }).then(result => {
        if (result.value) {
          this.setAsContact();
        } else if (result.dismiss) {
          this.bindingValues = false;
          this.addMore = false;
          this.submittingForm = false;
        }
      });
    },
    setPhoneValue() {
      let self = this;

      setTimeout(() => {
        $('#homePhone .phone').intlTelInput(
          "setNumber",
          "+" + self.userData["Home Phone Country Code"]
        );
        self.homeAreaCodeValue = self.userData["Home Phone Area Code"];
        self.homePhoneNumValue = self.userData["Home Phone Number"];

        $('#mobilePhone .phone').intlTelInput(
          "setNumber",
          "+" + self.userData["Mobile Phone Country Code"]
        );
        self.mobileAreaCodeValue = self.userData["Mobile Phone Area Code"];
        self.mobilePhoneNumValue = self.userData["Mobile Phone Number"];

        $('#workPhone .phone').intlTelInput(
          "setNumber",
          "+" + self.userData["Work Phone Country Code"]
        );
        self.workAreaCodeValue = self.userData["Work Phone Area Code"];
        self.workPhoneNumValue = self.userData["Work Phone Number"];
      }, 300);
    },
    formValidate(form) {
      let missing_fields = 0;
      let _self = this;

      Array.from($(".ins-form-field, .ins-select-value-input, .ins-textarea-field")).forEach(
        function (_field) {
          if (
            _field.hasAttribute("required") === true ||
            _field.getAttribute("required") == "true"
          ) {
            missing_fields += _self.validateInput(_field);
          }
          if (_field.getAttribute("type") === "email") {
            missing_fields += _self.validateEmail(_field);
          }
        }
      );

      if (missing_fields > 0) return false;
      return true;
    },
    validateInput(_field) {    
      _field = _field.target ? _field.target: _field;  

      if (_field.value.trim() === "" || _field.value === null) {
        this.invalidFieldNotif(_field);
        return 1;
      } else {
        this.validFieldNotif(_field);
        return 0;
      }
    },
    blurValidateInput(_field) {      
      if (_field.value.trim() === "" || _field.value === null) {
        this.invalidFieldNotif(_field);
        return 1;
      } else {
        this.validFieldNotif(_field);
        return 0;
      }
    },
    // update material design to show error message
    invalidFieldNotif(_field) {
      _field.closest("ins-input").setAttribute("has-error", true);
    },
    // update material design to remove error message
    validFieldNotif(_field) {
      _field.closest("ins-input").removeAttribute("has-error");
    },
    // check if email is valid
    validateEmail(_field) {
      _field = _field.target ? _field.target: _field; 
      let re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      if(_field.value === "") {
        this.emailErrorMessage = "Email address is required.";
      } else {
          if (re.test(String(_field.value).toLowerCase()) == true) {
            this.validFieldNotif(_field);
            return 0;
          } else {
            this.emailErrorMessage = "Invalid email address."
            this.invalidFieldNotif(_field);
            return 1;
          }
      }
      
    },
    initMap() {
      setTimeout(() => {
        if (this.googleMapsApiKey.results.length){
          this.searchAddressField = new google.maps.places.Autocomplete(this.$refs.searchAddressField.$el.querySelector('input'));
          this.searchAddressField.addListener('place_changed', this.fillAddress);
        }
      }, 300);
    },
    fillAddress() {
      // Get the place details from the autocomplete object.
      let place = this.searchAddressField.getPlace();
      this.searchAddress = place.formatted_address;

      this.userData['Home Street'] = "";
      this.userData['Home Address'] = "";
      this.userData['Home City'] = "";
      this.userData['Home State'] = "";
      this.userData['Home Country'] = "";
      this.userData['Home Area Code'] = "";

      place.address_components.forEach(item => {
        let addressType = item.types[0];
        switch (addressType) {
          case "street_number":
            this.userData['Home Address'] = item['short_name'] + " ";
            break;
          case "route":
            this.userData['Home Address'] += item['long_name'];
            break;
          case "locality":
            this.userData['Home City'] = item['long_name'];
            break;
          case "colloquial_area":
            this.userData['Home City'] = item['long_name'];
            break;
          case "administrative_area_level_1":
            this.userData['Home State'] = item['short_name'];
            break;
          case "country":
            this.userData['Home Country'] = item['long_name'];
            break;
          case "postal_code":
            this.userData['Home Area Code'] = item['short_name'];
            break;
        }
      });
    },
    setFormSettings() {
      let self = this;

      if (this.formType === "add") {
        this.buttonLabel = "Create";
        this.formMethod = "POST";
        this.formURL = "/api/users";
        this.formName = "add_new_customer";
        this.userData = {};
        
        if (this.$store.state.CrmStore.addFromCompany){
          this.userData.company_id = this.$store.state.CrmStore.addFromCompany;
        }

      } else if (this.formType === "view") {

        this.buttonLabel = "Update";
        this.userData = this.$store.state.CrmStore.userData;
        this.formMethod, this.formURL, (this.formName = "");
        let form = document.getElementById("crmForm");
        let elements = form.elements;

        for (var i = 0, len = elements.length; i < len; ++i) {
          elements[i].readOnly = true;
          elements[i].disabled = true;
        }

        this.setPhoneValue();
      } else if (this.formType === "edit") {
        this.buttonLabel = "Update";
        this.userData = self.$store.state.CrmStore.userData;
        this.formMethod = "PUT";
        this.formURL = `${this.location}/api/users/${this.userData.id}`;
        this.formName = "crm_update_customer";

        this.setPhoneValue();
      } else if (this.formType === "remove") {
        this.userData = this.$store.state.CrmStore.userData;
      } else {
        this.$router.push("/customers");
      }
      this.showForm = true;
      this.initMap();
      this.oldContactType = this.userData['Contact Type'];
    },
    archiveContact(){
      swal({
        title: "",
        html: `
          <ins-loader
            state-icon="warning"
            icon-color="warning"
            state-message="Are you sure you want to archive these items?">
          </ins-loader>`,
        type: "",
        showCancelButton: true,
        confirmButtonText: "Archive"
      }).then(result => {
        if (result.value) {
          this.deleteContact();
        }
      });
    },
    deleteContact() {
      this.submittingForm = true;
      let url = `/api/users/${this.userData.id}`;      
      let body = JSON.stringify(this.formModel);

      this.$http
        .put(url, body, { headers: this.headers })
          .then(response => {
            toastIt("success", "Contact has been archived.", this.ops);
            this.$router.push('/customers');
          }).catch(error => {
            
            this.submittingForm = false;
            toastIt("error", "Failed to archive user. Please try again.", this.ops);
          });
    },
    setInsRendererLabel(){
      let insRendererEl = document.querySelector('ins-renderer');
      if (insRendererEl){
          
          if (this.formType === "add") {
            insRendererEl.updateRouteLabel('Add Contact');
          } else if (this.formType === "edit") {
            insRendererEl.updateRouteLabel('Edit Contact');
          }

      } else {
        setTimeout(() => {
          this.setInsRendererLabel();
        }, 300);
      }
    },
    fetchContactList(){
      this.submittingForm = true;
      if (this.userData["company_id"] && this.userData["Contact Type"] === 'Primary'){
        let url = `/api/get-all-company-contact-list-data.json?id=${this.userData["company_id"]}`;

        this.$http.get(url)
          .then(this.responseHandler)
          .catch(this.errorHandlerCompany);

      } else {
        this.bindValues();
      }
    },
    responseHandler(response){
      let contactList = response.data.items.results;
      // console.log("old Contact Type: ", this.oldContactType, this.userData["Contact Type"]);

      if (contactList.length){
        this.checkPrimaryContact(contactList)
      } else {
        this.bindValues();
      }
    },
    errorHandlerCompany(error){
      console.error('error', error);
      this.addMore = false;
      this.submittingForm = false;
      toastIt('error', 'Failed to validate company for Primary Contact', this.ops);
    },
    checkPrimaryContact(contactList){
      console.log("old: ", this.oldContactType, " ",this.userData["Contact Type"]);
      let result = contactList.find(contact => {
        return contact.customer_details.contact_type === "Primary";
      });

      if (result) {
        this.oldContactType === this.userData["Contact Type"]  
        ? this.bindValues() 
        : this.primaryContactHandler(result)
      } else {
        this.bindValues();
      }
    },
    primaryContactHandler(contact){
      let primaryContactName = `${contact.first_name} ${contact.last_name}`;
      let companyName = this.getCompanyName();
      let message = `<strong>${primaryContactName}</strong> is set as Primary Contact for <strong>${companyName}</strong>. Do you want to change this?`;

      swal({
        title: '', 
        html: message, 
        type: 'warning',
        showCancelButton: true
      }).then(choice => {

        if (choice.value){
          this.selectPrimaryType(primaryContactName, contact);
        } else {
          this.addMore = false;
          this.submittingForm = false;
        }
      });
    },
    selectPrimaryType(primaryContactName, contact){
        let swalMessage = `Please select a contact type for <strong>${primaryContactName}</strong>`;
        swal({
            title: '',
            html: swalMessage,
            // type: 'input',
            input: 'select',
            inputOptions: this.swalContactTypes,
            inputPlaceholder: 'Please select contact type',
            allowOutsideClick: false,
            allowEscapeKey: false,
            showCancelButton: true,
            confirmButtonText: 'Update'

        }).then((selected) => {
          
            if (selected.value){
              this.updatePrimaryContact(contact, selected.value);
            } else {
              this.addMore = false;
              this.submittingForm = false;
            }
        });
    },
    updatePrimaryContact(contact, selected){
      let url = `${this.location}/api/users/${contact.id}`;
      let formData = {
        form_configuration_name: 'crm_update_customer',
        form: {
          profiles_attributes: {
            customer_attributes: {
              properties_attributes: {
                contact_type: selected
              }
            }
          }
        }
      }
      let parsedFormData = JSON.stringify(formData)
      this.$http.put(url, parsedFormData, {headers: this.headers})
        .then(res => {
          toastIt("success", "Successfully updated current primary contact.", this.ops);
          this.bindValues();
        }).catch(error => {
          console.error('error', error);
          this.addMore = false;
          this.submittingForm = false;
          toastIt("error", "Failed to update current primary contact.", this.ops);
        });
    },
    getCompanyName(){
      let company = this.companyOptions.find(option => {
        return option.id === this.userData["company_id"]
      })

      return company.label
    }
  },
  mounted() {
    this.setInsRendererLabel();
    this.headers = JSON.parse(JSON.stringify(this.apiHeader));
    setTimeout(() => {
      this.$store.state.CrmStore.userData["Home Phone Country Code"] =
        this.$store.state.CrmStore.userData["Home Phone Country Code"] === "" ?
        "61" :
        this.$store.state.CrmStore.userData["Home Phone Country Code"];

      this.$store.state.CrmStore.userData["Mobile Phone Country Code"] =
        this.$store.state.CrmStore.userData["Mobile Phone Country Code"] === "" ?
        "61" :
        this.$store.state.CrmStore.userData["Mobile Phone Country Code"];

      this.$store.state.CrmStore.userData["Work Phone Country Code"] =
        this.$store.state.CrmStore.userData["Work Phone Country Code"] === "" ?
        "61" :
        this.$store.state.CrmStore.userData["Work Phone Country Code"];

      this.setFormSettings();
    }, 500);

    if (window.location.hostname !== "localhost") {
      this.authToken = JSON.parse(
        document.getElementById("formConfig").innerHTML
      );
    } else {
      this.authToken = "+Qj2j1eQpuy1fFxW6atnOluUyHLbL5cwDYL14rhASm13C+nkIPB6A5Ajxf/Cf1xFveFpEKMUd812HpUhyiBShQ==";
      this.location = "https://insites-poc.staging-sydney.near-me.com";
    }
  },
  beforeDestroy() {
    this.$store.commit('setAddFromCompany', false);
  }
}
</script>

