<template>
  <div>
    <div class="grid-container-full">
      <div class="grid-x grid-padding-x">
        <div class="cell large-12 medium-12 small-12">
          <form id="crmForm" action="#" data-abide data-live-validate="true"
            novalidate onsubmit="event.preventDefault()" enctype="multipart/form-data"
            v-if="showForm"> <!-- v-on:submit="bindValues" -->

            <ins-card steady>
              <div class="grid-x grid-padding-x ">
                <div class="cell large-12 medium-12 small-12">
                  <h4 class="ins-card-heading">Profile Picture</h4>
                  <InsImagePickerWrap name="form[custom_images_attributes][profile_images_attributes][image]"
                    type="picture" button-color="blue" v-model="userData['Profile Image']"
                    v-on:fileNameChanged="getFilename" label="UPLOAD LOGO">
                  </InsImagePickerWrap>
                </div>

              </div>
              <div class="grid-x grid-padding-x ">
                <div class="large-12 small-12 cell">
                  <h4 class="ins-card-heading">Contact Details</h4>
                </div>
                <div class="large-4 medium-6 small-12 cell">
                  <InsInputWrap id="userNameField" name="form[properties_attributes][company_name]"
                    placeholder="Company Name" label="Company Name*" field="text"
                    autocomplete="off" required="true" errorMessage="Company Name is required."
                    v-model="userData['Company']"
                    v-on:keyup.native="validateInput" />

                  <InsSelectWrap id="userAssignedToField" name="form[properties_attributes][company_type]"
                    label="Company Type" placeholder="- Select -" v-model="userData['Company Type']"
                    searchable>
                    
                    <ins-select-option v-if="!companyTypes.results.length"
                      label="No company types yet." value="" disabled>
                    </ins-select-option>

                    <ins-select-option v-for="companyType in companyTypes.results"
                      v-bind:key="companyType.id" v-bind:label="companyType.value"
                      v-bind:value="companyType.id">
                    </ins-select-option>
                  </InsSelectWrap>

                </div>
                <div class="large-4 medium-6 small-12 cell">
                  <InsSelectWrap v-if="!loadingCRMData" id="userAssignedToField"
                    name="form[properties_attributes][assigned_to]" label="Assigned To"
                    placeholder="- Select -" v-model="userData['Assigned To']"
                    searchable>

                    <ins-select-option v-if="!administratorList.results.length"
                      label="No administrators yet." value="" disabled>
                    </ins-select-option>

                    <ins-select-option v-for="contact in administratorList.results"
                      v-bind:key="contact.id" v-bind:label="contact.first_name + ' ' + contact.last_name"
                      v-bind:value="contact.id">
                    </ins-select-option>

                  </InsSelectWrap>

                  <InsSelectWrap id="userAssignedToField" name="form[properties_attributes][industry_type]"
                    label="Industry Type" placeholder="- Select -" v-model="userData['Industry Type']"
                    searchable>
                    
                    <ins-select-option v-if="!industryTypes.results.length"
                      label="No industry types yet." value="" disabled>
                    </ins-select-option>

                    <ins-select-option v-for="industryType in industryTypes.results"
                      v-bind:key="industryType.id" v-bind:label="industryType.value"
                      v-bind:value="industryType.id">
                    </ins-select-option>

                  </InsSelectWrap>
                </div>
                <div class="large-4 medium-6 small-12 cell">
                  <InsInputWrap id="userWebsite" name="form[properties_attributes][website]"
                    placeholder="Website" label="Website" field="text"
                    autocomplete="off" ref="userWebsite" v-model="userData['Website']" />

                  <ins-input-tel id="mobilePhone" label="Phone"
                    area-code="userDataMobileAreaCode" areacode-placeholder="Area Code"
                    phone-number="userDataMobilePhoneNumber"
                    phonenum-placeholder="Phone Number" ref="mobilePhone"
                    v-bind:phonenum-value="mobilePhoneNumValue"
                    v-bind:areacode-value="mobileAreaCodeValue" v-bind:readonly="formType === 'view'">
                  </ins-input-tel>
                </div>
              </div>
              <div class="grid-x grid-padding-x spacer-top-m">
                <div class="large-12 small-12 cell">
                  <h4 class="ins-card-heading">Address</h4>
                </div>
                <div class="large-4 medium-6 small-12 cell">
                  <InsInputWrap id="searchAddress" name="searchAddress"
                    label="Search Address" field="text" ref="searchAddressField" 
                    v-bind:placeholder="googleMapsApiKey.results.length ? 'Search Address':'No Google API Key'"
                    v-bind:disabled="!googleMapsApiKey.results.length" autocomplete="off"
                    v-model="searchAddress" />

                  <div class="grid-x grid-padding-x"> <!-- align-bottom -->
                    <InsInputWrap id="userHomeStreetField" name="form[custom_addresses_attributes][home_address_attributes][street]"
                      class="large-5 medium-5 small-5 cell" placeholder="Address 1"
                      label="Address 1" field="text" autocomplete="off" v-model="userData['Home Street']" />
                    <InsInputWrap id="userHomeAddressField" name="form[custom_addresses_attributes][home_address_attributes][address]"
                      class="large-7 medium-7 small-7 cell" placeholder="Address 2" label="Address 2"
                      field="text" autocomplete="off" v-model="userData['Home Address']" />
                  </div>
                </div>
                <div class="large-4 medium-6 small-12 cell">
                  <InsInputWrap id="userHomeCityField" name="form[custom_addresses_attributes][home_address_attributes][city]"
                    placeholder="City" label="City" field="text" autocomplete="off"
                    v-model="userData['Home City']" />
                  <InsInputWrap id="userHomeStateField" name="form[custom_addresses_attributes][home_address_attributes][state]"
                    placeholder="State" label="State" field="text" autocomplete="off"
                    v-model="userData['Home State']" />
                </div>
                <div class="large-4 medium-6 small-12 cell">
                  <InsInputWrap id="userHomeAreaCodeField" name="form[custom_addresses_attributes][home_address_attributes][postcode]"
                    class="large-5 medium-5 small-5 cell" placeholder="Post Code"
                    label="Post Code" field="text" autocomplete="off" v-model="userData['Home Area Code']" />
                  <InsInputWrap id="userHomeCountryField" name="form[custom_addresses_attributes][home_address_attributes][country]"
                    class="large-5 medium-5 small-5 cell" placeholder="Country"
                    label="Country" field="text" autocomplete="off" v-model="userData['Home Country']" />
                </div>
              </div>
              <div class="grid-x grid-padding-x spacer-top-m">
                <div class="large-12 small-12 cell">
                  <h4 class="ins-card-heading">Additional Info</h4>
                </div>
                <div class="large-4 medium-6 small-12 cell">
                  <InsInputWrap id="userFacebookField" name="form[properties_attributes][facebook_link]"
                    placeholder="Facebook" label="Facebook Profile Link" field="text"
                    autocomplete="off" v-model="userData['Facebook']" />
                  <InsInputWrap id="userTwitterField" name="form[properties_attributes][twitter_link]"
                    placeholder="Twitter" label="Twitter Profile Link" field="text"
                    autocomplete="off" v-model="userData['Twitter']" />
                  <InsInputWrap id="userYoutubeField" name="form[properties_attributes][youtube_link]"
                    placeholder="Youtube" label="YouTube Profile Link" field="text"
                    autocomplete="off" v-model="userData['Youtube']" />
                  <!-- <InsInputWrap id="userGoogleField" name="form[properties_attributes][google_plus_link]"
                    placeholder="Google+" label="Google+ Profile Link" field="text"
                    autocomplete="off" v-model="userData['Google']" /> -->
                </div>
                <div class="large-4 medium-6 small-12 cell">
                  <InsInputWrap id="userLinkedinField" name="form[properties_attributes][linkedin_link]"
                    placeholder="Linkedin" label="Linkedin Profile Link" field="text"
                    autocomplete="off" v-model="userData['Linkedin']" />
                  <InsInputWrap id="userInstagramField" name="form[properties_attributes][instagram_link]"
                    placeholder="Instagram" label="Instagram Profile Link"
                    field="text" autocomplete="off" v-model="userData['Instagram']" />
                </div>
                <div class="large-4 medium-6 small-12 cell">
                  <InsTextareaWrap id="userNoteField" name="userNoteField"
                    placeholder="Notes" label="Notes" autocomplete="off"
                    maxlength="255"
                    v-model="userData['Notes']" />
                </div>
              </div>
            </ins-card>
            <div class="grid-x grid-padding-x grid-padding-y">
              
              <div class="cell large-4 medium-3 small-12" v-if="formType == 'edit'">
                <ins-button label="ARCHIVE COMPANY" color="negative" type="button"
                  v-on:onClickInsButton="archiveCompany">
                </ins-button>
              </div>

              <div v-bind:class="`${formType === 'edit' ? 'large-8':'large-12'} medium-9 small-12 cell text-right`">
                <ins-button type="button" label="Cancel" color="blue" outlined="true"
                  size="normal" v-on:onClickInsButton="backToList" v-bind:disabled="submittingForm"></ins-button>
                <ins-button type="submit" color="blue" solid="true" size="normal"
                  v-bind:label="buttonLabel" v-bind:disabled="submittingForm" v-on:onClickInsButton="bindValues"></ins-button>
                <ins-button type="button" label="Save and Create Another" color="blue"
                  solid="true" size="normal" v-on:onClickInsButton="saveAddMore"
                  v-bind:disabled="submittingForm" v-if="formType === 'add'"></ins-button>
              </div>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import moment from "moment";
import { each } from "lodash";
import { mapState } from 'vuex';

import InsInputWrap from "../components/InsInputWrap.vue";
import InsSelectWrap from "../components/InsSelectWrap.vue";
import InsImagePickerWrap from "../components/InsImagePickerWrap.vue";
import InsTextareaWrap from "../components/InsTextareaWrap.vue";

export default {
  name: "EditCompanyDetails",
  components: {
    InsInputWrap,
    InsSelectWrap,
    InsImagePickerWrap,
    InsTextareaWrap
  },
  computed: mapState({
    companyTypes: state => state.SystemFieldsStore.company_type,
    industryTypes: state => state.SystemFieldsStore.industry_type,
    administratorList: state => state.SystemFieldsStore.administratorList,
    googleMapsApiKey: state => state.SystemFieldsStore.google_maps_api_key
  }),
  data() {
    return {
      showForm: false,
      submittingForm: false,
      userData: {},
      userProfile: this.$store.state.CrmStore.userProfile,
      formType: this.$store.state.CrmStore.formType,
      formMethod: "",
      formName: "",
      fromURL: "/api/customizations",
      searchAddress: "",
      searchAddressField: undefined,
      parentResourceId: "company",
      profilePictureName: "",
      authToken: "",
      location: "",
      userCountryCode: "au",
      buttonLabel: "Add Contact",
      addMore: false,
      toastMsg: "",
      sourceProfile: "/api/get-current-user-data.json",
      workAreaCodeValue: "",
      workPhoneNumValue: "",
      mobileAreaCodeValue: "",
      mobilePhoneNumValue: "",
      homeAreaCodeValue: "",
      homePhoneNumValue: "",
      loadingCRMData: false,
      headers: {},
      sourceCRMDetails: "/api/get-company-contact-list-data.json?id=",
      contactModel: {
        form_configuration_name: "crm_delete_customer",
        form: {
          profiles_attributes: {
            customer_attributes: {
              properties_attributes: {
                is_enabled: "false"
              }
            }
          }
        }
      },
      companyModel: {
        form_configuration_name: "crm_delete_company",
        parent_resource_id: "company",
        form: {
          properties: {
            is_enabled: "false"
          }
        }
      }
    }
  },
  methods: {
    getFilename(event) {
      this.profilePictureName = event;
    },
    base64ToBlob(base64, mime) {
      mime = mime || '';
      let sliceSize = 1024;
      let byteChars = window.atob(base64);
      let byteArrays = [];

      for (let offset = 0, len = byteChars.length; offset < len; offset += sliceSize) {
        let slice = byteChars.slice(offset, offset + sliceSize);

        let byteNumbers = new Array(slice.length);
        for (let i = 0; i < slice.length; i++) {
          byteNumbers[i] = slice.charCodeAt(i);
        }

        let byteArray = new Uint8Array(byteNumbers);

        byteArrays.push(byteArray);
      }

      return new Blob(byteArrays, {
        type: mime
      });
    },
    appendToFormData(data) {
      let formData = new FormData();

      each(data, (val, key) => {
        formData.append(key, val);
      });
      return formData;
    },
    backToList() {
      this.$router.push("/customers");
    },
    saveAddMore() {
      this.addMore = true;
      this.bindValues();
    },
    attachFile() {
      this.$refs.profilePic.click();
    },
    bindValues() {
      this.submittingForm = true;
      let mobileCountryCode = this.$refs.mobilePhone.getCountryData();
      mobileCountryCode = mobileCountryCode.dialCode;

      let formData = {
        "authenticity_token": this.authToken,
        "form_configuration_name": this.formName,
        "parent_resource_id": this.parentResourceId,
        "form[properties_attributes][company_name]": this.userData["Company"],
        "form[properties_attributes][is_enabled]": "true",
        "form[properties_attributes][website]": this.userData["Website"],
        "form[properties_attributes][industry_type]": this.userData['Industry Type'],
        "form[properties_attributes][company_type]": this.userData['Company Type'],
        "form[properties_attributes][assigned_to]": this.userData['Assigned To'],
        "form[properties_attributes][notes]": this.userData['Notes'],
        "form[custom_addresses_attributes][location_1_attributes][street]": this.userData["Home Street"],
        "form[custom_addresses_attributes][location_1_attributes][address]": this.userData["Home Address"],
        "form[custom_addresses_attributes][location_1_attributes][city]": this.userData["Home City"],
        "form[custom_addresses_attributes][location_1_attributes][state]": this.userData["Home State"],
        "form[custom_addresses_attributes][location_1_attributes][postcode]": this.userData["Home Area Code"],
        "form[custom_addresses_attributes][location_1_attributes][country]": this.userData["Home Country"],
        "form[properties_attributes][mobile_phone_country_code]": mobileCountryCode,
        "form[properties_attributes][mobile_phone_area_code]": document.getElementsByName('userDataMobileAreaCode')[0].value,
        "form[properties_attributes][mobile_phone_number]": document.getElementsByName('userDataMobilePhoneNumber')[0].value,
        "form[properties_attributes][facebook_link]": this.userData["Facebook"],
        "form[properties_attributes][twitter_link]": this.userData["Twitter"],
        "form[properties_attributes][youtube_link]": this.userData["Youtube"],
        "form[properties_attributes][linkedin_link]": this.userData["Linkedin"],
        "form[properties_attributes][google_plus_link]": "", // this.userData["Google"]
        "form[properties_attributes][instagram_link]": this.userData["Instagram"],
        "form[properties_attributes][notes]": this.userData["Notes"]
      }

      let parsedFormData = this.appendToFormData(formData);
      let profile_img = this.userData['Profile Image'] ? this.userData['Profile Image'] : "";

      if (profile_img !== "" || profile_img !== undefined) {
        let base64ImageContent = profile_img.replace(/^data:image\/(png|jpg|jpeg);base64,/, "");
        let img_type = profile_img.match(/^data:image\/(png|jpg|jpeg);base64,/);

        if (img_type) {
          let blob = this.base64ToBlob(base64ImageContent, "image/" + img_type[1]);
          parsedFormData.append("form[custom_images_attributes][profile_image_attributes][image]", blob, this.profilePictureName);
        }
      }
      
      let form = document.getElementById("crmForm");
      if (this.formType === "add") {
        this.formURL = `/api/customizations`;
      }

      this.toastMsg = "Company";
      if (this.formValidate(form)) {
        this.submitForm(parsedFormData);
      } else {
        this.addMore = false;
        this.submittingForm = false;
      }
    },
    submitForm(data) {
      let self = this;
      let options = {
        type: this.formMethod,
        contentType: false,
        processData: false,
        data: data,
        success: response => {
          self.successHandler();
        },
        error: error => {
          self.errorHandler(error);
        }
      };
      $.ajax(this.formURL, options);
    },
    successHandler() {
      toastIt(
        "success",
        `${this.toastMsg} has been ${this.formType === "add" ? "created" : "updated"}.`,
        this.ops
      );
      document.getElementById("crmForm").reset();
      this.userData["Name"] = "";
      this.userData["Company"] = "";
      !this.addMore ? this.$router.push("/customers") : "";
      this.addMore = false;
      this.submittingForm = false;
    },
    errorHandler(error) {
      this.addMore = false;
      this.submittingForm = false;
      let errorMsg = "";
      if (error.responseJSON){
        if (error.responseJSON.errors) {
          if (
            error.responseJSON.errors.email !== "undefined" ||
            error.responseJSON.errors.email !== null
          ) {
            errorMsg += error.responseJSON.errors.email ?
              "- Email " + error.responseJSON.errors.email[0] + "<br />" :
              "";
          }
          if (
            error.responseJSON.errors.password !== "undefined" ||
            error.responseJSON.errors.password !== null
          ) {
            errorMsg += error.responseJSON.errors.password ?
              "- Password " + error.responseJSON.errors.password[0] :
              "";
          }
        }
      }
      toastIt("error", errorMsg, this.ops);
    },
    setPhoneValue() {
      let self = this;
      let mobilePhone = self.$refs.mobilePhone;

      setTimeout(() => {
        $('#mobilePhone .phone').intlTelInput(
          "setNumber",
          "+" + self.userData["Company Mobile Phone Country Code"]
        );
        self.mobileAreaCodeValue = self.userData["Company Mobile Phone Area Code"];
        self.mobilePhoneNumValue = self.userData["Company Mobile Phone Number"];
      }, 300);
    },
    formValidate(form) {
      let missing_fields = 0;
      let _self = this;

      Array.from($(".ins-form-field, .ins-select-value-input, .ins-textarea-field")).forEach(
        function (_field) {
          if (
            _field.hasAttribute("required") === true ||
            _field.getAttribute("required") == "true"
          ) {
            missing_fields += _self.validateInput(_field);
          }
          if (_field.getAttribute("type") === "email") {
            missing_fields += _self.validateEmail(_field);
          }
        }
      );

      if (missing_fields > 0) return false;
      return true;
    },
    // validate input value
    validateInput(_field) {
      _field = _field.target ? _field.target: _field; 
      
      if (_field.value.trim() === "" || _field.value === null) {
        this.invalidFieldNotif(_field);
        return 1;
      } else {
        this.validFieldNotif(_field);
        return 0;
      }
    },
    // update material design to show error message
    invalidFieldNotif(_field) {
      _field.closest("ins-input").setAttribute("has-error", true);
    },
    // update material design to remove error message
    validFieldNotif(_field) {
      _field.closest("ins-input").removeAttribute("has-error");
    },
    // check if email is valid
    validateEmail(_field) {
      let re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

      if (re.test(String(_field.value).toLowerCase()) == true) {
        this.validFieldNotif(_field);
        return 0;
      } else {
        this.invalidFieldNotif(_field);
        return 1;
      }
    },
    initMap() {
      setTimeout(() => {
        if (this.googleMapsApiKey.results.length){
          this.searchAddressField = new google.maps.places.Autocomplete(this.$refs.searchAddressField.$el.querySelector('input'));
          this.searchAddressField.addListener('place_changed', this.fillAddress);
        }
      }, 300);
    },
    fillAddress() {
      // Get the place details from the autocomplete object.
      let place = this.searchAddressField.getPlace();
      this.searchAddress = place.formatted_address;
      this.userData['Home Street'] = "";
      this.userData['Home Address'] = "";
      this.userData['Home City'] = "";
      this.userData['Home State'] = "";
      this.userData['Home Country'] = "";
      this.userData['Home Area Code'] = "";

      place.address_components.forEach(item => {
        let addressType = item.types[0];
        switch (addressType) {
          case "street_number":
            this.userData['Home Address'] = item['short_name'] + " ";
            break;
          case "route":
            this.userData['Home Address'] += item['long_name'];
            break;
          case "locality":
            this.userData['Home City'] = item['long_name'];
            break;
          case "colloquial_area":
            this.userData['Home City'] = item['long_name'];
            break;
          case "administrative_area_level_1":
            this.userData['Home State'] = item['short_name'];
            break;
          case "country":
            this.userData['Home Country'] = item['long_name'];
            break;
          case "postal_code":
            this.userData['Home Area Code'] = item['short_name'];
            break;
        }
      });
    },
    setFormSettings() {
      let self = this;

      if (this.formType === "add") {

        this.buttonLabel = "Create";
        this.formMethod = "POST";
        this.formURL = "/api/customizations";
        this.parentResourceId = "company";
        this.formName = "add_new_company";
        this.userData = {};

      } else if (this.formType === "view") {

        this.buttonLabel = "Update";
        this.userData = this.$store.state.CrmStore.userData;
        this.formMethod = "", this.formURL = "", this.formName = "";
        let form = document.getElementById("crmForm");
        let elements = form.elements;

        for (var i = 0, len = elements.length; i < len; ++i) {
          elements[i].readOnly = true;
          elements[i].disabled = true;
        }

        this.setPhoneValue();
      } else if (this.formType === "edit") {
        this.buttonLabel = "Update";
        this.userData = self.$store.state.CrmStore.userData;
        this.formMethod = "PUT";
        this.formURL = `/api/customizations/${this.userData.id}`;
        this.parentResourceId = "company";
        this.formName = "crm_update_company";

        this.setPhoneValue();
      } else if (this.formType === "remove") {
        this.userData = this.$store.state.CrmStore.userData;
      } else {
        this.$router.push("/customers");
      }

      this.showForm = true;
      this.initMap();
    },
    archiveCompany() {
      let deleteMsg = 'Are you sure you want to archive this company? This will also archive related contacts.';
      swal({
        title: "",
        html: `
          <ins-loader
            state-icon="warning"
            icon-color="warning"
            state-message="${deleteMsg}">
          </ins-loader>`,
        type: "",
        showCancelButton: true,
        confirmButtonText: "Archive"
      }).then(result => {
        if (result.value) {
          this.getAllContacts();
        }
      });
    },
    getAllContacts() {
      let url = `${this.sourceCRMDetails}${this.userData.id}&sortBy=updated_at&orderBy=desc`;
      this.$http.get(url)
        .then(response => {
          if (response.data.items.results.length){
            this.deleteAllContacts(response.data.items.results);
          } else {
            this.deleteCompany();
          }
        }).catch(this.errorHandler);
    },
    deleteAllContacts(contacts){
      let go = true;
      let counter = 0;
      contacts.forEach(contact => {
        if (go) {
          let url = `/api/users/${contact.id}`;      
          let body = JSON.stringify(this.contactModel);
          this.$http.put(url, body, { headers: this.headers })
            .then(response => {
              counter++;
              if (counter === contacts.length){
                this.deleteCompany();
              }
            }).catch(error => {
              this.errorHandler(error);
              go = false;
            });
        }
      });
    },
    deleteCompany() {
      let url = `/api/customizations/${this.userData.id}`;      
      let body = JSON.stringify(this.companyModel);
      this.$http.put(url, body, { headers: this.headers })
        .then(response => {
          toastIt("success", "Successfully archived company.", this.ops);
          this.$store.commit('removeCompanyOption', this.userData.id);
          this.$router.push('/customers');
        }).catch(this.errorHandler);
    },
    setInsRendererLabel(){
      let insRendererEl = document.querySelector('ins-renderer');
      if (insRendererEl){
          
          if (this.formType === "add") {
            insRendererEl.updateRouteLabel('Add Company');
          } else if (this.formType === "edit") {
            insRendererEl.updateRouteLabel('Edit Company');
          }

      } else {
        setTimeout(() => {
          this.setInsRendererLabel();
        }, 300);
      }
    }
  },
  mounted() {
    this.setInsRendererLabel();
    this.headers = JSON.parse(JSON.stringify(this.apiHeader));
    setTimeout(() => {
      this.$store.state.CrmStore.userData["Company Mobile Phone Country Code"] =
        this.$store.state.CrmStore.userData["Company Mobile Phone Country Code"] === "" ?
        "61" :
        this.$store.state.CrmStore.userData["Company Mobile Phone Country Code"];

      this.setFormSettings();
    }, 500);

    if (window.location.hostname !== "localhost") {
      this.authToken = JSON.parse(
        document.getElementById("formConfig").innerHTML
      );
    } else {
      this.authToken = "+Qj2j1eQpuy1fFxW6atnOluUyHLbL5cwDYL14rhASm13C+nkIPB6A5Ajxf/Cf1xFveFpEKMUd812HpUhyiBShQ==";
      this.location = "";
    }
  }
}

</script>

