import Vue from 'vue'
import Router from 'vue-router'
import Overview from './views/Overview.vue'
import ListUsers from './views/ListUsers.vue'
import ViewUserDetails from './views/ViewUserDetails.vue'
import MyProfile from './views/MyProfile.vue'
import ListCRM from './views/ListCRM.vue'
import ManageCRM from './views/EditCRMDetails.vue'
import ManageCompany from './views/EditCompanyDetails.vue'
import Cases from './views/ListCases.vue'
import CaseDetails from './views/CaseDetails.vue'
import ViewCompany from './views/ViewCompanyDetails.vue'
import ViewContact from './views/ViewContactDetails.vue'
import SystemFields from './views/SystemFields.vue'
import ApiKeys from './views/ApiKeys.vue'

Vue.use(Router)

export default new Router({
  routes: [   
    {
      path: '/overview',
      name: 'overview',
      component: Overview
    },
    {
      path: '/admin-users',
      name: 'adminUsers',
      component: ListUsers
    },
    {
      path: '/manage-admin-user',
      name: 'manageAdminUser',
      component: ViewUserDetails
    },
    {
      path: '/my-profile',
      name: 'myProfile',
      component: MyProfile
    },
    {
      path: '/customers',
      name: 'Customers',
      component: ListCRM
    },
    {
      path: '/manage-crm',
      name: 'manageCRM',
      component: ManageCRM
    },
    {
      path: '/manage-company',
      name: 'manageCompany',
      component: ManageCompany
    },
    {
      path: '/cases',
      name: 'Cases',
      component: Cases
    },
    {
      path: '/case-details',
      name: 'CaseDetails',
      component: CaseDetails
    },
    {
      path: '/view-company',
      name: 'ViewCompany',
      component: ViewCompany
    },
    {
      path: '/view-contact',
      name: 'ViewContact',
      component: ViewContact
    },
    {
      path: '/system-fields',
      name: 'SystemFields',
      component: SystemFields
    },
    {
      path: '/api-keys',
      name: 'ApiKeys',
      component: ApiKeys
    }
  ]
})
