<template>
    <div class="grid-container-full" id="caseDetails">
        <div class="grid-x grid-padding-x">
            <div class="cell large-6 medium-12 small-12 margin-bottom-30">
                <ins-card steady>
                    <div class="grid-x grid-padding-x">
                        <div class="cell large-12 medium-12 small-12">
                            <h4>Case Details</h4>
                            <table class="information-wrap">
                                <tr>
                                    <td class="td-label">Case ID</td>
                                    <td>{{ checkValue(caseDetails['id']) }}</td>
                                </tr>
                                <tr>
                                    <td class="td-label">Date Created</td>
                                    <td>{{ checkValue(caseDate) }}</td>
                                </tr>
                                <tr>
                                    <td class="td-label">Form Title</td>
                                    <td>{{ checkValue(capitalize(caseDetails.raw.case_label)) }}</td>
                                </tr>
                                <tr>
                                    <td class="td-label">Work Flow</td>
                                    <td>-</td>
                                </tr>                               
                            </table>
                           <InsSelectWrap id="userAssignedToField" name="userAssignedToField"
                                label="Assigned To" placeholder="- Select -"
                                dynamic-placeholder="Please enter option" v-model="caseAssignedTo"
                                searchable>

                                <ins-select-option v-for="item in administratorList.results"
                                v-bind:key="item.id" v-bind:label="`${checkValue(item.first_name)} ${checkValue(item.last_name)}`"
                                v-bind:value="item.id">
                                </ins-select-option>
                            </InsSelectWrap>
                            <InsSelectWrap id="caseStatus" name="caseStatus"
                                label="Status" placeholder="- Select -" v-model="caseStatus">
                                <ins-select-option label="New" value="new"></ins-select-option>
                                <ins-select-option label="Flagged" value="flagged"></ins-select-option>
                                <ins-select-option label="Open" value="open"></ins-select-option>
                                <ins-select-option label="Closed" value="closed"></ins-select-option>
                            </InsSelectWrap>
                        </div>
                    </div>
                </ins-card>

                <div class="grid-x grid-padding-y">
                    <div class="cell large-6 medium-6 small-12">
                        <ins-button label="Delete Case" type="button"
                          color="negative" v-on:onClickInsButton="deleteItem">
                        </ins-button>
                    </div>
                    <div class="cell large-6 medium-6 small-12 text-right">
                        <ins-button label="Update Details" type="button"
                        color="solid" v-on:onClickInsButton="updateItem">
                        </ins-button>
                    </div>
                </div>
            </div>
            <div class="cell large-6 medium-12 small-12 margin-bottom-30">
                <ins-card steady>
                     <div class="grid-x grid-padding-x">
                        <div class="cell large-12 medium-12 small-12">
                            <h4>Form Submission</h4>
                            <table class="information-wrap">
                                <tr v-for="item in caseSubmissionData"
                                    v-bind:key="item.case_id"
                                    v-if="item.label !== 'case_id'">                                    
                                    <td class="td-label">{{ item.label }}</td>
                                    <td v-if="item.type === 'text'">{{ item.value }}</td>
                                    <td v-if="item.type === 'email'"><a v-bind:href="`mailto:${item.value}`">{{ item.value }}</a></td>
                                </tr>                            
                            </table>
                        </div>
                    </div>
                </ins-card>
            </div>           
        </div>
    </div>
</template>

<script>
import moment from "moment";
import { isEmpty } from 'lodash';
import { mapState } from 'vuex';
import { each } from "lodash";
import InsSelectWrap from "../components/InsSelectWrap.vue";

export default {
    name: "CaseDetails",
    components: {
        InsSelectWrap
    },
    data() {
        return {
            administratorSource: "/api/get-all-administrator-list-data.json",
            adminList: [],
            sourceCaseData: "/api/get-case-data.json?id=",
            caseDetails: {},
            caseDate: "",
            caseAssignedTo: "",
            caseStatus: "new",
            caseForm: [],
            caseSubmissionData: [],
            headers: {},
            newCase: true,
            rawFormData: {
              utf8: "" ,
              form_configuration_name: "delete_case_customization",
              authenticity_token: "",
              resource_id: "new",
              parent_resource_id: "",
              parent_resource_class: "CustomModelType",
              _method: "DELETE",
              customization_id: ""
            },
            addCaseFormData: {
                form_configuration_name: "add_case_details",
                parent_resource_id: "cases",
                form: {
                    properties: {
                        assigned_to: "",
                        case_id: "",
                        status: ""
                    }
                }
            }
        }
    },
    computed: mapState({
        caseData: state => state.CaseStore.caseData,
        administratorList: state => state.SystemFieldsStore.administratorList
    }),
    methods: {
        setCaseData() {
            this.caseDetails = this.caseData;
            this.caseAssignedTo = this.caseDetails.caseData ? this.caseDetails.caseData.assigned_to : "";
            this.caseStatus = this.caseDetails.caseData ? this.caseDetails.caseData.status : "new";

            // console.log("caseStatus ", this.caseStatus, " assigned to ", this.caseAssignedTo, this.caseDetails);
            // check if caseDetails empty, redirect back to case list
            isEmpty(this.caseDetails)
            ? this.$router.push("/cases") 
            : this.bindCase();
        },            
        bindCase() {
            this.caseDate = moment(this.caseDetails.raw.created_at).format('DD-MMM-YYYY hh:mm A ZZ'); 
            // this.caseSubmissionData         
            let formData = this.caseDetails.raw.case_data; 
            let self = this;  

            for (let key in formData) {

                let type = key.toLowerCase().indexOf('email') >= 0 ? "email" : "text";
                let tempObj = {
                    label : this.capitalize(key),
                    value : this.checkValue(formData[key]),
                    type: type
                }               
                this.caseSubmissionData.push(tempObj);
            }
            this.caseSubmissionData.sort(this.dynamicKeySort("label", "asc"));
          
        },
        checkValue(value, withPlus) {
            return value && value !== 'undefined' && value !== null ? `${withPlus ? '+':''}${value}` : '';
        },
        capitalize(item) {
            return this.checkValue(item).replace(/_/g," ").split(' ').map((s) => s.charAt(0).toUpperCase() + s.substring(1)).join(' ');
        },
        dynamicKeySort(key, order) {
            let OrderSortIndex = order === 'asc' ? 1 : -1;

            return function (a, b) {
                let num = (a[key] < b[key]) ? -1 : (a[key] > b[key]) ? 1 : 0;
                return num * OrderSortIndex;
            }
        },       
        deleteItem() {
            let deleteMsg = "Are you sure you want to delete this case?"
            let self = this; 
            swal({
                title: "",
                html: `
                <ins-loader
                    state-icon="warning"
                    icon-color="warning"
                    state-message="${deleteMsg}">
                </ins-loader>`,
                type: "",
                showCancelButton: true,
                confirmButtonText: "Delete"
            }).then(result => {
                if (result.value) {          
                  this.continueDeletion = true;
                  this.deleteCase(self.caseDetails);               
                }
            });
        },
        deleteCase(item) {
          let url = `/api/customizations/${item.id}`
          // let body = JSON.stringify(this.formModel);
          this.rawFormData.customization_id = item.id;
          this.rawFormData.parent_resource_id = item.raw.case_type;
          let parsedFormData = this.appendToFormData(this.rawFormData); 

          this.$http.post(url, parsedFormData) //{ headers: this.headers}
              .then(res => { 
                  parent.toastIt("success", "Case has been successfully deleted.", this.ops); 
                  this.$router.push("/cases");
              }).catch(error => {
                  console.error('error', error);
                  parent.toastIt("error", "Failed to delete case.", this.ops);
              });
        
        },
        appendToFormData(data) {
          let formData = new FormData();

          each (data, (val, key) => {
            formData.append(key, val);
          });
          return formData;
        },
        async updateItem() {
            let url = "";
            let caseInfo = [];
            let formType = "post";
            this.addCaseFormData.form.properties.assigned_to = this.caseAssignedTo;
            this.addCaseFormData.form.properties.case_id = this.caseDetails.id;
            this.addCaseFormData.form.properties.status = this.caseStatus;
            let parsedFormData = JSON.stringify(this.addCaseFormData);
            
            caseInfo = await this.getCaseData(this.caseDetails.id);
            // console.log('case id: ', this.caseDetails.id, ' check case data: ', caseInfo, this.newCase, caseInfo.length);

            if (this.newCase && !caseInfo.length) {
                url = `/api/customizations`;
                formType = "post";
            } else {           
                url = `/api/customizations/${caseInfo[0].id}`;
                formType = "put";
            }

            this.$http[formType](url, parsedFormData, { headers: this.headers}).then(res => { 
                  parent.toastIt("success", "Case has been updated.", this.ops); 
                 // this.$router.push("/cases");
              }).catch(error => {
                  // console.log('error', error);
                  parent.toastIt("error", "Failed to update case.", this.ops);
              });
        },
        getCaseData(id) {
            let url = `${this.sourceCaseData}${id}`
            let self = this;
            return this.$http
                .get(url)
                .then(response => {
                    let caseInfo = response.data.items.results;
                        if (caseInfo.length) {
                            self.newCase = false;
                            return caseInfo
                        } else {
                            self.newCase = true;
                            return [];
                        }
                    })
                .catch(error => {
                    console.error("Error: " + error);
                    this.errorHandler();
                    return [];
                });
        },
        setInsRendererLabel(){
          let insRendererEl = document.querySelector('ins-renderer');
          if (insRendererEl.updateRouteLabel){
            insRendererEl.updateRouteLabel('Case Details');
          } else {
            setTimeout(() => {
              this.setInsRendererLabel();
            }, 300);
          }
        }
      },
      mounted() {
        this.setInsRendererLabel();
        this.setCaseData();
      },
    created() {
        // this.getAdministrators();
        this.headers = JSON.parse(JSON.stringify(this.apiHeader));
        this.rawFormData.authenticity_token = this.headers["X-CSRF-Token"];
    }
}
</script>

