<template>
<div class="grid-container-full">
  <div class="grid-x grid-padding-x">
    <div class="large-12 small-12 medium-12 cell" style="margin-bottom:30px">
      <ins-filter id="boom" with-date-filter date-title="Date Period">
        <ins-filter-item name="Status" options='["All Status", "Pending", "Completed", "Declined"]'>
        </ins-filter-item>
      </ins-filter>
    </div>

    <div class="large-3 medium-6 small-12 cell margin-bottom-30">
      <ins-card>
        <ins-sparkline data-src="https://8podbzvdw2.execute-api.us-east-2.amazonaws.com/insites-dummy-sparkline-deployment/insites-dummy-get-sparkline"></ins-sparkline>
      </ins-card>
    </div>

    <div class="large-3 medium-6 small-12 cell margin-bottom-30">
      <ins-card>
        <ins-sparkline id="users"
          ref="Users"
          icon="icon-user-1" 
          name="Users" 
          value="720" 
          percentage="7" 
          description="From previous year" 
          movement="decrease">
        </ins-sparkline>
      </ins-card>
    </div>

    <div class="large-3 medium-6 small-12 cell margin-bottom-30">
      <ins-card>
        <ins-sparkline id="bounceRate" 
          ref="BounceRate"
          icon="icon-analytic-1" 
          name="Bounce Rate" 
          value="86%" 
          percentage="12" 
          description="From previous year"
          movement="increase">
        </ins-sparkline>
      </ins-card>
    </div>

    <div class="large-3 medium-6 small-12 cell margin-bottom-30">
      <ins-card>
        <ins-sparkline id="goals"
          ref="Goals"
          icon="icon-goal-1"
          name="Goals"
          value="2.5K"
          percentage="7"
          description="From previous year"
          movement="decrease">
        </ins-sparkline>
      </ins-card>
    </div>

    <div class="large-12 medium-12 small-12 cell margin-bottom-30">
      <ins-card>
        <ins-line-chart id="lineChart" name="Sessions" data-src="https://8yu847n8rb.execute-api.us-east-2.amazonaws.com/insites-linegraph2-deployment/insites-dummy-linegraph2"></ins-line-chart>
      </ins-card>
    </div>

    <div class="large-12 medium-12 small-12 cell margin-bottom-30">
      <!-- <ins-card steady no-padding>
        <ins-table data-src="https://ag64gmtsc0.execute-api.us-east-2.amazonaws.com/insites-dummy-deployment/insites-dummy-get-table" table-title="Table"></ins-table>
      </ins-card> -->
      <ins-base-table
        id="insBaseTable"
        searchbar-placeholder="Search Default Column...">
      </ins-base-table>
    </div>

    <div class="large-12 medium-12 small-12 cell margin-bottom-30">
      <ins-card>
          <ins-bar-chart id="barChart" name="Column Chart" data-src="https://9p1pa4b3fa.execute-api.us-east-2.amazonaws.com/insites-barchart-deployment/insites-dummy-barchart"></ins-bar-chart>
      </ins-card>
    </div>
  </div>
</div>
</template>

<script>
import InsInputWrap from '../components/InsInputWrap'
export default {
  name: 'Overview',
  components: { InsInputWrap },
  data(){
    return {
      sampleModel: '',
      sparkLineSampleData: [["2017-12-18", 13], ["2017-12-25", 0], ["2018-01-01", 0], ["2018-01-08", 8], ["2018-01-15", 5], ["2018-01-22", 2], ["2018-01-29", 1], ["2018-02-05", 1], ["2018-02-12", 1], ["2018-02-19", 0], ["2018-02-26", 1], ["2018-03-05", 0], ["2018-03-12", 0], ["2018-03-19", 2], ["2018-03-26", 1], ["2018-04-02", 11], ["2018-04-09", 2], ["2018-04-16", 2], ["2018-04-23", 0], ["2018-04-30", 0], ["2018-05-07", 0], ["2018-05-14", 0], ["2018-05-21", 1], ["2018-05-28", 0]]
    }
  },

  mounted(){

    setTimeout(() => {
      this.$refs.Users.chartData = this.sparkLineSampleData;
      this.$refs.BounceRate.chartData = this.sparkLineSampleData;
      this.$refs.Goals.chartData = this.sparkLineSampleData;
    }, 1000);

    var insBaseTable = document.getElementById('insBaseTable');
      var rawData = [];
      var filteredData = [];
      var displayedData = [];
      var tableHeaders = [];
      var totalCount = 0;
      var pageNumber = 1;
      var pageSize = 10;
      var searchKeyword = '';
      var sortedKeyword = '';

      insBaseTable.loadingScreen = true;
      insBaseTable.bulkActions = ['Archive'];
      insBaseTable.rowActions = ['Archive'];
      insBaseTable.addEventListener('onPaginationChange', tablePaginationHandler);
      insBaseTable.addEventListener('onTableSearch', tableSearchHandler);
      insBaseTable.addEventListener('onTableSort', tableSortHandler);
      insBaseTable.addEventListener('onTableBulkAction', tableBulkActionHandler);
      insBaseTable.addEventListener('onTableRowAction', tableBulkRowHandler);


        // firefox purposes
      $('ins-filter-item').click(function () {
        var filterOption = $('.filter-item__options');
        filterOption.toggleClass('active');
      });  
      
      function tablePaginationHandler(event){
        insBaseTable.loadingScreen = true;
        pageSize = event.detail.pageSize;
        pageNumber = event.detail.pageNumber;
        processTableData()
      }
      function tableSearchHandler(event){
        insBaseTable.loadingScreen = true;
        searchKeyword = event.detail.value;

        if (!searchKeyword) {
          processTableData();
          return;
        }

        filteredData = [];
        rawData.forEach((value) => {
          Object.keys(value).forEach((key) => {
            if (key === 'Default Column'){
              var match = value[key];
              if (match.toLowerCase().includes(searchKeyword.toLowerCase())) {
                filteredData.push(value);
              }
            }
          });
        });

        if (sortedKeyword && filteredData.length > 1) {
          filteredData.sort(dynamicKeySort(sortedKeyword));
        }
        insBaseTable.pageNumber = 1;
        processTableData();

        if (!filteredData.length) {
          insBaseTable.loadingScreen = true;
          insBaseTable.loaderTitle = '';
          insBaseTable.loaderMessage = 'No result found for "' + searchKeyword + '"';
          insBaseTable.loaderIcon = '';
        }
      }
      function tableSortHandler(event){
        sortedKeyword = event.detail.order === 'asc' ? event.detail.keyword : `-${event.detail.keyword}`;
        if (searchKeyword){
          filteredData.sort(dynamicKeySort(sortedKeyword));
        } else {
          rawData.sort(dynamicKeySort(sortedKeyword));
        }
        processTableData();
      }
      function tableBulkActionHandler(event){
        if (event.detail.action === 'Archive'){
          archiveSelection(event.detail.selections);
        }
      }
      function tableBulkRowHandler(event){
        if (event.detail.action === 'Archive'){
          archiveItem(event.detail.data);
        }
      }
      function archiveItem(value){
        swal({
          title: '',
          // text: "Are you sure you want to delete these items",
          html: `
            <ins-loader
              state-icon="warning"
              icon-color="warning"
              state-message="Are you sure you want to archive this item?">
            </ins-loader>`,
          type: '',
          showCancelButton: true,
          confirmButtonText: 'Archive'
        }).then((result) => {
          deleteItem(value);
          processTableData();
        });
      }
      function archiveSelection(selections){
        swal({
          title: '',
          html: `
          <ins-loader
            state-icon="warning"
            icon-color="warning"
            state-message="Are you sure you want to archive these items?">
          </ins-loader>`,
          type: '',
          showCancelButton: true,
          confirmButtonText: 'Archive'
        }).then((result) => {
          if (result.value) {
              let counter = 0;
              selections.forEach(value => {

                this.deleteItem(value);
                counter++;

                if (selections.length === counter){
                  insBaseTable.resetSelections();
                  processTableData()
                }
              });
            }
        });
      }

      function deleteItem(value) {
        var selectionIndex;
        var searchSelectionIndex;

        if (rawData) {
          selectionIndex = rawData.findIndex(item => {
            return item.Id === value;
          });
        }

        if (filteredData) {
          searchSelectionIndex = filteredData.findIndex(item => {
            return item.Id === value;
          });
        }

        if (selectionIndex > -1){
          rawData.splice(selectionIndex, 1);
        }

        if (searchSelectionIndex > -1){
          filteredData.splice(searchSelectionIndex, 1);
        }
      }

      function processTableData(){
        var last_index = pageNumber * pageSize;
        var first_index = (last_index - pageSize) + 1;

        displayedData = [];

        if (searchKeyword) {

          filteredData.forEach((value, index) => {
            if (index >= first_index && index <= last_index) {
              displayedData.push(value);
            } else if (filteredData.length < pageSize){
              displayedData.push(value);
            }
          });
          insBaseTable.totalCount = filteredData.length;

        } else {

          rawData.forEach((value, index) => {
            if (index >= first_index && index <= last_index) {
              displayedData.push(value);
            }
          });
          insBaseTable.totalCount = rawData.length;

        }

        insBaseTable.tableData = displayedData;
        insBaseTable.loadingScreen = false;
      }
      function dynamicKeySort(key) {
        var OrderSortIndex = 1;

        if (key[0] === "-") {
          OrderSortIndex = -1;
          key = key.substr(1);
        }

        return function (a, b) {
          var num = (a[key] < b[key]) ? -1 : (a[key] > b[key]) ? 1 : 0;
          return num * OrderSortIndex;
        }
      }
      function fetchDataFromSrc () {

        insBaseTable.loadingScreen = true;

        insBaseTable.loaderTitle = "Processing Your Data";
        insBaseTable.loaderMessage = "Just a moment, we're fetching and processing your data.";
        insBaseTable.loaderIcon = "processing";

        var xhttp = new XMLHttpRequest();
        xhttp.onreadystatechange = function () {
          if (this.readyState == 4 && this.status == 200) {
            var tableData = JSON.parse(this.responseText);
            rawData = tableData.list;
            insBaseTable.totalCount = tableData.count;
            insBaseTable.tableHeaders = [
              { label: "Default Column", type: 'string', sortable: true, hasLink: true },
              { label: "Date Column", type: 'string', sortable: true },
              { label: "Number Column", type: 'number', sortable: true },
              { label: "Currency Column", type: 'currency', sortable: true }
            ];

            processTableData();
          }
        };
        xhttp.open("GET", 'https://ag64gmtsc0.execute-api.us-east-2.amazonaws.com/insites-dummy-deployment/insites-dummy-get-table', true);
        xhttp.send();
      }

      fetchDataFromSrc();

  }
}
</script>

