<template>
  <div id="app">
    <router-view/>
    <!-- <ActivityModal /> -->
  </div>
</template>

<script>
import ActivityModal from "./components/ActivityModal.vue";

export default {
  name: 'app',
  components: {
    ActivityModal
  },
  methods: {
    getAdmins() {
      this.$http
        .get('/api/get-all-administrator-list-data.json')
        .then(res => {
          this.$store.commit('setAdministratorList', res.data.items);
        }).catch(this.errorHandler);
    },
    errorHandler(error){
      console.error('error', error);
      toastIt("error", "Failed to load system fields.", this.ops);
    }
  },
  mounted(){
    this.$store.dispatch('fetchSystemFields', 'company_type');
    this.$store.dispatch('fetchSystemFields', 'contact_type');
    this.$store.dispatch('fetchSystemFields', 'industry_type');
    this.$store.dispatch('fetchSystemFields', 'google_maps_api_key');
    this.getAdmins();
  }
}
</script>

<style src="./App.scss" lang="scss"></style>
