export default {
  data() {
    return {
      insRendererEl: undefined,
      get apiHeader() {
        let headers = {
          "X-CSRF-Token": "",
          "X-Requested-With": "XMLHttpRequest",
          Accept: "application/json",
          "Content-Type": "application/json"
        }
        if (window.location.hostname !== "localhost") {
          headers["X-CSRF-Token"] = JSON.parse(
            document.getElementById("formConfig").innerHTML
          );
        }
        return headers;
      },
      get ops() {
        let ops = {
          closeHtml: '<button><i class="icon-close-1"></i></button>',
          closeButton: true,
          timeOut: 0,
          extendedTimeOut: 0,
          preventDuplicates: true
        };
        return ops;
      },
      get getTheme() {
        let themeSnipet = [{ "featureType": "water", "elementType": "geometry", "stylers": [{ "color": "#e9e9e9" }, { "lightness": 17 }] }, { "featureType": "landscape", "elementType": "geometry", "stylers": [{ "color": "#f5f5f5" }, { "lightness": 20 }] }, { "featureType": "road.highway", "elementType": "geometry.fill", "stylers": [{ "color": "#ffffff" }, { "lightness": 17 }] }, { "featureType": "road.highway", "elementType": "geometry.stroke", "stylers": [{ "color": "#ffffff" }, { "lightness": 29 }, { "weight": 0.2 }] }, { "featureType": "road.arterial", "elementType": "geometry", "stylers": [{ "color": "#ffffff" }, { "lightness": 18 }] }, { "featureType": "road.local", "elementType": "geometry", "stylers": [{ "color": "#ffffff" }, { "lightness": 16 }] }, { "featureType": "poi", "elementType": "geometry", "stylers": [{ "color": "#f5f5f5" }, { "lightness": 21 }] }, { "featureType": "poi.park", "elementType": "geometry", "stylers": [{ "color": "#dedede" }, { "lightness": 21 }] }, { "elementType": "labels.text.stroke", "stylers": [{ "visibility": "on" }, { "color": "#ffffff" }, { "lightness": 16 }] }, { "elementType": "labels.text.fill", "stylers": [{ "saturation": 36 }, { "color": "#333333" }, { "lightness": 40 }] }, { "elementType": "labels.icon", "stylers": [{ "visibility": "off" }] }, { "featureType": "transit", "elementType": "geometry", "stylers": [{ "color": "#f2f2f2" }, { "lightness": 19 }] }, { "featureType": "administrative", "elementType": "geometry.fill", "stylers": [{ "color": "#fefefe" }, { "lightness": 20 }] }, { "featureType": "administrative", "elementType": "geometry.stroke", "stylers": [{ "color": "#fefefe" }, { "lightness": 17 }, { "weight": 1.2 }] }];
        return themeSnipet;
      }
    };
  },
  methods: {
    getGoogleAPIKey() {
      let url = `/api/get-system-fields-data.json?key=google_maps_api_key`;
      return this.$http.get(url)
        .then(response => {
          if (response.data.items.results.length) {
            return response.data.items.results[0].value;
          } else {
            return '-';
          }
        }).catch(this.errorHandler);
    },
    toastNotif(type, message) {
      let options = {
        closeHtml: '<button><i class="icon-close-1"></i></button>',
        closeButton: true,
        timeOut: 0,
        extendedTimeOut: 0,
        preventDuplicates: true
      }
      toastIt(type, message, options);
    }
  }
}