<template>
  <ins-base-table
    ref="contactListTable"
    id="testTable"
    v-on:onPaginationChange="tablePaginationHandler"
    v-on:onTableSearch="tableSearchHandler"
    v-on:onTableSort="tableSortHandler"
    v-on:onTableBulkAction="tableBulkActionHandler"
    v-on:onTableRowAction="tableRowActionHandler" />
</template>

<script>
  import { isEmpty } from 'lodash';
  export default {
    name: "ViewCompanyDetails",
    props: ['companyId'],
    data() {
      return {
        hasTableData: false,
        searchKeyword: '',
        pageNumber: 1,
        pageSize: 10,
        sortBy: 'email',
        sortOrder: 'asc',
        source: '/api/get-company-contact-list-data.json',
        sourceSearch: '/api/search-company-contact-list-data.json',
        contactModel: {
          form_configuration_name: "crm_delete_customer",
          form: {
            profiles_attributes: {
              customer_attributes: {
                properties_attributes: {
                  is_enabled: "false"
                }
              }
            }
          }
        },
        headers: {}
      }
    },
    methods: { 
      tablePaginationHandler(event){
        this.pageSize = event.detail.pageSize;
        this.pageNumber = event.detail.pageNumber;
        this.fetchContactList();
      },
      tableSearchHandler(event){
      
        if ((event.detail.keyCode === 13 && event.detail.value !==  "") ||
            (event.detail.value === "" && this.searchKeyword !== "")
          ){
          this.searchKeyword = event.detail.value;
          this.pageNumber = 1;
          this.fetchContactList();
        } else if (event.detail.value === "") {
          this.searchKeyword = '';
          this.pageNumber = 1;
          this.fetchContactList();
        }
      },
      tableSortHandler(event){
        this.sortBy = event.detail.keyword;
        this.sortOrder = event.detail.order;
        this.fetchContactList();
      },
      tableBulkActionHandler(event){
        switch(event.detail.action){
          case 'Archive':
            this.warnUser('Are you sure you want to archive these contacts?')
              .then(res => res.value ? this.archiveSelection(event.detail.selections) : false);
            break;
        }
      },
      tableRowActionHandler(event){
        switch(event.detail.action){
          case 'Archive':
            this.warnUser('Are you sure you want to archive this contact?')
              .then(res => res.value ? this.archiveItem(event.detail.data.id) : false);
            break;

          case 'Edit':
            this.editContactHandler(event.detail.data);
            break;

          case 'View':
            this.viewContactHandler(event.detail.data);
            break;
        }
      },
      archiveSelection(selections){
        let counter = 0
        selections.forEach(item => {
          counter++;
          this.archiveItem(item.id, selections.length, counter);
        });
      },
      archiveItem(id, bulk = 1, counter = 1){
        let url = `/api/users/${id}`;      
        let body = JSON.stringify(this.contactModel);
        this.$http.put(url, body, { headers: this.headers })
          .then(response => {
            if (counter === bulk){
              toastIt('success', `Successfully delete contact${bulk > 1 ? 's':''}`, this.ops);
              this.fetchContactList();
            }
          }).catch(error => {
            console.error('error', error);
            toastIt('error', 'Failed to archive contact', this.ops);
          });
      },
      warnUser(message){
        return swal({
          title: "",
          html: `
            <ins-loader
              state-icon="warning"
              icon-color="warning"
              state-message="${message}">
            </ins-loader>`,
          type: "",
          showCancelButton: true,
          confirmButtonText: "Archive"
        });
      },
      alertHandler(result, data){
        // if (result.value){
        //   callback(data);
        // }
      },
      editContactHandler(data){
        data['Contact Type'] = data.temp_contact_type;
        this.$store.commit("pushUserData", data);
        this.$store.commit("pushFormType", "edit");
        this.$router.push("/manage-crm");
      },
      viewContactHandler(data){
        this.$store.commit("setContactData", data);
        this.$router.push("/view-contact");
      },
      fetchContactList(){

        this.loadingStateHandler(
          "Processing Your Data",
          "Just a moment, we're fetching and processing your data.",
          "processing");

        let url = '';
        if (this.searchKeyword){
          url = this.constructURL(this.sourceSearch);
        } else {
          url = this.constructURL(this.source);
        }

        this.$http.get(url)
          .then(this.responseHandler)
          .catch(this.errorHandler);
      },
      responseHandler(response){
        let data = response.data.items;
        this.$refs.contactListTable.totalCount = data.total_entries;

        if (data.total_entries){
          this.$refs.contactListTable.pageNumber = this.pageNumber;
          this.$refs.contactListTable.pageSize = this.pageSize;
          this.$refs.contactListTable.loadingScreen = false;
          this.$refs.contactListTable.tableData = this.constructTableData(data.results);
        } else {
          if (this.searchKeyword){
            this.loadingStateHandler('', `No results for "${this.searchKeyword}".`, 'no data');
          } else {
            this.loadingStateHandler('', 'No contacts yet.', 'no data');
          }
        }
      },
      errorHandler(error){
        console.error('error', error);
        this.loadingStateHandler('', 'Failed to fetch contact list. Please try again.', 'error');
      },
      loadingStateHandler(title, message, icon){
        this.$refs.contactListTable.loadingScreen = true;
        this.$refs.contactListTable.loaderTitle = title;
        this.$refs.contactListTable.loaderMessage = message;
        this.$refs.contactListTable.loaderIcon = icon;
      },
      constructURL(source){
        let url = `${source}?id=${this.companyId}`;
        url += `&size=${this.pageSize}`;
        url += `&page=${this.pageNumber}`;
        url += `&sortBy=${this.sortBy === "Name" ? 'first_name' : 'email'}`;
        url += `&orderBy=${this.sortOrder}`;
        url += `&keyword=${this.searchKeyword}`;
        return url;
      },
      constructTableHeader(label, type, sortable, hasImage, hasColumnAction){
        return { label, type, sortable, hasImage, hasColumnAction }
      },
      constructTableData(data){
        return data.map(this.constructCRMTableData);
      },
      checkValue(value, withPlus){
        if (value && value !== 'undefined' && value !== 'null'){
          return value ? `${withPlus ? '+':''}${value}` : '';
        }
        return '';
      },
      constructPhoneNumber(item, field){
        let phoneNumber = "";
        if (item) {

          if (this.checkValue(item[field + '_phone_area_code']) ||
              this.checkValue(item[field + '_phone_number'])) {

            phoneNumber = `${this.checkValue(item[field + '_phone_country_code'], true)}`;
            phoneNumber += ` ${this.checkValue(item[field + '_phone_area_code'])}`;
            phoneNumber += ` ${this.checkValue(item[field + '_phone_number'])}`;  
          }
        }
        return phoneNumber.trim();
      },
      constructCRMTableData(item, lastActivity){
        return {
          "id": item.id === null ? "" : item.id,
          "raw": item,
          "First Name": item.first_name,
          "Last Name": item.last_name,
          "Name": `${item.first_name} ${item.last_name}`,
          "Email": item.email,
          "Home Phone": this.constructPhoneNumber(item.contact_number, 'home'),
          "Mobile Phone": this.constructPhoneNumber(item.contact_number, 'mobile'),
          "Work Phone": this.constructPhoneNumber(item.contact_number, 'work'),
          "Mobile Phone Country Code": isEmpty(item.contact_number) ? '' :this.checkValue(item.contact_number.mobile_phone_country_code),
          "Mobile Phone Area Code": isEmpty(item.contact_number) ? '' :this.checkValue(item.contact_number.mobile_phone_area_code),                
          "Mobile Phone Number": isEmpty(item.contact_number) ? '' : this.checkValue(item.contact_number.mobile_phone_number),
          "Home Phone Country Code": isEmpty(item.contact_number) ? '' : this.checkValue(item.contact_number.home_phone_country_code),
          "Home Phone Area Code": isEmpty(item.contact_number) ? '' : this.checkValue(item.contact_number.home_phone_area_code),
          "Home Phone Number": isEmpty(item.contact_number) ? '' : this.checkValue(item.contact_number.home_phone_number), 
          "Work Phone Country Code": isEmpty(item.contact_number) ? '' : this.checkValue(item.contact_number.work_phone_country_code),
          "Work Phone Area Code": isEmpty(item.contact_number) ? '' : this.checkValue(item.contact_number.work_phone_area_code),
          "Work Phone Number": isEmpty(item.contact_number) ? '' : this.checkValue(item.contact_number.work_phone_number),
          "Notes": isEmpty(item.customer_details) ? '' : this.checkValue(item.customer_details.notes),
          "Facebook" : isEmpty(item.social_links) ? '' : this.checkValue(item.social_links.facebook_link),
          "Instagram" : isEmpty(item.social_links) ? '' : this.checkValue(item.social_links.instagram_link),
          "Twitter" : isEmpty(item.social_links) ? '' : this.checkValue(item.social_links.twitter_link),
          "Google" : isEmpty(item.social_links) ? '' : this.checkValue(item.social_links.google_plus_link),
          "Linkedin" : isEmpty(item.social_links) ? '' : this.checkValue(item.social_links.linkedin_link),
          "Youtube" : isEmpty(item.social_links) ? '' : this.checkValue(item.social_links.youtube_link),
          "company_id": isEmpty(item.customer_details) ? '' : this.checkValue(item.customer_details.company_id),
          "Contact Type": isEmpty(item.customer_details) ? '' : this.findContactType(item.customer_details.contact_type),
          "temp_contact_type": isEmpty(item.customer_details) ? '' : this.checkValue(item.customer_details.contact_type),
          "Name_Img": isEmpty(item.customer_details) ? '' : item.customer_details.profile_image === null ? '' : this.checkValue(item.customer_details.profile_image.original),
          "Profile Image": isEmpty(item.customer_details) ? '' : item.customer_details.profile_image === null ? '' : this.checkValue(item.customer_details.profile_image.original),
          "Home Address": isEmpty(item.customer_details) ? '' : item.customer_details.home_address.length > 0 ? this.checkValue(item.customer_details.home_address[0].address) : '', 
          "Home Street": isEmpty(item.customer_details) ? '' : item.customer_details.home_address.length > 0 ? this.checkValue(item.customer_details.home_address[0].street) : '', 
          "Home City": isEmpty(item.customer_details) ? '' : item.customer_details.home_address.length > 0 ? this.checkValue(item.customer_details.home_address[0].city) : '', 
          "Home State": isEmpty(item.customer_details) ? '' : item.customer_details.home_address.length > 0 ? this.checkValue(item.customer_details.home_address[0].state) : '', 
          "Home Area Code": isEmpty(item.customer_details) ? '' : item.customer_details.home_address.length > 0 ? this.checkValue(item.customer_details.home_address[0].postcode) : '', 
          "Home Country": isEmpty(item.customer_details) ? '' : item.customer_details.home_address.length > 0 ? this.checkValue(item.customer_details.home_address[0].country) : '', 
          "Last Activity": item.updated_at
        };
      },
      findContactType(id){
        if (id){
          if (id === 'Primary'){
            return 'Primary';
          } else if (id === 'Secondary'){
            return 'Secondary';
          } else {
            let data = this.$store.state.SystemFieldsStore.contact_type.results.find(item => {
              return item.id === id;
            });
            return data ? data.value : '';
          }
        }
        return '';
      },
      checkEmpty(obj, field){
        if (!isEmpty(obj)){
          return this.checkValue(obj[field])
        }
        return '';
      },
    },
    mounted() {
      this.fetchContactList();
      this.headers = JSON.parse(JSON.stringify(this.apiHeader));
      this.$refs.contactListTable.tableHeaders = [
        this.constructTableHeader('Name', 'string', true, true, true),
        this.constructTableHeader('Contact Type', 'string'),
        this.constructTableHeader('Work Phone', 'string'),
        this.constructTableHeader('Mobile Phone', 'string'),
        this.constructTableHeader('Email', 'string', true)
      ]
      this.$refs.contactListTable.rowActions = ['View','Edit','Archive']
      this.$refs.contactListTable.bulkActions = ['Archive']
      this.$refs.contactListTable.searchbarPlaceholder = 'Search';
    }
  }
</script>

<style lang="scss" scoped>
</style>
