<template>
<div> 
    <ins-button
        field="addNewAdmin"
        id="addNewAdmin" 
        label="Add New Admin User" 
        color="blue" 
        solid="true"
        size="medium"
        v-on:click='addNewAdmin()' 
        disabled="false">
    </ins-button>  

    <ins-base-table
      id="insBaseTable"
      searchbar-placeholder="Search by Name or Email..."
      v-on:onPaginationChange="tablePaginationHandler"
      v-on:onTableSearch="tableSearchHandler"
      v-on:onTableSort="tableSortHandler"
      v-on:onTableBulkAction="tableBulkActionHandler"
      v-on:onTableRowAction="tableRowActionHandler">
    </ins-base-table>

</div>
</template>

<script>
import router from "../router";
import moment from "moment";

export default {
  name: "ListUsers",
  data() {
    return {
      source: "/api/get-all-administrator-data.json",
      searchSource: "/api/search-administrators-data.json",
      location: "",
      insBaseTable: "",
      rawData: [],
      filteredData: [],
      displayedData: [],
      tableHeaders: [],
      totalCount: 0,
      pageNumber: 1,
      pageSize: 10,
      searchKeyword: "",
      sortedKeyword: "",
      sortedBy: "asc",
      formModel: {
        form_configuration_name: "delete_admin_user",
        form: {
          profiles_attributes: {
            administrator_attributes: {
              properties_attributes: {
                is_enabled: "false"
              }
            }
          }
        }
      },
      headers: {
        "X-CSRF-Token": "3b70c2a1087ba76b70ae54f620cd8710da18ba55",
        "X-Requested-With": "XMLHttpRequest",
        "Accept": "application/json",
        "Content-Type": "application/json"
      },
      continueDeletion: false
    };
  },
  methods: {
    addNewAdmin() {
      this.$store.commit("pushFormType", "add");
      router.push("/manage-admin-user");
    },
    tablePaginationHandler(event) {
      this.insBaseTable.loadingScreen = true;
      this.pageSize = event.detail.pageSize;
      this.pageNumber = event.detail.pageNumber;
      this.fetchDataFromSrc();
    },
    tableSearchHandler(event) {
      
      if ((event.detail.keyCode === 13 && event.detail.value !==  "") ||
          (event.detail.value === "" && this.searchKeyword !== "")
        ){

        this.searchKeyword = event.detail.value;
        this.pageNumber = 1;
        this.insBaseTable.pageNumber = 1;
        this.fetchDataFromSrc();
      }
    },
    tableSortHandler(event) {
      this.sortedKeyword =
        event.detail.keyword === "Admin User" || event.detail.keyword === "Name"
          ? "first_name"
          : event.detail.keyword;
      this.sortedBy = event.detail.order;
      this.fetchDataFromSrc();
    },
    tableBulkActionHandler(event) {
      if (event.detail.action === "Remove") {
        this.archiveSelection(event.detail.selections);
      }
    },
    tableRowActionHandler(event) {
      if (event.detail.action === "Remove") {
        this.archiveItem(event.detail.data);
      } else if (event.detail.action === "Edit") {
        this.$store.commit("pushUserData", event.detail.data);
        this.$store.commit("pushFormType", "edit");
        this.viewItem();
      } else if (event.detail.action === "View") {
        this.$store.commit("pushUserData", event.detail.data);
        this.$store.commit("pushFormType", "view");
        this.viewItem();
      }
    },
    viewItem() {
      router.push("/manage-admin-user");
    },
    archiveItem(value) {
      swal({
        title: "",
        html: `
          <ins-loader
            state-icon="warning"
            icon-color="warning"
            state-message="Are you sure you want to remove this user?">
          </ins-loader>`,
        type: "",
        showCancelButton: true,
        confirmButtonText: "Remove"
      }).then(result => {
        if (result.value){
          this.deleteItem(value);
        }
      });
    },
    archiveSelection(selections) {
      swal({
        title: "",
        html: `
          <ins-loader
            state-icon="warning"
            icon-color="warning"
            state-message="Are you sure you want to remove these users?">
          </ins-loader>`,
        type: "",
        showCancelButton: true,
        confirmButtonText: "Remove"
      }).then(result => {
        if (result.value) {
          let counter = 0;
          this.continueDeletion = true;
          selections.forEach(value => {

            if (this.continueDeletion){

              this.deleteItem(value);
              counter++;

              if (selections.length === counter) {
                this.continueDeletion = false;
                this.insBaseTable.resetSelections();
              }
            }
          });
        }
      });
    },
    deleteItem(value) {
      // this.formModel.form.deleted_at = moment().format("DD-MM-YYYY HH:mm:ss");

      let url = `${this.location}/api/users/${value.id}`;
      let body = JSON.stringify(this.formModel);

      let ops = {
        closeHtml: '<button><i class="icon-close-1"></i></button>',
        closeButton: true,
        timeOut: 0,
        extendedTimeOut: 0,
        preventDuplicates: true
      };

      this.$http
        .put(url, body, { headers: this.headers })
        .then(response => {
          if (!this.continueDeletion){
            toastIt("success", "User has been removed.", ops);
            this.fetchDataFromSrc();
          }
        }).catch(error => {
          this.continueDeletion = false;
          this.fetchDataFromSrc();
          toastIt("error", "Failed to remove user. Please try again.", ops);
        });
    },
    processTableData() {
      this.insBaseTable.tableData = this.rawData;
      this.insBaseTable.loadingScreen = false;
    },
    dynamicKeySort(key) {
      let OrderSortIndex = 1;

      if (key[0] === "-") {
        OrderSortIndex = -1;
        key = key.substr(1);
      }

      return function(a, b) {
        let num = a[key] < b[key] ? -1 : a[key] > b[key] ? 1 : 0;
        return num * OrderSortIndex;
      };
    },
    fetchDataFromSrc() {
      this.insBaseTable.loadingScreen = true;

      this.insBaseTable.loaderTitle = "Processing Your Data";
      this.insBaseTable.loaderMessage =
        "Just a moment, we're fetching and processing your data.";
      this.insBaseTable.loaderIcon = "processing";

      let self = this;
      let url = `${this.source}?size=${this.pageSize}&page=${this.pageNumber}&sortBy=${this.sortedKeyword}&orderBy=${this.sortedBy}`;

      if (this.searchKeyword){
        url = `${this.searchSource}?size=${this.pageSize}&page=${this.pageNumber}&sortBy=${this.sortedKeyword}&orderBy=${this.sortedBy}&keyword=${this.searchKeyword}`;
      }

      this.$http
        .get(url)
        .then(response => {
          let tableData = response.data.items.results;
          if (tableData.length) {
            self.insBaseTable.totalCount = response.data.items.total_entries;
            this.bindTableData(tableData);
          } else {
            this.insBaseTable.loaderTitle = "";

            if (this.searchKeyword) {
              this.insBaseTable.loaderMessage = `No results for "${this.searchKeyword}"`;
            } else {
              this.insBaseTable.loaderMessage = "There are no Admin Users yet.";
            }
            this.insBaseTable.loaderIcon = "no-data";
          }
        })
        .catch(error => {
          console.error("Error: " + error);
          this.errorHandler();
        });
    },
    errorHandler(){
      this.insBaseTable.loaderTitle = undefined;
      this.insBaseTable.loaderMessage = "Failed to load Data. Please try again.";
      this.insBaseTable.loaderIcon = "error";
    },
    bindTableData(tableData) {
      let tempArr = [];
      tableData.forEach(item => {
        let phone = "";
        if (item.contact_number  === null) {
            phone = "";
         } else {
            let countryCode = item.contact_number.mobile_phone_country_code !== null ?'+' + item.contact_number.mobile_phone_country_code : '';
            let areaCode = item.contact_number.mobile_phone_area_code !== null ? item.contact_number.mobile_phone_area_code : '';
            let phoneNumber = item.contact_number.mobile_phone_number !== null ? item.contact_number.mobile_phone_number : '';
            phone = `${countryCode} ${areaCode} ${phoneNumber}`;
         }
        // "Name": item.first_name !== null || item.first_name !== "" ? item.first_name : '' + " " + item.first_name !== null || item.first_name !== "" ? item.first_name : ''
        let tempObj = {
          id: item.id,
          "First Name": item.first_name !== null || item.first_name !== "" ? item.first_name : '',
          "Last Name": item.last_name !== null || item.last_name !== "" ? item.last_name : '',
          "Name": `${item.first_name ? item.first_name : ''} ${item.last_name ? item.last_name : ''}`,
          "Email": item.email !== null || item.email !== "" ? item.email : '',
          "Role": "Administrator",
          "Company":
            item.administrator_details.company_name === null
              ? ""
              : item.administrator_details.company_name,
          "Mobile Phone": phone,
          "Mobile Phone Country Code":
            item.contact_number === null
              ? ""
              : item.contact_number.mobile_phone_country_code,
          "Mobile Phone Area Code":
            item.contact_number === null
              ? ""
              : item.contact_number.mobile_phone_area_code,
          "Mobile Phone Number":
            item.contact_number === null
              ? ""
              : item.contact_number.mobile_phone_number,
          "Last Activity": moment(item.updated_at).calendar(null, {sameElse: 'Do MMM YYYY'})
        };

        tempArr.push(tempObj);
        this.rawData = tempArr;

        if (tableData.length === tempArr.length) {
          this.processTableData();
        }
      });
    },
    setInsRendererLabel(){
      let insRendererEl = document.querySelector('ins-renderer');
      if (insRendererEl.updateRouteLabel){
        insRendererEl.updateRouteLabel('Admin Users');
      } else {
        setTimeout(() => {
          this.setInsRendererLabel();
        }, 300);
      }
    }
  },
  mounted() {
    this.setInsRendererLabel();

    if (window.location.hostname !== "localhost") {
      this.headers["X-CSRF-Token"] = JSON.parse(
        document.getElementById("formConfig").innerHTML
      );
    } else {
      this.headers["X-CSRF-Token"] = "3b70c2a1087ba76b70ae54f620cd8710da18ba55";
      this.location = "https://insites-poc.staging-sydney.near-me.com";
    }

    this.insBaseTable = document.getElementById("insBaseTable");
    this.insBaseTable.bulkActions = ["Remove"];
    this.insBaseTable.rowActions = ["View", "Edit", "Remove"];
    this.insBaseTable.tableHeaders = [
      { label: "Name", type: "string", sortable: true, hasLink: true },
      { label: "Email", type: "string", sortable: true },
      { label: "Role", type: "string", sortable: false },
      { label: "Mobile Phone", type: "string", sortable: false },
      { label: "Last Activity", type: "string", sortable: false }
    ];

    this.fetchDataFromSrc();
  }
};
</script>

<style scoped>
  /* #addNewAdmin {
    position: absolute;
    top: 0px;
    z-index: 1;
  } */
</style>
