<template>
  <div id="case-list-tbl">
    <div id="case-filters">
       <!--  <ins-filter class="hydrated" v-on:onFilterApply="onFilterApplyHandler">
            <ins-filter-item name="Case Status" options='["All", "New", "Pending", "Open", "Flagged", "Closed"]'></ins-filter-item>
        </ins-filter> -->
    </div>
    <ins-base-table
      id="insBaseTable"
      searchbar-placeholder="Search by Subject, Contact, Email or Assignee ..."
      v-on:onPaginationChange="tablePaginationHandler"
      v-on:onTableSearch="tableSearchHandler"
      v-on:onTableSort="tableSortHandler"
      v-on:onTableBulkAction="tableBulkActionHandler"
      v-on:onTableRowAction="tableRowActionHandler">
    </ins-base-table>
  </div>
</template>

<script>
import moment from "moment";
import { isEmpty } from 'lodash';
import { each } from "lodash";

export default {
    name: "ListCases",
    components: {

    },   
    data () {
        return {
            caseData: this.caseData,
            source: "/api/get-all-cases-data.json",
            sourceCaseData: "/api/get-case-data.json?id=",
            sourceAdminData: "/api/get-crm-data.json?id=",
            totalCount: 0,
            pageNumber: 1,
            pageSize: 10,
            searchKeyword: "",
            sortedKeyword: "created_at",
            sortedBy: "desc",            
            headers: {},
            rawFormData: {
              utf8: "" ,
              form_configuration_name: "delete_case_customization",
              authenticity_token: "",
              resource_id: "new",
              parent_resource_id: "",
              parent_resource_class: "CustomModelType",
              _method: "DELETE",
              customization_id: ""
            },
            continueDeletion: false
        }
    },
    methods: {
        onFilterApplyHandler(event) {
            let status = event.detail['Case Status'];
            // let date_from = event.detail['Date Period'].from;
            // let date_to = event.detail['Date Period'].to;
        },
        tablePaginationHandler(event) {
            this.insBaseTable.loadingScreen = true;
            this.pageSize = event.detail.pageSize;
            this.pageNumber = event.detail.pageNumber;
            this.fetchDataFromSrc();
        },
        tableSearchHandler(event){
            if (
                (event.detail.keyCode === 13 && event.detail.value !== "") ||
                (event.detail.value === "" && this.searchKeyword !== "")
                ) {     
                    this.insBaseTable.loadingScreen = true;
                    this.searchKeyword = event.detail.value;
                    if (!this.searchKeyword) {
                        this.processTableData();
                        return;
                    }
                    this.filteredData = [];
                    this.rawData.forEach((value) => {
                        Object.keys(value).forEach((key) => {
                            if (key === 'Subject' || key === 'Contact' || key === 'Email' || key === 'Assigned To') {
                                let match = value[key]; 
                                console.log("value ", value, " ",key);
                                console.log("match ", match, "search: ", this.searchKeyword.toLowerCase());
                            if (match.toLowerCase().includes(this.searchKeyword.toLowerCase())) {
                                
                                    let checkExist = this.filteredData.find(item => {
                                        return item.id === value.id;
                                    })
                                    if (!checkExist) { this.filteredData.push(value); }
                                }
                            }
                        });
                    });
                    
                    if (this.sortedKeyword && this.filteredData.length > 0) {
                        this.filteredData.sort(this.dynamicKeySort(this.sortedKeyword));
                    }
                        this.insBaseTable.pageNumber = 1;
                        this.processTableData();
                    if (!this.filteredData.length) {
                        this.insBaseTable.loadingScreen = true;
                        this.insBaseTable.loaderTitle = '';
                        this.insBaseTable.loaderMessage = 'No result found for "' + this.searchKeyword + '"';
                        this.insBaseTable.loaderIcon = '';
                    }
                }
            },
        dynamicKeySort(key, order) {
            let OrderSortIndex = order === 'asc' ? 1 : -1;

            return function (a, b) {
                let num = (a[key] < b[key]) ? -1 : (a[key] > b[key]) ? 1 : 0;
                return num * OrderSortIndex;
            }
        },
        tableSortHandler(event) { 
            this.sortedKeyword = event.detail.keyword;
            this.sortedBy = event.detail.order;
           
            if (this.searchKeyword){
                this.filteredData.sort(this.dynamicKeySort(this.sortedKeyword, this.sortedBy));
            } else {
                this.rawData.sort(this.dynamicKeySort(this.sortedKeyword, this.sortedBy));
            }
            this.processTableData();
        },
        tableBulkActionHandler(event) {
            if (event.detail.action === "Delete") {
                this.archiveSelection(event.detail.selections);
            }
        },
        tableRowActionHandler(event) {
            if (event.detail.action === "View") {
                this.viewItem(event.detail);
            }
            if (event.detail.action === "Delete") {
                this.deleteItem(event.detail, true);
            }
        },
        archiveSelection(selections) {
            console.log("sections: ", selections);
            let self = this;
            swal({
                title: "",
                html: `
                <ins-loader
                    state-icon="warning"
                    icon-color="warning"
                    state-message="Are you sure you want to delete these cases?">
                </ins-loader>`,
                type: "",
                showCancelButton: true,
                confirmButtonText: "Delete"
            }).then(result => {
                if (result.value) {
                let counter = 0;
                self.continueDeletion = true;

                selections.forEach(async value => {
                    if (self.continueDeletion) {
                        await self.deleteCase(value, false);                   
                        counter++;

                    if (selections.length === counter) {
                        self.continueDeletion = false;
                        parent.toastIt("success", "Cases have been deleted.", self.ops);
                        // self.insBaseTable.resetSelections();
                        self.fetchDataFromSrc();
                    }
                    }
                }); 
                }
            });
        },
        deleteItem(event, showSuccess) {
            // console.log("trigger: ", event);
            let deleteMsg = "Are you sure you want to delete this case?"
            let self = this; 
            swal({
                title: "",
                html: `
                <ins-loader
                    state-icon="warning"
                    icon-color="warning"
                    state-message="${deleteMsg}">
                </ins-loader>`,
                type: "",
                showCancelButton: true,
                confirmButtonText: "Delete"
            }).then(result => {
                if (result.value) {          
                    this.continueDeletion = true;
                    this.deleteCase(event.data, true);               
                }
            })
            .catch(error => {
                this.continueDeletion = false;
                console.error('error', error);
                parent.toastIt("error", "Failed to delete case.", this.ops);
                this.fetchDataFromSrc();
            });
        },
        deleteCase(item, showSuccess) {
            let url = `/api/customizations/${item.id}`
            this.rawFormData.customization_id = item.id;
            this.rawFormData.parent_resource_id = item.raw.case_type;
            let parsedFormData = this.appendToFormData(this.rawFormData); 

            this.$http.post(url, parsedFormData) //{ headers: this.headers}
                .then(res => { 
                    if (!this.continueDeletion) {
                        showSuccess? parent.toastIt("success", "Successfully deleted case.", this.ops) : '';
                        this.fetchDataFromSrc();
                    }
                })
                .catch(error => {
                    this.continueDeletion = false;
                    console.error('error', error);
                    parent.toastIt("error", "Failed to delete case.", this.ops);
                    this.fetchDataFromSrc();
                });
        },
        appendToFormData(data) {
          let formData = new FormData();

          each (data, (val, key) => {
            formData.append(key, val);
          });
          return formData;
        },
        viewItem(header) {
            this.$store.commit("pushCaseData", header.data);
            this.$router.push("/case-details");            
        },
        processTableData() {
            let last_index = this.pageNumber * this.pageSize;
            let first_index = (last_index - this.pageSize);

            this.displayedData = [];

            if (this.searchKeyword) {
                this.insBaseTable.totalCount = this.filteredData.length;

                this.filteredData.forEach((value, index) => {
                    if (index >= first_index && index <= last_index) {
                        this.displayedData.push(value);
                    } else if (this.filteredData.length < this.pageSize){
                        this.displayedData.push(value);
                    }
                });
            } else {
                this.rawData.forEach((value, index) => {
                    if (index >= first_index && index < last_index) {
                        this.displayedData.push(value);
                    }
                });
                
                this.insBaseTable.totalCount = this.rawData.length; 
                this.displayedData.sort(this.dynamicKeySort(this.sortedKeyword, this.sortedBy));
            }
            
            this.insBaseTable.tableData = this.displayedData;
            this.insBaseTable.loadingScreen = false;
        
        },
        loaderStateHandler(title, message, type) {
            this.insBaseTable.loadingScreen = true;
            this.insBaseTable.loaderTitle = title;
            this.insBaseTable.loaderMessage = message;
            this.insBaseTable.loaderIcon = type;
        },
        fetchDataFromSrc() {
            this.rawData = [];
            this.displayedData = [];
            this.filteredData = [];

            this.loaderStateHandler(
                "Processing Your Data",
                "Just a moment, we're fetching and processing your data.",
                "processing");

            this.insBaseTable.loadingScreen = true;

            let self = this;
            let url = `${this.source}?size=1000&page=1&sortBy=${this.sortedKeyword}&orderBy=${this.sortedBy}`;
            
            this.$http
                .get(url)
                .then(response => {
                let tableData = response.data.items.results;
                    if (tableData.length) {
                        this.bindTableData(tableData);
                    } else {
                        this.insBaseTable.loaderTitle = "";

                        if (this.searchKeyword) {
                        this.insBaseTable.loaderMessage = `No results for "${this.searchKeyword}"`;
                        } else {
                        this.insBaseTable.loaderMessage = "There are no Admin Users yet.";
                        }
                        this.insBaseTable.loaderIcon = "no-data";
                    }
                })
                .catch(error => {
                    console.error("Error: " + error);
                    this.errorHandler();
                });
        },
        errorHandler(){
            this.insBaseTable.loaderTitle = undefined;
            this.insBaseTable.loaderMessage = "Failed to load Data. Please try again.";
            this.insBaseTable.loaderIcon = "error";
        },
        checkValue(value, withPlus){
            return value && value !== 'undefined' && value !== null ? `${withPlus ? '+':''}${value}` : '';
        },
        capitalize(item){
            return this.checkValue(item).replace("_"," ").split(' ').map((s) => s.charAt(0).toUpperCase() + s.substring(1)).join(' ');
        },
        getName(item) {
            let name = "";
            if (!isEmpty(item.userData)) {
                if (item.userData.name) 
                    name = item.userData.name
                else {
                    if (this.checkValue(item.userData.first_name) !== "") 
                        name = this.userData.first_name;
                    if (this.checkValue(this.userData.last_name) !== "")
                        name += ` ${item.userData.last_name}`;
                } 
            }
            return name;
        },
        getEmail(item) {
            return !isEmpty(item.userData) ? item.userData.email ? item.userData.email : item.userData.email_address ? item.userData.email_address : '' : '';
        },
        getPhone(field,item) {
            return !isEmpty(item) ? this.constructPhoneNumber(field, item) : '';
        },
        constructPhoneNumber(field, item){
            let phoneNumber = "";
            if(this.checkValue(item.contact_number) != '') {
                phoneNumber = `${this.checkValue(item.contact_number[field + '_phone_country_code'], true)}`;
                phoneNumber += ` ${this.checkValue(item.contact_number[field + '_phone_area_code'])}`;
                phoneNumber += ` ${this.checkValue(item.contact_number[field + '_phone_number'])}`;  
            }
            return phoneNumber.trim();
        },

        getCaseData2(caseID) {
            let url = `${this.sourceCaseData}${caseID}`;
            return new Promise((resolve, reject) => {
                this.$http.get(url).then(response => {
                    let caseData = response.data.items.results;
                    resolve({state: true, data: caseData});              
                }).catch((error) => {
                    window.error_test = error;
                    let error_obj = error;
                    let collections = Object.keys(error_obj).length;
                    if(collections === 0) {
                        reject({state: false, issue: "other_issue"});
                    } else {
                        let status = error_obj["request"]["status"];
                        if(status === 429) {
                            reject({state: false, issue: 429});
                        } else {
                            reject({state: false, issue: "other_issue"});
                        }
                    }
                });
            });
        },
        /* getCaseData(caseID) {
            let url = `${this.sourceCaseData}${caseID}`
            return this.$http
                .get(url)
                .then(response => {
                    console.log(response);
                    let caseData = response.data.items.results;
                    return caseData.length > 0 ? caseData : [];                        
                    })
                .catch(error => {
                    console.log("Error: " + error);
                    this.errorHandler();
                    return [];
                });
        }, */
        getCaseDataWrapperRetry(caseID) {
            let self = this;
            let maxRetries = 3;
            return new Promise((resolve) => {
                (function internalFxn(retriesLeft) {
                  if (retriesLeft==0) {
                      // return something
                      resolve([])
                  }

                  setTimeout(async() => {
                    try {
                        const result = await self.getCaseData2(caseID);
                        // console.log('results getCaseDataWrapperRetry', result);
                        resolve(result);
                    } catch (error) {
                       // console.log('errors getCaseDataWrapperRetry', error);
                        if(error.issue === 429) {
                           // console.log('429 it is getCaseDataWrapperRetry', error);
                            retriesLeft -= 1;
                            internalFxn(retriesLeft);
                        } else {
                            // console.log("other issue getCaseDataWrapperRetry", error);
                            // change this later if we have other known errors
                            resolve([])
                        }
                    }
                  }, 2000);
                })(maxRetries);
            })
               
      
        },
        getAssignedTo(userID) {
            let url = `${this.sourceAdminData}${userID}` 
            return this.$http
                .get(url)
                .then(response => {
                    let assignedTo = response.data.items.results;
                    return assignedTo.length > 0 ? `${assignedTo[0].first_name} ${assignedTo[0].last_name}` : "";                      
                    })
                .catch(error => {
                    console.error("Error: " + error);
                    this.errorHandler();
                    return "";
                });
        },
        bindTableData(tableData) {
            let tempArr = [];
            let self = this;
            let counter = 0;
            tableData.forEach(async item => {
                let phone = "";
                let assigned_to = "";
                let status = "new";
                let rawCaseData = [];

                let caseData = await this.getCaseDataWrapperRetry(item.case_id); // get case data
                
                if (caseData.data) {
                    if (caseData.data.length > 0) {
                        rawCaseData = caseData.data[0];
                        assigned_to = this.checkValue(rawCaseData.assigned_to) !== '' ? await this.getAssignedTo(rawCaseData.assigned_to) : '';
                        status = this.checkValue(rawCaseData.status) !== '' ? rawCaseData.status.toLowerCase() : 'new';
                        
                    console.log("case: ", rawCaseData);
                    console.log("id: ", rawCaseData.assigned_to, "assigned_to: ", assigned_to);
                    }
                }
                let tempObj = {
                    id: item.case_id,
                    raw: item,
                    caseData: rawCaseData,
                    "Subject": `${this.capitalize(this.checkValue(item.case_label))}`,
                    "Contact": this.getName(item),
                    "Email": this.getEmail(item),
                    "Mobile Phone": this.getPhone('mobile', item.userData),
                    "Assigned To": assigned_to,
                    "Date Created": moment(item.created_at).calendar(null, {sameElse: 'Do MMM YYYY'}),
                    "created_at": item.created_at,
                    "Status": status
                };

                tempArr.push(tempObj);
                this.rawData = tempArr;

                if (tableData.length === tempArr.length) {
                    this.processTableData();
                }
            });
        },
        setInsRendererLabel(){
          let insRendererEl = document.querySelector('ins-renderer');
          if (insRendererEl.updateRouteLabel){
            insRendererEl.updateRouteLabel('Cases');
          } else {
            setTimeout(() => {
              this.setInsRendererLabel();
            }, 300);
          }
        }
      },
      mounted() {
        this.setInsRendererLabel();
        this.headers = JSON.parse(JSON.stringify(this.apiHeader));
        this.rawFormData.authenticity_token = this.headers["X-CSRF-Token"];

        this.insBaseTable = document.getElementById("insBaseTable");
        this.insBaseTable.rowActions = ["View", "Delete"];
        this.insBaseTable.bulkActions = ["Delete"];
        this.insBaseTable.tableHeaders = [
            { label: "Subject", type: "string", sortable: true, hasColumnAction: true, hasLink: true },
            { label: "Contact", type: "string", sortable: true, },
            { label: "Email", type: "string", sortable: true },
            { label: "Mobile Phone", type: "string", sortable: false },
            { label: "Assigned To", type: "string", sortable: true },
            { label: "Date Created", type: "string", sortable: true },
            { label: "Status", type: "string", sortable: true, hasTag: true }
        ];

        this.fetchDataFromSrc();

    }
} 
</script>

<style>
#case-list-tbl {
  position: relative;
}
/* #case-filters {
        position: absolute;
        top: 0;
        z-index: 1;
    }     */
</style>