<template>
  <ins-card no-padding steady>

    <div class="activities-header">
      <h1>Timeline</h1>
    </div>

    <div class="grid-x grid-padding-x activities-wrap">
      <div v-if="activities" class="large-12 medium-12 small-12 cell" id="activity-container-list">
        
        <div class="grid-x grid-padding-x activity-row"
          v-for="activity in activities"
          v-bind:key="activity.id">

          <div class="large-12 medium-12 small-12 cell no-padding-left">
            <div class="stick-wrap">
              <div v-bind:class="['icon-wrap ' + activity.attributes.type]">
                <i class="icon-type icon-work"></i>
                <i class="icon-type icon-phone"></i>
                <i class="icon-type icon-mail"></i>
                <i class="icon-type icon-headset"></i>
                <i class="icon-type icon-notepad"></i>
                <i class="icon-type icon-notepad-1"></i>
                <i class="icon-type icon-goal-1"></i>
                <i class="icon-type icon-check"></i>
              </div>
            </div>

            <span class="date-time-wrap">
              <!-- <span class="date"> {{ activity.date }} </span> <span class="time"> {{ activity.time }} </span> -->
              <div class="date"> {{ activity.attributes.date }} </div>
              <div class="time"> {{ activity.attributes.time }} </div>
            </span>

            <div class="details-wrap" v-html="activity.attributes.details"></div>
            <div class="actions-wrap">
              <button class="edit-btn" v-on:click="editActivity(activity)"><i class="icon-edit" ></i></button>
              <button class="delete-btn" v-on:click="deleteActivity(activity.id)"><i class="icon-delete"></i></button>
            </div>
          </div>
        </div>

      </div>

      <div v-if="!activities" class="large-12 medium-12 small-12 cell">
        <ins-loader state-title="" state-message="No Activites. This is a premium feature." state-icon=""></ins-loader>
      </div>
    </div>

    <!-- <div class="activities-footer text-right">
      data-open="activityModal" id="addActivityModal"
      <button v-on:click="addActivity">
        ADD ACTIVITY
      </button>
    </div> -->
  </ins-card>
</template>

<script>
/*eslint no-console: ["error", { allow: ["warn", "error", "log"] }] */
import swal from "sweetalert2";

export default {
  name: "AdminActivities",
  computed: {
    // activities(){
    //   return this.$store.state.CrmStore.activities.timeline
    // }
  },
  props: ['activities'],
  methods: {
    editActivity(activity){
      // this.$store.commit('setActivity', activity);
      // this.$store.commit('setMode', 'UPDATE');

      // eslint-disable-next-line
      $('#activityModal').foundation('open');
    },
    addActivity(){
      let activity = {
        attributes: {
          attendees_id: [],
          attendees: [],
          attachment: [],
          client_type: "",
          company_uuid: "",
          site_id: "",
          date: "",
          duration: "",
          entity_id: "",
          file_name: "",
          file_note: "",
          first_name: "",
          staff_id: "",
          last_name: "",
          notes: "",
          subject: "",
          time: "",
          type: "meeting"
        },
        type: "activities"
      }
      // this.$store.commit('setActivity', activity);
      // this.$store.commit('setMode', 'ADD');

      // eslint-disable-next-line
      $('#activityModal').foundation('open');
    },
    deleteActivity(id){
      const vm = this;
      let baseUrl = `${vm.apiBaseUrl}/activities/${id}`;
      let headers = vm.apiHeader.headers;
      
      // eslint-disable-next-line
      $.ajax({
        url: baseUrl,
        method: "DELETE",
        headers: headers,
        success(response){
          // console.log(response);
          let id = this.$router.history.current.params.id;
          let baseUrl = this.apiBaseUrl + "/checks/" + id + "/activities";
          this.$store.dispatch('getTimeline', {url: baseUrl});
          swal("", "Activity deleted successfully.", "success");
        },
        error(err){
          // console.log('error: ', err);
          swal("", "Failed to delete activity. Please try again.", "error");
        }
      });
    }
  }
};
</script>

<style lang="scss" scoped>

.ins-card-wrap {
  height: 100%;
}

#activity-container-list {
  min-height: 500px;
  height: 500px;
  overflow: auto;
  width: 99%;
}

::-webkit-scrollbar {
    width: 8px;
    margin-right: 10px;
    padding-right: 10px;
}

/* Track */
::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 3px rgba(0,0,0,0.1); 
}
 
/* Handle */
::-webkit-scrollbar-thumb {
    -webkit-border-radius: 10px;
    border-radius: 10px;
    background: #E4E6EC; 
    -webkit-box-shadow: 3px rgba(0,0,0,0.5); 
}
::-webkit-scrollbar-thumb:window-inactive {
	background: #E4E6EC; 
}

.activities-header,
.activities-footer {
  border-bottom: 1px solid #eee;
  font-family: 'Open Sans', sans-serif;
  padding: 0.9375rem;
}

.activities-header {
  border-bottom: 1px solid #eee;

  h1 {
    font-size: 18px;
    margin: 0;
  }
}
.activities-footer {
  border-top: 1px solid #eee;

  button {
    color: #008AE6;
    font-size: 16px;
    font-weight: 600;
    letter-spacing: 1px;
    cursor: pointer;
    outline: none;

    &:hover {
      color: #0068c4
    }
  }
}
.activities-wrap {
  font-size: 12px;
  padding: 0 .9375rem;
  height: calc(100% - 56px);
  width: 100%;
  position: absolute;

  .stick-wrap {
    width: 24px;
    height: 100%;
    position: absolute;
    left: 2.5%;

    &:before {
      content: '';
      width: 1px;
      height: 100%;
      position: absolute;
      background-color: #eee;
      left: calc(50% - 1px);
    }
  }

  .icon-wrap {
    width: 24px;
    height: 24px;
    background-color: #eee;
    border-radius: 50%;
    color: #fff;
    position: absolute;
    top: calc(50% - 12px);

    .icon-type {
      display: none;
      padding: 8px 0;
      text-align: center;
      font-size: 9px;
    }

		&.call {
      background-color: #24be8d;
      .icon-type.icon-phone {
        display: block;
      }
		}

		&.meeting {
			background-color: #1e86e3;
      .icon-type.icon-work {
        display: block;
      }
		}

		&.email {
			background-color: #59d2ec;
      .icon-type.icon-mail {
        display: block;
      }
		}

		&.event {
      background-color: #fdd835;
      .icon-type.icon-headset {
        display: block;
      }
		}

		&.file_note {
			background-color: #f27474;
      .icon-type.icon-notepad {
        display: block;
      }
    }

		&.log {
			background-color: #f0f0f0;
      .icon-type.icon-notepad-1 {
        display: block;
      }
		}

		&.task {
      background-color: #FFa639;
      .icon-type.icon-check {
        display: block;
      }
    }
  }

  .date-time-wrap {
    color: #323D53;
    font-weight: 600;
    position: absolute;
    // top: calc(50% - 9px);
    top: calc(50% - 18px);
  }
  .details-wrap {
    color: #8C94A4;
    // width: calc(100% - (.9375rem * 2 + 120px));
    width: calc(100% - (.9375rem * 2 + 60px));
    float: right;
    padding: 20px 0;
  }
  .actions-wrap {
    position: absolute;
    right: 5px;
    bottom: 4px;
    display: none;
   
    button {
      display: inline-block;
      width: 20px;
      height: 20px;
      font-size: 12px;
      padding: 3px;
      border-radius: 3px;
      cursor: pointer;

      &:hover {
        background-color: #e2e4e8;
      }

      &.edit-btn {
        color: #1e86e3;
      }

      &.delete-btn {
        color: #f27474;
      }
    }
  }

  .activity-row {
    position: relative;
    padding-left: calc(.9375rem * 2 + 24px);

    &:hover {
      background-color: #f2f4f8;
      .actions-wrap {
        display: block;
      }
    }
  }
}
</style>