<template>
  <ins-input
    v-bind:label="label"
    v-bind:value="value"
    v-bind:placeholder="placeholder"
    v-bind:name="name"
    v-bind:field="field"
    v-bind:field-id="fieldId"
    v-bind:icon="icon"
    v-bind:disabled="disabled"
    v-bind:readonly="readonly"
    v-bind:required="required"
    v-bind:maxlength="maxlength"
    v-bind:error-message="errorMessage"
    v-bind:has-error="hasError"
    @valueChange="onValueChange" />
</template>

<script>
export default {
  name: "InsInputWrap",
  props: {
    label: String,
    value: String,
    placeholder: String,
    name: String,
    field: String,
    fieldId: String,
    icon: String,
    errorMessage: String,
    hasError: Boolean,
    disabled: Boolean,
    readonly: Boolean,
    required: String,
    maxlength: String
  },
  methods: {
    onValueChange(ev) {
      this.$emit("input", ev.target.value);
    }
  }
};


/* Sample Usage
  <InsInputWrap
    label="Test"
    placeholder="Sample"
    v-model="sampleModel" />
*/


</script>
<style scoped></style>