<template>
  <div class="profile-header-wrap">
    <div class="profile-image-wrap" v-if="profileInitials">
      <img v-if="profileImg"
        v-bind:src="profileImg"
        v-bind:alt="`${profileName} Profile Image`">

      <div v-if="!profileImg" class="initials">
        {{ profileInitials }}
      </div>
    </div>
    <div class="profile-name-wrap">
      <h2>{{ profileName }}</h2>
    </div>
    <div class="social-links-wrap">
      <a target="_blank" 
        v-if="profileFacebook"
        v-bind:href="checkURL(profileFacebook)"
        class="social-link">
        <i class="icon-facebook-square"></i>
      </a>
      <a target="_blank" 
        v-if="profileGooglePlus"
        v-bind:href="checkURL(profileGooglePlus)"
        class="social-link">
        <i class="icon-google-plus-square"></i>
      </a>
      <a target="_blank" 
        v-if="profileInstagram"
        v-bind:href="checkURL(profileInstagram)"
        class="social-link">
        <i class="icon-instagram"></i>
      </a>
      <a target="_blank" 
        v-if="profileLinkedin"
        v-bind:href="checkURL(profileLinkedin)"
        class="social-link">
        <i class="icon-linkedin-square"></i>
      </a>
      <a target="_blank" 
        v-if="profileTwitter"
        v-bind:href="checkURL(profileTwitter)"
        class="social-link">
        <i class="icon-twitter-square"></i>
      </a>
      <a target="_blank" 
        v-if="profileYoutube"
        v-bind:href="checkURL(profileYoutube)"
        class="social-link">
        <i class="icon-youtube-square"></i>
      </a>
    </div>
  </div>
</template>
<script>
export default {
  name: "ProfileHead",
  props: [
    "profileName",
    "profileImg",
    "profileFacebook",
    "profileTwitter",
    "profileInstagram",
    "profileGooglePlus",
    "profileLinkedin",
    "profileYoutube"
  ],
  data() {
    return { profileInitials: "" };
  },
  methods: {
    getInitials() {
      if (this.profileName) {
        let initials = "";
        let split = this.profileName.split(" ");

        if (split.length > 1) {
          initials = `${split[0][0]}${split[1][0]}`;
        }
        if (split.length === 1) {
          initials = `${split[0][0]}${split[0][1]}`;
        }
        return initials;
      }
      return false;
    },
    checkURL(url){
      if (url.includes('https://')){
        return url;
      } else if (url.includes('http://')){
        return url;
      } else {
        return `//${url}`;
      }
    }
  },
  mounted() {
    this.profileInitials = this.getInitials();
  }
};
</script>
<style lang="scss" scoped>
.profile-header-wrap {
  display: flex;
  align-items: center;
  margin-bottom: 20px;

  .profile-image-wrap {
    width: 96px;
    height: 96px;
    border: solid 1px #e4e6ec;
    border-radius: 50%;
    box-sizing: border-box;
    overflow: hidden;

    img {
      width: 100%;
    }

    .initials {
      font-size: 3em;
      font-weight: 300;
      padding: 10px 14px 12px;
      background-color: #f2f4f8;
      color: #8c94a4;
      min-height: 96px;
    }
  }

  .profile-name-wrap h2 {
    margin-bottom: 0;
    height: 24px;
    opacity: 0.87;
    color: #1a1d2c;
    font-family: "Open Sans", sans-serif;
    font-size: 18px;
    letter-spacing: 0.14px;
    margin-left: 16px;
  }

  .social-links-wrap {
    text-align: right;
    flex-grow: 1;

    a.social-link {
      margin-left: 10px;
      display: inline-block;
      font-size: 19px;
      color: #8c94a4;

      &:hover {
        color: #1e86e3;
      }
    }
  }
}
</style>