<template>
  <div class="grid-container-full">
    <div class="grid-x grid-padding-x margin-bottom-30">

      <div class="large-4 medium-12 small-12 cell">
        <div class="grid-x">
          <div class="large-12 medium-12 small-12 cell">
            <ins-card steady>
              <InsInputWrap
                label="Google Maps API Keys"
                placeholder="Enter your API key here"
                v-model="tempGoogleMapsApiKey" />
            </ins-card>
          </div>

          <div class="large-12 medium-12 small-12 cell text-right button-wrap">
            <ins-button label="SAVE" solid v-bind:loading="saving"
              v-on:onClickInsButton="updateSystemFieldHandler">
            </ins-button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import InsInputWrap from "../components/InsInputWrap";
import { mapState } from 'vuex';

export default {
  name: "ApiKeys",
  components: { InsInputWrap },
  computed: mapState({
    googleMapsApiKey: state => state.SystemFieldsStore.google_maps_api_key,
  }),
  data() {
    return {
      tempGoogleMapsApiKey: '',
      saving: false,
      method: 'post',
      headers: {},
      sourceURL: '/api/customizations/',
      systemFieldModel: {
        form_configuration_name: "add_system_fields",
        parent_resource_id: "system_fields",
        form: {
          properties: {
            key: 'google_maps_api_key',
            value: '',
            is_enabled: 'true'
          }            
        }
      }
    }
  },
  watch: {
    googleMapsApiKey(){
      this.updateTempValue();
    }
  },
  methods: {
    updateSystemFieldHandler(){
      
      this.saving = true;
      this.systemFieldModel.form.properties.value = this.tempGoogleMapsApiKey;
      
      let url = `${this.sourceURL}`;

      if (this.method === 'put'){
        url += `${this.googleMapsApiKey.results[0].id}`;
      }
      
      let body = JSON.stringify(this.systemFieldModel);
      let config = { headers: this.headers }
      this.$http[this.method](url, body, config)
        .then(res => {
          // this.$store.dispatch('fetchSystemFields', 'google_maps_api_key');
          // this.method = 'put';
          // this.saving = false;
          toastIt('success', 'Successfully updated API Key', this.ops);
          setTimeout(() => {
            window.location.reload(true);
          }, 1000);
        }).catch(this.errorHandler);
    },
    errorHandler(error){
      console.error('error...', error);
      this.saving = false;
      toastIt('error', 'Failed to update API Key', this.ops);
    },
    updateTempValue(){
      if (this.googleMapsApiKey.results.length){
        this.tempGoogleMapsApiKey = this.googleMapsApiKey.results[0].value;
        this.method = 'put';
      }
    }
  },
  mounted(){
    this.headers = JSON.parse(JSON.stringify(this.apiHeader));
    this.updateTempValue();
  }
}
</script>
<style lang="scss">
.button-wrap {
  .ins-button {
    margin-top: 20px;
  }
}
</style>