<template>
  <div class="system-field-wrap">
    <h2>{{ heading }}</h2>
    <ins-base-table v-if="!loading"
      without-search static-table ref="systemFieldTable"
      v-on:onPaginationChange="onPaginationChangeHandler">
      <ins-base-table-row>
        <ins-base-table-td>
          <div class="new-system-field-wrap">
            <InsInputWrap v-model="newSystemField"
              v-bind:placeholder="`Please enter ${heading}`" />

            <ins-button label="ADD" solid
              ref="newSystemFieldButton"
              v-bind:loading="addingSystemField"
              v-on:onClickInsButton="addSystemField">
            </ins-button>
          </div>

        </ins-base-table-td>
      </ins-base-table-row>

      <ins-base-table-row v-if="!loading" v-for="field in fields.results" v-bind:key="field.id">
        <ins-base-table-td class="system-field-wrap__data">

          <div class="view-mode" v-if="!field.editMode">
            {{ field.value }}
          </div>

          <div class="edit-mode" v-if="field.editMode">
            <InsInputWrap v-model="field.tempValue"
              v-bind:placeholder="`Please enter ${heading}`" />

            <div class="edit-mode__buttons">
              <ins-button label="update" size="small"
                v-on:onClickInsButton="updateSystemField(field)">
              </ins-button>

              <ins-button label="cancel" size="small"
                v-on:onClickInsButton="cancelEditMode(field)">
              </ins-button>
            </div>
          </div>

          <div class="actions-wrap" v-if="!field.editMode">
            <button class="action edit"
              v-on:click="enableEditMode(field)">
              Edit
            </button>
            <span class="seperator">|</span>
            <button class="action archive"
              v-on:click="archiveSystemField(field)">
              Archive
            </button>
          </div>

        </ins-base-table-td>
      </ins-base-table-row>

    </ins-base-table>
  </div>
</template>

<script>
import InsInputWrap from "../components/InsInputWrap";

export default {
  name: "SystemField",
  components: {
    InsInputWrap
  },
  props: {
    heading: String,
    type: String
  },
  data() {
    return {
        fields: {
          results: []
        },
        loading: false,
        newSystemField: '',
        addingSystemField: false,
        pageNumber: 1,
        pageSize: 10,
        headers: {},
        sourceURL: '/api/customizations/',
        systemFieldModel: {
          form_configuration_name: "add_system_fields",
          parent_resource_id: "system_fields",
          form: {
            properties: {
              key: '',
              value: '',
              is_enabled: 'true'
            }            
          }
        }
    }
  },
  methods: {
    enableEditMode(field){
      field.editMode = true;
    },
    cancelEditMode(field){
      field.tempValue = field.value;
      field.editMode = false;
    },
    mutateSystemField(method, field, callback) {
      this.systemFieldModel.form.properties.key = this.type;
      this.systemFieldModel.form.properties.value = field.tempValue;
      
      let url = `${this.sourceURL}${method === 'put' ? field.id : ''}`;
      let body = JSON.stringify(this.systemFieldModel);
      let config = { headers: this.headers }
      this.$http[method](url, body, config)
        .then(callback).catch(this.errorHandler);
    },
    addSystemField(){
      this.addingSystemField = true;
      let lowCase = this.newSystemField.toLowerCase();
      if (this.type === 'contact_type' &&
         lowCase === 'primary' &&
         lowCase === 'secondary'){

          this.addingSystemField = false;
          toastIt("error", `${this.newSystemField} is a reserved system field for Contact Type.`, this.ops);
      } else {

        let field = { tempValue: this.newSystemField }
        this.mutateSystemField('post', field, () => {
          setTimeout(() => {
            toastIt("success", "New system field created successfully.", this.ops);
            this.addingSystemField = false;
            this.fetchSystemFields();
            this.$store.dispatch('fetchSystemFields', this.type);
          }, 1000);
        });
      }
    },
    updateSystemField(field){
      this.mutateSystemField('put', field, res => {
        field.value = field.tempValue;
        field.editMode = false;
        setTimeout(() => {
          this.$store.dispatch('fetchSystemFields', this.type);
        }, 1000)
      });
    },
    archiveSystemField(field){
      let deleteMsg = 'Are you sure you want to delete this System Field?';
      swal({
        title: "",
        html: `
          <ins-loader
            state-icon="warning"
            icon-color="warning"
            state-message="${deleteMsg}">
          </ins-loader>`,
        type: "",
        showCancelButton: true,
        confirmButtonText: "Archive"
      }).then(result => {
        if (result.value) {    
          this.systemFieldModel.form.properties.is_enabled = false;
          this.mutateSystemField('put', field, () => {
            setTimeout(() => {
              this.fetchSystemFields();
              this.$store.dispatch('fetchSystemFields', this.type);
            }, 1000);
          });
        }
      });
    },
    onPaginationChangeHandler(event){
      this.pageNumber = event.detail.pageNumber;
      this.pageSize = event.detail.pageSize;
      this.fetchSystemFields();
    },
    fetchSystemFields(){
      this.loading = true;
      this.loaderStateHandler("", "Loading system fields...", "processing");
      let url = `/api/get-system-fields-data.json?key=${this.type}&page=${this.pageNumber}&size=${this.pageSize}`;

      this.$http.get(url)
        .then(res => {
          res.data.items.results.map(this.constructResults);
          this.fields = res.data.items;
          this.loading = false;
          this.setupTable();
        }).catch(this.errorHandler);
    },
    constructResults(result){
      result.tempValue = result.value;
      result.editMode = false;
      return result;
    },
    errorHandler(error){
      console.error('error...', error);
      this.addingSystemField = false;
      toastIt('error', 'Failed to load System Fields.', this.ops);
      // this.loading = true;
      // this.loaderStateHandler('', 'Failed to load System Fields.', 'error');
    },
    loaderStateHandler(title, message, type) {
      this.$refs.systemFieldTable.loadingScreen = true;
      this.$refs.systemFieldTable.loaderTitle = title;
      this.$refs.systemFieldTable.loaderMessage = message;
      this.$refs.systemFieldTable.loaderIcon = type;
    },
    setupTable(){
      setTimeout(() => {
        this.$refs.systemFieldTable.totalCount = this.fields.total_entries;
        this.$refs.systemFieldTable.pageNumber = this.pageNumber;
        this.$refs.systemFieldTable.pageSize = this.pageSize;
        this.$refs.systemFieldTable.loadingScreen = false;
      }, 300);
    },
  },
  mounted(){
    this.headers = JSON.parse(JSON.stringify(this.apiHeader));
    this.fetchSystemFields();
  },
}
</script>

<style lang="scss">
  .system-field-wrap {
    h2 {
      font-size: 14px;
      font-weight: 600;
      color: #2C3148;
    }

    .new-system-field-wrap {
      .ins-input-wrap {
        width: calc(100% - 76px);
        display: inline-block;
        margin-right: 11px;

        input {
          margin-bottom: 0;
        }
      }
      .ins-button {
        margin-top: 2px;
        width: 64px;

        &.is-loading {
          .spinner {
            margin: 0 auto;
          }
        }
      }
    }

    .actions-wrap {
      font-size: 12px;
      opacity: 0;
      padding-top: 4px;

        .action {
          color: #1e86e3;
          cursor: pointer;

          &.archive {
            color: #f47474;
          }
        }
        .seperator {
          color: #e4e6ec;
          margin: 0 7px 0 6px;
        }
    }

    &__data:hover {
      .actions-wrap {
        opacity: 1;
      }
    }

    .edit-mode {
      
      input {
        margin-bottom: 5px;
      }

      &__buttons {
        text-align: right;

        button {
          padding: 0 8px;
        }
        ins-button + ins-button {
          margin-left: 0;
        }
      }
    }
  }
</style>