<template>
  <div class="grid-container-full">
    <div class="grid-x grid-padding-x" v-if="companyData">
      <div class="cell large-8 medium-12 small-12 margin-bottom-30">
        <ins-card steady>
          <div class="grid-x grid-padding-x">
            <div class="cell large-12 medium-12 small-12">
              <ProfileHead
                v-bind:profile-name="checkValue(companyData['Company'])"
                v-bind:profile-img="checkValue(companyData['Company_Img'])"
                v-bind:profile-facebook="checkValue(companyData['Facebook'])"
                v-bind:profile-google-plus="checkValue(companyData['Google'])"
                v-bind:profile-instagram="checkValue(companyData['Instagram'])"
                v-bind:profile-linkedin="checkValue(companyData['Linkedin'])"
                v-bind:profile-twitter="checkValue(companyData['Twitter'])"
                v-bind:profile-youtube="checkValue(companyData['Youtube'])" />

              <div class="cell large-12 medium-12 small-12">
                <div class="grid-x">
                  <!-- <div v-bind:class="`cell ${constructedAddress ? 'large-6':'large-12'} medium-12 small-12 `"> -->
                  <div class="cell large-6 medium-12 small-12">
                    <table class="information-wrap">
                      <tr>
                        <td class="td-label">Customer ID</td>
                        <td>{{ checkValue(companyData['company_id']) }}</td>
                      </tr>
                      <tr>
                        <td class="td-label">Phone</td>
                        <td>{{ constructPhoneNumber() }}</td>
                      </tr>
                      <tr>
                        <td class="td-label">Website</td>
                        <td>
                          <a v-bind:href="checkURL(companyData['Website'])" target="_blank">
                            {{ checkValue(companyData['Website']) }}
                          </a>
                        </td>
                      </tr>
                      <tr>
                        <td class="td-label">Assigned To</td>
                        <td>
                          <span v-if="assignedAdmin">
                            {{ `${assignedAdmin.first_name} ${assignedAdmin.last_name}` }}
                          </span>
                        </td>
                      </tr>
                      <tr>
                        <td class="td-label">Company Type</td>
                        <td>
                          <span v-if="companyType">
                            {{ companyType.value }}
                          </span>
                        </td>
                      </tr>
                      <tr v-if="industryType">
                        <td class="td-label">Industry Type</td>
                        <td>
                          <span v-if="industryType">
                            {{ industryType.value }}
                          </span>
                        </td>
                      </tr>
                    </table>
                  </div>
                  <div class="cell large-6 medium-12 small-12">
                    <!-- <GoogleMapDirection
                      v-if="constructedAddress"
                      v-bind:address="constructedAddress" /> -->
                    <GoogleMapDirection                     
                      v-bind:address="constructedAddress" />
                  </div>
                </div>
              </div>

              <!-- <div class="cell large-12 medium-12 small-12" v-if="checkValue(companyData['Notes'])"> -->
              <div class="cell large-12 medium-12 small-12">
                <div class="notes-wrap">
                  <InsTextareaWrap 
                    label="Notes"
                    v-bind:value="checkValue(companyData['Notes'])"
                    readonly />
                </div>
              </div>

              <div class="cell large-12 medium-12 small-12 text-right">
                <ins-button label="EDIT COMPANY" outlined
                  v-on:onClickInsButton="editCompanyHandler">
                </ins-button>
              </div>
            </div>
          </div>
        </ins-card>
      </div>
      <div class="cell large-4 medium-12 small-12 activities-if margin-bottom-30">
        <AdminActivities v-bind:activities="activities" />
      </div>
      <div class="cell large-12 medium-12 small-12 margin-bottom-30" id="contactListWrapper">
        <ins-button class="add-new-contact-btn"
          label="ADD NEW CONTACT" solid
          v-on:onClickInsButton="addNewContactHandler">
        </ins-button>
        <ContactList v-bind:company-id="companyData['company_id']" />
      </div>
      <div class="cell large-12 medium-12 small-12 margin-bottom-30">
        <ins-button label="ARCHIVE COMPANY" color="negative"
          v-on:onClickInsButton="archiveCompany">
        </ins-button>
      </div>
    </div>
  </div>
</template>

<script>
import moment from "moment";

import InsInputWrap from "../components/InsInputWrap.vue";
import InsSelectWrap from "../components/InsSelectWrap.vue";
import InsTextareaWrap from "../components/InsTextareaWrap.vue";
import AdminActivities from "../components/AdminActivities.vue";
import ProfileHead from "../components/ProfileHead.vue";
import GoogleMapDirection from "../components/GoogleMapDirection.vue";
import ContactList from "../components/ContactList.vue";

export default {
    name: "ViewCompanyDetails",
    components: {
      InsInputWrap,
      InsSelectWrap,
      InsTextareaWrap,
      AdminActivities,
      ProfileHead,
      GoogleMapDirection,
      ContactList
    },
    data() {
      return {
        showDetails: false,
        assignedAdmin: '',
        companyType: '',
        industryType: '',
        activities: undefined,
        companyData: undefined,
        constructedAddress: undefined,
        sourceCRMDetails: "/api/get-company-contact-list-data.json?id=",
        mapTheme: this.getTheme,
        headers: {},
        contactModel: {
          form_configuration_name: "crm_delete_customer",
          form: {
            profiles_attributes: {
              customer_attributes: {
                properties_attributes: {
                  is_enabled: "false"
                }
              }
            }
          }
        },
        companyModel: {
          form_configuration_name: "crm_delete_company",
          parent_resource_id: "company",
          form: {
            properties: {
              is_enabled: "false"
            }            
          }
        }
      }
    },
    methods: { 
      constructAddress() {
        let fullAddress = '';

        if (this.checkValue(this.companyData['Home Address'])) fullAddress = this.companyData['Home Address'];
        if (this.checkValue(this.companyData['Home Street'])) fullAddress += ` ${this.companyData['Home Street']}`;
        if (this.checkValue(this.companyData['Home City'])) fullAddress += ` ${this.companyData['Home City']}`;
        if (this.checkValue(this.companyData['Home State'])) fullAddress += ` ${this.companyData['Home State']}`;
        if (this.checkValue(this.companyData['Home Country'])) fullAddress += ` ${this.companyData['Home Country']}`;
        if (this.checkValue(this.companyData['Home Area Code'])) fullAddress += ` ${this.companyData['Home Area Code']}`;

        return fullAddress;
      },
      constructPhoneNumber() {
        let phoneNumber = '';

        if (this.checkValue(this.companyData['Company Mobile Phone Area Code']) ||
            this.checkValue(this.companyData['Company Mobile Phone Number'])){

          phoneNumber = `${this.checkValue(this.companyData['Company Mobile Phone Country Code'], true)}`;
          phoneNumber += ` ${this.checkValue(this.companyData['Company Mobile Phone Area Code'])}`;
          phoneNumber += ` ${this.checkValue(this.companyData['Company Mobile Phone Number'])}`;
        }

        return phoneNumber.trim();
      },
      addNewContactHandler() {
        this.$store.commit("pushUserProfile", "contact");
        this.$store.commit("pushFormType", "add");
        this.$store.commit("pushFormName", "add_new_customer");
        this.$store.commit('setAddFromCompany', this.companyData['id']);
        this.$router.push("/manage-crm");
      },
      checkValue(value, withPlus){
        if (value && value !== 'undefined' && value !== 'null'){
          return value ? `${withPlus ? '+':''}${value}` : '';
        }
        return '';
      },
      archiveCompany() {
        let deleteMsg = 'Are you sure you want to archive this company? This will also archive related contacts.';
        swal({
          title: "",
          html: `
            <ins-loader
              state-icon="warning"
              icon-color="warning"
              state-message="${deleteMsg}">
            </ins-loader>`,
          type: "",
          showCancelButton: true,
          confirmButtonText: "Archive"
        }).then(result => {
          if (result.value) {
            this.getAllContacts();
          }
        });
      },
      getAllContacts() {
        let url = `${this.sourceCRMDetails}${this.companyData.company_id}&sortBy=updated_at&orderBy=desc`;
        this.$http.get(url)
          .then(response => {
            if (response.data.items.results.length){
              this.deleteAllContacts(response.data.items.results);
            } else {
              this.deleteCompany();
            }
          }).catch(this.errorHandler);
      },
      deleteAllContacts(contacts){
        let go = true;
        let counter = 0;
        contacts.forEach(contact => {
          if (go) {
            let url = `/api/users/${contact.id}`;      
            let body = JSON.stringify(this.contactModel);
            this.$http.put(url, body, { headers: this.headers })
              .then(response => {
                counter++;
                if (counter === contacts.length){
                  this.deleteCompany();
                }
              }).catch(error => {
                this.errorHandler(error);
                go = false;
              });
          }
        });
      },
      deleteCompany() {
        let url = `/api/customizations/${this.companyData.company_id}`;      
        let body = JSON.stringify(this.companyModel);
        this.$http.put(url, body, { headers: this.headers })
          .then(response => {
            toastIt("success", "Successfully archived company.", this.ops);
            this.$store.commit('removeCompanyOption', this.companyData.company_id);
            this.$router.push('/customers');
          }).catch(this.errorHandler);
      },
      errorHandler(error){
        console.error('error...', error);
        toastIt("error", "Failed to archive company.", this.ops);
      },
      editCompanyHandler() {
        this.$store.commit("pushUserData", this.companyData);
        this.$store.commit("pushFormType", "edit");
        this.$router.push("/manage-company");
      },
      mapInit() {
        this.mapTheme = this.getTheme;
        let profileMap = document.getElementById("profileMap");
        let geocoder = new google.maps.Geocoder();
        let map = new google.maps.Map(profileMap, {
            scrollwheel: false,
            draggable: true,
            disableDoubleClickZoom: true,
            zoomControll: false,            
            zoom: 16,
            styles: this.mapTheme
        });
        // set address location
        this.mapGetCoordinates(geocoder, map);
      },
      mapGetCoordinates(geocoder, resultsMap) {
        let address = this.constructedAddress;
        geocoder.geocode({'address': address}, function(results, status) {
          if (status === 'OK') {
            resultsMap.setCenter(results[0].geometry.location);
            let marker = new google.maps.Marker({
              map: resultsMap,
              position: results[0].geometry.location
            });
          } else {
            console.error('Geocode was not successful for the following reason: ' + status);
          }
        });
      },
      getDataFromStore(label, storeKey){
        let storedData = this.$store.state.SystemFieldsStore[storeKey]
        let data = storedData.results.find(item => {
          return item.id === this.companyData[label]
        });
        return data ? data : false;
      },
      checkURL(url){
        let checked = this.checkValue(url);
        
        if (checked){
          if (checked.includes('https://')){
            return checked;
          } else if (checked.includes('http://')){
            return checked;
          } else {
            return `//${checked}`;
          }
        }

        return '';
      },
      setInsRendererLabel(){
        let insRendererEl = document.querySelector('ins-renderer');
        if (insRendererEl.updateRouteLabel){
          insRendererEl.updateRouteLabel('Customer Details');
        } else {
          setTimeout(() => {
            this.setInsRendererLabel();
          }, 300);
        }
      }
    },    
    created() {
      this.companyData = JSON.parse(JSON.stringify(this.$store.state.CrmStore.companyData));
      if (this.companyData.Company){
        this.headers = JSON.parse(JSON.stringify(this.apiHeader));
        this.constructedAddress = this.constructAddress();
        this.assignedAdmin = this.getDataFromStore('Assigned To', 'administratorList');
        this.companyType = this.getDataFromStore('Company Type', 'company_type');
        this.industryType = this.getDataFromStore('Industry Type', 'industry_type');
      } else {
        this.$router.push('/customers');
      }
    },
    mounted() {
      this.setInsRendererLabel();
      if (this.constructedAddress) {
        this.mapInit();
      }
    }
}
</script>

<style lang="scss" scoped>
  #contactListWrapper { position: relative; }
  .add-new-contact-btn {
    position: absolute; 
    top: 0; 
    z-index: 1; 
  }
</style>
